
import MeshVisualizer from  "../../core/Source/MeshVisualizer.js";

import ImageTileLayer from  "./layers/imagery/ImageTileLayer.js";

import imageryLayersOrderByZIndex from  "./layers/imageryLayersOrderByZIndex.js";


/**
 * 
 * @param {object} params 
 * @param {string|HTMLElement}params.container
 */
function Earth(params) {
    // CesiumCoreExt();
    //import Cesium Class
    const {
        Camera, Viewer, Cartesian3, Transforms, Matrix4,
        Rectangle, TileProviderError
    } = Cesium;

    //get params

    let { container, viewBox } = params;
    viewBox = viewBox || [70, -1, 140, 60]
    if (viewBox) {
        Camera.DEFAULT_VIEW_RECTANGLE = Rectangle.fromDegrees(
            viewBox[0], viewBox[1], viewBox[2], viewBox[3]
        );
    } else {
        Camera.DEFAULT_VIEW_RECTANGLE = new Rectangle(
            1.28283693362122, 0.05999788534537946, 2.3577393069441595, 0.9348140736573897
        );
    }

    //consts
    const viewer = new Viewer(container, params);
    const { homeButton, baseLayerPicker, scene, entities, imageryLayers, postProcessStages } = viewer;
    const { globe, primitives, camera } = scene;
    if (baseLayerPicker) {
        const { imageryProviderViewModels } = baseLayerPicker.viewModel;
        container = viewer.container;
        baseLayerPicker.viewModel.selectedImagery = imageryProviderViewModels[imageryProviderViewModels.length - 1];
    }
    postProcessStages.fxaa.enabled = true;
    if (scene.highDynamicRangeSupported && params.highDynamicRange) {
        scene.highDynamicRange = true;
    }

    //
    let globalScene = null;
    // const globalScene = new MeshVisualizer()
    // globalScene.init(viewer.scene.frameState);
    // globalScene.useSunLight = true;
    // requestAnimationFrame(() => {
    //     primitives.add(globalScene);
    // })
    // 

    //
    var _localScenes = [];
    var _layers = [];

    function findLayerIndex(pLayer) {
        var layerIndex = -1;
        for (let i = 0; i < _layers.length; i++) {
            const layer = _layers[i];
            if (layer == pLayer) {
                layerIndex = i;
                break;
            }
        }
        return layerIndex;
    }

    function look(lon, lat, alt, offsetX, offsetY, offsetZ) {
        if (!viewer || viewer.isDestroyed()) {
            return;
        }
        var center;
        if (lon instanceof Cartesian3) {
            center = lon;
            offsetZ = offsetX;
            offsetY = alt;
            offsetX = lat;
        }
        else {
            center = Cartesian3.fromDegrees(lon, lat, alt);
        }

        var transform = Transforms.eastNorthUpToFixedFrame(center);

        camera.constrainedAxis = Cartesian3.UNIT_Z;
        camera.lookAtTransform(transform, new Cartesian3(offsetX, offsetY, offsetZ));

        setTimeout(function () {
            camera.lookAtTransform(Matrix4.IDENTITY);
        }, 100)
    }
    this.look = look;

    this.flyTo = function (target, options) {
        if (!target) return;

        if (target.boundingSphere) {
            return viewer.camera.flyToBoundingSphere(target.boundingSphere, options)
        } else if (target instanceof Cesium.Rectangle
            || target instanceof Cesium.Cartesian3) {
            return viewer.camera.flyTo(Object.assign(options || {}, {
                destination: target
            }))
        } else if (target) {
            return viewer.flyTo(target, options)
        }
    }

    /**
     * 
     * @param {MeshVisualizer} localScene 
     */
    this.addLocalScene = function (localScene) {
        localScene = localScene instanceof MeshVisualizer ? localScene : new MeshVisualizer(localScene);
        localScene.init(viewer.scene.frameState);
        scene.primitives.add(localScene)
        _localScenes.push(localScene);
        return localScene
    }

    /**
     * 
     * @param {MeshVisualizer} localScene 
     */
    this.removeLocalScene = function (localScene) {
        if (!localScene) return;
        var i = _localScenes.indexOf(localScene);
        if (i !== -1) {
            scene.primitives.remove(localScene)
            _localScenes.splice(i, 1);
        }
    }

    this.addLayer = function addLayer(layer) {
        if (findLayerIndex(layer) >= 0) {
            console.warn(' `' + layer.name + '` ');
            return;
        }
        layer.load(this);
        _layers.push(layer);
        return layer;
    }

    this.removeLayer = function removeLayer(layer) {
        var layerIndex = findLayerIndex(layer);
        if (layerIndex == -1) return false;
        _layers.splice(layerIndex, 1);
        layer.unload(this);
        return true;
    }

    /**
     * 
     * @param {ImageTileLayer} imgLayer 
     * @returns {ImageTileLayer}
     */
    this.addImageryLayer = function addImageryLayer(imgLayer) {
        if (!(imgLayer instanceof ImageTileLayer)) {
            imgLayer = new ImageTileLayer(imgLayer);
        }

        this.addLayer(imgLayer);
        imageryLayersOrderByZIndex(imageryLayers)
        return imgLayer;
    }

    /**
     * 
     * @param {ImageTileLayer} imgLayer 
     * @returns {ImageTileLayer}
     */
    this.removeImageryLayer = function removeImageryLayer(imgLayer) {
        if (!this.removeLayer(imgLayer)) return;
        imageryLayersOrderByZIndex(imageryLayers)
    }

    this.updateOrder = function updateOrder() {
        imageryLayersOrderByZIndex(imageryLayers)
    }

    this.onHomeClick = new Cesium.Event()
    homeButton.viewModel.command.beforeExecute.addEventListener((evt) => {
        this.onHomeClick.raiseEvent(evt)
        // evt.cancel = this.onHomeClick.numberOfListeners > 0;
    });

    var oldTileProviderErrorHandleError = TileProviderError.handleError;
    TileProviderError.handleError = function (
        previousError,
        provider,
        event,
        message,
        x,
        y,
        level,
        retryFunction,
        errorDetails
    ) {
        if (provider.constructor.name == 'VectorTileImageryProvider') {
            return;
        }

        oldTileProviderErrorHandleError(
            previousError,
            provider,
            event,
            message,
            x,
            y,
            level,
            retryFunction,
            errorDetails
        );
    }

    var viewRectangle = new Cesium.Rectangle()
    Object.defineProperties(this, {
        /**
         * 
         */
        maxTileLevel: {
            enumerable: false,
            get() {
                var tilesToRender = globe._surface._tilesToRender
                var maxTileLevel = -1;
                for (const tile of tilesToRender) {
                    maxTileLevel = Math.max(tile.level, maxTileLevel)
                }
                return maxTileLevel
            }
        },
        /**
         * 
         */
        viewRectangle: {
            enumerable: false,
            get() {
                var tilesToRender = globe._surface._tilesToRender
                if (tilesToRender.length == 0) {
                    Rectangle.clone(Rectangle.MAX_VALUE, viewRectangle)
                }
                Rectangle.clone(tilesToRender[0]._rectangle, viewRectangle);
                for (let i = 1; i < tilesToRender.length; i++) {
                    const tile = tilesToRender[i]
                    Rectangle.union(tile._rectangle, viewRectangle, viewRectangle)
                }
                return viewRectangle;
            }
        },
        viewer: {
            enumerable: false,
            get() {
                return viewer;
            }
        },
        scene: {
            enumerable: false,
            get() {
                return scene;
            }
        },
        globalScene: {
            enumerable: false,
            get() {
                globalScene = new MeshVisualizer()
                globalScene.init(viewer.scene.frameState);
                globalScene.useSunLight = true;
                primitives.add(globalScene);
                return globalScene;
            }
        },
        localScenes: {
            enumerable: false,
            get() {
                return _localScenes;
            }
        },
        globe: {
            enumerable: false,
            get() {
                return globe;
            }
        },
        layers: {
            enumerable: false,
            get() {
                return _layers;
            }
        },
        imageryLayers: {
            enumerable: false,
            get() {
                return imageryLayers;
            }
        },
        entities: {
            enumerable: false,
            get() {
                return entities;
            }
        },
        primitives: {
            enumerable: false,
            get() {
                return primitives;
            }
        },
        postProcessStages: {
            enumerable: false,
            get() {
                return postProcessStages
            }
        }
    })

}

export default Earth;