
import { InputStream } from  "./InputStream.js";

import { DataTypes } from  "../Common/DataTypes.js";

import { BinaryStreamOperator } from  "./BinaryStreamOperator.js";

import { AsciiStreamOperator } from  "./AsciiStreamOperator.js";

import { Log } from  "../Common/Log.js";


let PROPERTY = new DataTypes.ObjectProperty();

const OSG_HEADER_LOW = 0x6C910EA1;
const OSG_HEADER_HIGH = 0x1AFB4545;
const OSGT_HEADER = "#Ascii";

const WriteType = {
    WRITE_UNKNOWN: 0,
    WRITE_SCENE: 1,
    WRITE_IMAGE: 2,
    WRITE_OBJECT: 3
};

/**
 *
 * @param {buffer} buffer
 * @return {object} the serialized node
 */
function readBuffer(buffer) {


    if (typeof Buffer != 'undefined' && buffer instanceof Buffer) {
        buffer = new Uint8Array(buffer).buffer;
    }

    let reader = new BinaryStreamOperator(buffer, 0);

    const low = reader.readUInt();
    const high = reader.readUInt();

    reader._position = 0;

    let isASCIIFormat = buffer.byteLength >= OSGT_HEADER.length;
    if (isASCIIFormat) {
        var magicBuffer = reader.readBuffer(OSGT_HEADER.length)
        var magic = String.fromCharCode.apply(null, magicBuffer)
        isASCIIFormat = magic === OSGT_HEADER;
    }


    let type, version, openscenegraph_soversion, hasDomainVersion;
    if (low === OSG_HEADER_LOW && high === OSG_HEADER_HIGH) {

        reader._position = 8;
        let useCompressSource, useRobustBinaryFormat;
        type = reader.readUInt();// buf.readUInt32LE(ReadPosition);
        openscenegraph_soversion = reader.readUInt();
        let attributes = reader.readUInt();
        hasDomainVersion = (attributes & 0x1) !== 0;
        useCompressSource = (attributes & 0x2) !== 0;
        useRobustBinaryFormat = (attributes & 0x4) !== 0;
        reader.supportBinaryBrackets = useRobustBinaryFormat;


        let next = reader.readString();

        if (next !== "0") {
            throw "compresses data not supported";
        }
    }
    else if (isASCIIFormat) {
        Log("OSGT File");
        reader = new AsciiStreamOperator(buffer, OSGT_HEADER.length);
        type = reader.readString();
        reader.goToNextRow();
        type = WriteType["WRITE_" + type.toUpperCase()] || 0;
        reader.readObjectProperty(PROPERTY.set("#Version"));
        openscenegraph_soversion = reader.readUInt();
        reader.goToNextRow();
        reader.readObjectProperty(PROPERTY.set("#Generator"));
        reader.readString(); // Generator Name
        reader.goToNextRow();
        hasDomainVersion = reader.lookForward() === "#CustomDomain"
    }
    else {
        console.warn("osg-serializer-js: osg format not supported. use  `parseOsg` instead");
        return false;
    }

    if (hasDomainVersion) {
        // TODO
        throw new Error("osg DomainVersion not supported.");

    }
    reader.version = openscenegraph_soversion;

    let inputStream = new InputStream(reader);
    let startTime = new Date().getTime();
    let obj = inputStream.readObject();
    Log("timeElapsed:", new Date().getTime() - startTime);
    return obj;

}

/**
 *
 * @param {string} path
 * @param {function} cb callback function
 */
function readFile(path, cb) {
    if (!cb) {
        throw "Call back required";
    }
    var req = 'require';
    var fs = eval(req + '(\'fs\');');
    fs.readFile(path, function (err, data) {
        if (err) cb(err);
        let node;
        try {
            console.log(err, data);
            node = readBuffer(data);
        } catch (e) {
            console.trace(e)
            return cb(e);
        }
        if (!node) {
            return cb("Failed to convert file")
        }
        cb(null, node)
    })
}


export {
    readBuffer,
    readFile
};