import GeometryUtils from  "../../../../core/Source/GeometryUtils.js";

// import MeshVisualizer from "../../../core/MeshVisualizer.js";
import ExtrudedPolygonGeometry from  "../../geometries/ExtrudedPolygonGeometry.js";

import GenericWorker from  "../../../../utils/Source/GenericWorker.js";

import GeoPolygonGeometry from  "../../geometries/GeoPolygonGeometry.js";


function getPickId(feature, provider) {
    var pickIdMap = provider._pickIdMap;
    var pickId = pickIdMap.get(feature);
    if (!pickId) {
        var context = provider.localScene.frameState.context
        pickId = context.createPickId({
            feature: feature,
            primitive: provider.root
        })
        pickIdMap.set(feature, pickId);
        provider.pickIds.push(pickId);
    }
    return pickId;
}

export default class Vector3DStandardProvider {

    /**
     * 
     * @param {*} root 
     * @param {*} styleFilter 
     * @param {MeshVisualizer} localScene 
     * @param  {THREE.Vector2|Cesium.Cartesian2}uvUnit
     * @param  {boolean}[roundUv=false]
     * @param  {boolean}[useUvUnit=true]
     */
    constructor(root, styleFilter, localScene, uvUnit, roundUv, useUvUnit) {
        const defaultValue = Cesium.defaultValue;
        this.root = root;
        this.styleFilter = styleFilter;
        this.localScene = localScene
        this.uvUnit = uvUnit;
        this.roundUv = defaultValue(roundUv, false);
        this.useUvUnit = defaultValue(useUvUnit, true);
        this.pickIds = [];
        this._pickIdMap = new Map();
        this.terrainProvider = null;
        this.clampToGround = false;
        this.receiveShadow = true;
        this.castShadow = true;
    }

    destroy() {
        this._pickIdMap.clear()
        this.pickIds.splice(0);
    }

    createMesh(feature, overrideUvUnit, createTopExtrudedUvNormal, createBottomExtrudedUvNormal, closeTop, uvGenerator, rtcCenter) {
        const { styleFilter } = this
        const { Mesh } = THREE;
        const { Cartesian3, Cartographic, defaultValue } = Cesium;
        const provider = this;
        /**
         * @type {MeshVisualizer}
         */
        const localScene = provider.localScene;
        const uvUnit = defaultValue(overrideUvUnit, provider.uvUnit);
        createTopExtrudedUvNormal = defaultValue(createTopExtrudedUvNormal, true);
        createBottomExtrudedUvNormal = defaultValue(createBottomExtrudedUvNormal, true);
        const roundUv = provider.roundUv;
        const useUvUnit = provider.useUvUnit;
        const clampToGround = provider.clampToGround;
        const terrainProvider = provider.terrainProvider;


        let style = {
            useUvUnit: useUvUnit,
            roundUv: roundUv,
            receiveShadow: this.receiveShadow,
            castShadow: this.castShadow
        };

        styleFilter(feature, style);
        var extrudedHeight = style.extrudedHeight //|| 1
        var height = 0
        if (clampToGround && feature.properties._terrianHeight
            && terrainProvider) {
            height = feature.properties._terrianHeight;
        }

        let geometry = GeoPolygonGeometry.fromFeature(feature, clampToGround ? height : null, height + extrudedHeight, uvGenerator, closeTop);

        if (extrudedHeight) {
            geometry = ExtrudedPolygonGeometry.createExtrudedUvNormal(geometry, height, style.useUvUnit ? style.uvUnit || uvUnit : null, style.roundUv, createTopExtrudedUvNormal, createBottomExtrudedUvNormal);
            geometry = GeometryUtils.toBufferGeometry3js(geometry);
            geometry.attributes.uv = geometry.attributes.extrudedUv
            geometry.deleteAttribute('extrudedUv');
            geometry.deleteAttribute('extrudedNormal');
        } else {
            geometry = GeometryUtils.toBufferGeometry3js(geometry);
        }

        if (rtcCenter) {

            let positions = geometry.attributes.position.array;
            for (let i = 0; i < positions.length; i += 3) {
                positions[i] -= rtcCenter.x;
                positions[i + 1] -= rtcCenter.y;
                positions[i + 2] -= rtcCenter.z;
            }
        } else {

            let positions = geometry.attributes.position.array;
            var cartesian3 = new Cartesian3();
            var cartographic = new Cartographic()
            var hs = [], maxY = 0;
            for (let i = 0; i < positions.length; i += 3) {
                cartesian3.x = positions[i]
                cartesian3.y = positions[i + 1]
                cartesian3.z = positions[i + 2]
                Cartographic.fromCartesian(cartesian3, null, cartographic);
                hs.push(cartographic.height - height)
                localScene.worldCoordinatesToLocal(cartesian3, cartesian3, { y: 1 })
                positions[i] = cartesian3.x
                positions[i + 1] = cartesian3.y
                positions[i + 2] = cartesian3.z

                maxY = Math.max(cartesian3.y, maxY);
            }

            if (extrudedHeight) {
                var eps = extrudedHeight * 0.1, id = 0;
                for (let i = 0; i < hs.length; i++) {
                    const h = hs[i];
                    if (h >= eps) {//top
                        positions[id + 1] = maxY;
                    }
                    id += 3;
                }
            }

            geometry.computeVertexNormals()
            geometry.computeBoundingSphere()
        }

        var mesh = new Mesh(geometry, style.material);
        return mesh

    }
    requestContent(contentSet, rtcCenter) {
        let groups = [], materialMap = new WeakMap();
        const { root, styleFilter } = this
        const { Object3D, Mesh, BufferAttribute } = THREE;
        const { Cartesian3, Cartographic, BoundingSphere, sampleTerrainMostDetailed } = Cesium;
        const provider = this;
        /**
         * @type {MeshVisualizer}
         */
        const localScene = provider.localScene;
        // const uvUnit = provider.uvUnit;
        // const roundUv = provider.roundUv;
        // const useUvUnit = provider.useUvUnit;
        // const clampToGround = provider.clampToGround;
        // const terrainProvider = provider.terrainProvider;
        const { uvUnit, roundUv, useUvUnit, clampToGround, terrainProvider,
            receiveShadow, castShadow } = provider;

        function createContent(content) {

            let group, style = {
                useUvUnit: useUvUnit,
                roundUv: roundUv
            }, feature = content.uri;

            let featureId = feature.properties.featureId;
            styleFilter(feature, style);
            var extrudedHeight = style.extrudedHeight //|| 1
            var height = 0
            if (provider.clampToGround && feature.properties._terrianHeight
                && provider.terrainProvider) {
                height = feature.properties._terrianHeight;
            }

            let geometry = GeoPolygonGeometry.fromFeature(feature, provider.clampToGround ? height : null, height + extrudedHeight);

            if (extrudedHeight) {
                geometry = ExtrudedPolygonGeometry.createExtrudedUvNormal(geometry, height, style.useUvUnit ? style.uvUnit || uvUnit : null, style.roundUv);
                geometry = GeometryUtils.toBufferGeometry3js(geometry);
                geometry.attributes.uv = geometry.attributes.extrudedUv
                geometry.deleteAttribute('extrudedUv');
                geometry.deleteAttribute('extrudedNormal');
            } else {
                geometry = GeometryUtils.toBufferGeometry3js(geometry);
            }

            let ptCount = geometry.attributes.position.count;
            let batchIds = new Float32Array(ptCount);
            batchIds.fill(featureId);
            geometry.setAttribute('batchId', new BufferAttribute(batchIds, 1, false));

            let material = style.material;

            //pickColors

            material.pickColorQualifier = 'varying';
            material.pickColorName = 'pickColor';

            if (!geometry.boundingSphere) geometry.computeBoundingSphere();
            var pickId = getPickId(feature, provider);
            pickId.object.boundingSphere = BoundingSphere.clone(geometry.boundingSphere)

            var pickColors = [], { red, green, blue, alpha } = pickId.color;
            for (let i = 0; i < ptCount; i++) {
                pickColors.push(red, green, blue, alpha)
            }
            pickColors = new Float32Array(pickColors);
            geometry.setAttribute('pickColor', new BufferAttribute(pickColors, 4, false));

            if (rtcCenter) {

                let positions = geometry.attributes.position.array;
                for (let i = 0; i < positions.length; i += 3) {
                    positions[i] -= rtcCenter.x;
                    positions[i + 1] -= rtcCenter.y;
                    positions[i + 2] -= rtcCenter.z;
                }
            } else {

                let positions = geometry.attributes.position.array;
                var cartesian3 = new Cartesian3();
                var cartographic = new Cartographic()
                var hs = [], maxY = 0;
                for (let i = 0; i < positions.length; i += 3) {
                    cartesian3.x = positions[i]
                    cartesian3.y = positions[i + 1]
                    cartesian3.z = positions[i + 2]
                    Cartographic.fromCartesian(cartesian3, null, cartographic);
                    hs.push(cartographic.height - height)
                    localScene.worldCoordinatesToLocal(cartesian3, cartesian3, { y: 1 })
                    positions[i] = cartesian3.x
                    positions[i + 1] = cartesian3.y
                    positions[i + 2] = cartesian3.z

                    maxY = Math.max(cartesian3.y, maxY);
                }

                if (extrudedHeight) {
                    var eps = extrudedHeight * 0.1, id = 0;
                    for (let i = 0; i < hs.length; i++) {
                        const h = hs[i];
                        if (h >= eps) {//top
                            positions[id + 1] = maxY;
                        }
                        id += 3;
                    }
                }

                geometry.computeVertexNormals()
                geometry.computeBoundingSphere()
            }

            if (materialMap.has(material)) {
                group = materialMap.get(material);
            } else {
                group = {
                    material: material,
                    geometries: []
                };
                groups.push(group)
                materialMap.set(material, group);
            }
            group.geometries.push(geometry);
        }


        let batchList = [], batch = [], batchSize = 100, features = [];
        batchList.push(batch);
        contentSet.contain.forEach(content => {
            if (batch.length > batchSize) {
                batch = [];
                batchList.push(batch);
            }
            var feature = content.uri;
            if (feature.properties._terrianHeight == undefined) {
                features.push(feature);
            }
            batch.push(content)
        });
        contentSet.intersect.forEach(content => {
            if (batch.length > batchSize) {
                batch = [];
                batchList.push(batch);
            }
            var feature = content.uri;
            if (feature.properties._terrianHeight == undefined) {
                features.push(feature);
            }
            batch.push(content)
        });

        //
        var terrianPromise = null;
        if (clampToGround && terrainProvider && features.length) {
            terrianPromise = new Promise((resolve, reject) => {
                var positions = features.map(feature => {
                    var bbox = feature.geometry.bbox;
                    var lon = (bbox[0] + bbox[2]) / 2., lat = (bbox[1] + bbox[3]) / 2.;
                    return Cartographic.fromDegrees(lon, lat);
                })
                sampleTerrainMostDetailed(terrainProvider, positions).then(positions => {
                    for (let i = 0; i < positions.length; i++) {
                        const feature = features[i];
                        feature.properties._terrianHeight = positions[i].height || 0;
                    }
                    features.length = 0;
                    resolve();
                }).otherwise(err => {
                    features.length = 0;
                    reject(err)
                });
            })
        }

        return Promise.all([terrianPromise]).then(() => {

            return new GenericWorker(batchList, batch => {
                for (let i = 0; i < batch.length; i++) {
                    const el = batch[i];
                    try {
                        createContent(el)
                    } catch (err) {
                        console.error(err);
                    }
                }
            }).run().then(() => {

                let object3d = new Object3D();
                groups.forEach(group => {
                    let geometry = GeometryUtils.mergeGeometries3js(group.geometries);
                    let material = group.material;
                    let mesh = new Mesh(geometry, material);

                    mesh.pickObject = {
                        id: mesh,
                        mesh: mesh,
                        primitive: root
                    }
                    mesh.receiveShadow = receiveShadow;
                    mesh.castShadow = castShadow;
                    object3d.add(mesh);

                    if (rtcCenter) {
                        mesh.matrixAutoUpdate = false;
                        mesh.modelMatrixNeedsUpdate = false;
                    }
                    mesh.frustumCulled = false;

                    materialMap.delete(material)
                });

                if (rtcCenter) {
                    object3d.matrixAutoUpdate = false;
                    object3d.modelMatrixNeedsUpdate = false;
                }
                object3d.frustumCulled = false;

                groups.splice(0);
                // contentSet.contain.splice(0)
                // contentSet.intersect.splice(0)
                return object3d;
            });

        })

    }
}