
import GeometryUtils from  "../../../../core/Source/GeometryUtils.js";

import GeoPolygonGeometry from  "../../geometries/GeoPolygonGeometry.js";

import GeoMesh from  "./GeoMesh.js";


export default class GeoPolygonMesh extends GeoMesh {

    constructor(options) {
        const { MeshBasicMaterial } = THREE;
        const { defaultValue } = Cesium

        super(options);

        this.useRtcCenter = defaultValue(options.useRtcCenter, true);
        this.height = options.height || 0;
        this._material = options.material || new MeshBasicMaterial({ color: 'yellow', opacity: 0.5 })
        this.uvGenerator = options.uvGenerator;
        this.needsUpdate = false;
    }

    set material(val) {
        this._material = val;
        if (this.mesh) this.mesh.material = val;
    }
    get material() {
        return this._material;
    }

    createHighlightGeometry(feature) {
        const { height, uvGenerator } = this;
        var geometry = GeoPolygonGeometry.fromFeature(feature, height, null, uvGenerator);
        geometry = GeometryUtils.toBufferGeometry3js(geometry)
        return geometry;
    }

    createMesh(json) {
        const { BoundingSphere } = Cesium
        const { Mesh } = THREE;
        const { material, visualizer } = this;

        var geometries = [], features = json.features;
        const needsCreatePickId = !!visualizer._frameState && !!visualizer._frameState.context

        for (let i = 0; i < features.length; i++) {
            const feature = features[i];
            if (!feature.geometry) {
                continue;
            }
            var tempGeometry = GeoPolygonGeometry.fromFeature(feature, this.height, null, this.uvGenerator);
            needsCreatePickId && this.setPickId(feature, tempGeometry);
            geometries.push(tempGeometry);
        }

        var geometry = GeometryUtils.mergeGeometries(geometries);
        geometry = GeometryUtils.toBufferGeometry3js(geometry)
        if (!geometry.boundingSphere) geometry.computeBoundingSphere();
        BoundingSphere.clone(geometry.boundingSphere, this.boundingSphere);

        material.pickColorQualifier = 'varying';
        material.pickColorName = 'pickColor';

        //
        var mesh = new Mesh(geometry, this.material);
        if (this._rtcCenter) {
            this._rtcCenter = this.boundingSphere.center;
            mesh.up.set(0, 0, 1)
            mesh.modelMatrixNeedsUpdate = false;
            mesh.matrixAutoUpdate = false
        } else {
            this._rtcCenter = null
        }
        mesh.receiveShadow = true;

        this.transformGeometry(geometry, this._rtcCenter);

        return mesh;
    }

    render(frameSate, renderList) {
        if (this.needsUpdate && this.mesh) {
            this.visualizer.remove(this.mesh, true);
            this.initMesh()
            this.needsUpdate = false
        }
        super.render(frameSate, renderList)
    }
}
function DefaultUvGenerator(uvUnit) {
    return {
        uvUnit: uvUnit ? Object.assign({
            x: 40, y: 50,
        }, uvUnit) : {
            x: 40, y: 50,
        },
        generate(positions) {
            const { Cartesian3, Cartographic } = Cesium
            var _Math = Cesium.Math;
            var bbox = [Math.PI, Math.PI / 2, -Math.PI, -Math.PI / 2]
            positions = Cartesian3.unpackArray(positions);
            positions = positions.map(p => {
                var p2 = Cartographic.fromCartesian(p)
                bbox[0] = Math.min(bbox[0], p2.longitude)
                // bbox[2] = Math.max(bbox[2], p2.longitude)
                bbox[1] = Math.min(bbox[1], p2.latitude)
                // bbox[3] = Math.max(bbox[3], p2.latitude)
                return p2;
            });
            var uvs = []
            positions.forEach(p => {
                uvs.push(
                    (1000 * 110 * _Math.toDegrees(p.longitude - bbox[0])) / this.uvUnit.x,
                    (2 * 1000 * 110 * _Math.toDegrees(p.latitude - bbox[1])) / this.uvUnit.y
                )
            });
            return uvs;

        }
    }
}
GeoPolygonMesh.DefaultUvGenerator = DefaultUvGenerator;