import GeometryUtils from  "../../../../core/Source/GeometryUtils.js";

import GeoPolygonGeometry from  "../../geometries/GeoPolygonGeometry.js";

import GeoMesh from  "./GeoMesh.js";

import { Water } from  "./Water.js";


export default class GeoWaterMesh extends GeoMesh {

    constructor(options) {
        const { defaultValue } = Cesium
        const { TextureLoader, Vector3, RepeatWrapping } = THREE;

        super(options);
        this.skyBox = options.skyBox;
        this.waterNormals = options.waterNormals;
        this.alpha = defaultValue(options.alpha, 0.8);
        this.clampToGround = defaultValue(options.clampToGround, true);
        this._terrainProvider = null;
        this.waterParams = {
            textureWidth: 1024,
            textureHeight: 1024,
            waterNormals: new TextureLoader().load(this.waterNormals, function (texture) {
                texture.wrapS = texture.wrapT = RepeatWrapping;
            }),
            alpha: this.alpha,
            sunDirection: new Vector3(),
            sunColor: 0xffffff,
            waterColor: 0x001e0f,
            distortionScale: 3.7,
            fog: false//localScene.fog != undefined
        }

    }

    createGeomerty(feature, boundingSpheres) {
        const { Cartesian3, BoundingSphere } = Cesium;
        const localScene = this.visualizer;
        let height;
        if (this.clampToGround && this._terrainProvider &&
            feature.properties && feature.properties._terrainHeight
        ) {
            height = feature.properties._terrainHeight;
        }
        var geometry = GeoPolygonGeometry.fromFeature(feature, height)

        boundingSpheres.push(BoundingSphere.clone(geometry.boundingSphere));

        var postions = geometry.attributes.position.values

        for (let i = 0; i < postions.length; i += 3) {
            var x = postions[i], y = postions[i + 1], z = postions[i + 2];
            var cart3 = new Cartesian3(x, y, z);
            localScene.worldCoordinatesToLocal(cart3, cart3, Cartesian3.UNIT_Y)
            // cart3.y = 0;
            postions[i] = cart3.x
            postions[i + 1] = cart3.y
            postions[i + 2] = cart3.z
        }
        geometry = GeometryUtils.toBufferGeometry3js(geometry);
        geometry.computeBoundingSphere();
        geometry.computeVertexNormals();

        return geometry;
    }

    createMesh(json) {

        if (!this.visualizer) return;

        const { Object3D } = THREE;
        var boundingSpheres = [];
        var root = new Object3D();

        for (let index = 0; index < json.features.length; index++) {
            const feature = json.features[index];
            const waterGeometry = this.createGeomerty(feature, boundingSpheres);
            var water = new Water(waterGeometry, this.waterParams);
            root.add(water);
        }

        Cesium.BoundingSphere.fromBoundingSpheres(boundingSpheres, this.boundingSphere);
        return root;
    }

    render(frameSate, renderList) {
        if (this.ready && !this.mesh) {
            this.initMesh();
        }
        if (!this.ready || !this.mesh || !this.show) return;
        this.pickId = this.mesh.pickId;
        renderList.push(this.mesh);

        /**
         * @type {Cesium.Scene}
         */
        const scene = this.visualizer.scene;
        const { EllipsoidTerrainProvider, Cartographic, sampleTerrainMostDetailed } = Cesium

        var terrainProvider;
        if (!this.clampToGround || scene.terrainProvider instanceof EllipsoidTerrainProvider) {
            terrainProvider = null
        } else {
            terrainProvider = scene.terrainProvider
        }

        if (terrainProvider != this._terrainProvider) {

            this._terrainProvider = terrainProvider;
            if (!terrainProvider) {
                this.initMesh()
            }
            else if (!this._isUpdatingTerrainProperties) {
                this._isUpdatingTerrainProperties = true;
                var features = []
                this.source.features.forEach(feature => {
                    if (!feature.geometry) return;
                    features.push(feature);
                });

                var positions = features.map(feature => {
                    var bbox = feature.geometry.bbox;
                    var lon = (bbox[0] + bbox[2]) / 2, lat = (bbox[1] + bbox[3]) / 2;
                    return Cartographic.fromDegrees(lon, lat)
                });

                sampleTerrainMostDetailed(terrainProvider, positions).then(() => {
                    for (let i = 0; i < features.length; i++) {
                        const feature = features[i];
                        feature.properties = feature.properties || {};
                        feature.properties._terrainHeight = positions[i].height;
                    }
                    positions.length = 0;

                    this.initMesh();

                    this._isUpdatingTerrainProperties = false;
                }).otherwise((err) => {
                    positions.length = 0
                    console.error(err);
                })
            }
        }

        const skyBox = this.skyBox;
        const sunPosition = skyBox ? skyBox.sunPosition : this.visualizer._sunPositionLC;

        this.mesh.children.forEach(water => {
            const waterMaterial = water.material;
            waterMaterial.uniforms.sunDirection.value.copy(sunPosition).normalize()
            waterMaterial.uniforms['time'].value += 1.0 / 60.0;
        })

    }
}