
/**
 * 
 * @param {object} parameters 
 * @param {THREE.Color} parameters.lineColor 
 * @param {number} parameters.lineOpacity
 * @param {THREE.Color} parameters.faceColor
 * @param {number} parameters.faceOpacity
 * @constructor
 * @extends THREE.ShaderMaterial
 */
function SolidWireframeMaterial(parameters = {}) {
    const { ShaderMaterial, Color } = THREE;
    var shader = SolidWireframeMaterial.Shader
    parameters = Object.assign({
        uniforms: {
            "lineWidth": { type: "f", value: typeof parameters.lineWidth == 'number' ? parameters.lineWidth : 4.0 },
            "faceColor": { type: "c", value: parameters.faceColor || new Color(0x222222) },
            "faceOpacity": { type: "f", value: typeof parameters.faceOpacity == 'number' ? parameters.faceOpacity : 1.0 },
            "lineColor": { type: "c", value: parameters.lineColor || new Color(0xffffff) },
            "lineOpacity": { type: "f", value: typeof parameters.lineOpacity == 'number' ? parameters.lineOpacity : 1.0 }
        },
        vertexShader: shader.vertexShader,
        fragmentShader: shader.fragmentShader
    }, parameters);
    delete parameters.lineWidth
    delete parameters.faceColor
    delete parameters.faceOpacity
    delete parameters.lineColor
    delete parameters.lineOpacity
     
    var material = new ShaderMaterial(parameters);

    var uniforms = material.uniforms;
    const defineProperty = Object.defineProperty;
    function defNumProp(name) {
        defineProperty(material, name, {
            get() {
                return uniforms[name].value
            },
            set(val) {
                uniforms[name].value = val
            }
        })
    }
    function defColorProp(name) {
        defineProperty(material, name, {
            get() {
                return uniforms[name].value
            },
            set(val) {
                if (val != null && val != undefined)
                    uniforms[name].value.set(val)
            }
        })
    }
    defColorProp('lineColor')
    defColorProp('faceColor')
    defNumProp('lineWidth')
    defNumProp('faceOpacity')
    defNumProp('lineOpacity')
    return material;
}
SolidWireframeMaterial.Shader = {
    vertexShader: [
        "attribute vec4 center;",
        "varying vec4 vCenter;",

        "void main() {",
        "   vCenter = center;",
        "   gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );",
        "}",
    ].join('\n'),

    fragmentShader: [
        "#extension GL_OES_standard_derivatives : enable",

        "varying vec4 vCenter;",
        // control parameter
        "uniform float lineWidth;",
        "uniform float faceOpacity;",
        "uniform float lineOpacity;",
        "uniform vec3 faceColor;",
        "uniform vec3 lineColor;",

        "float _lineWidth;",
        "float edgeFactorTri() {",
        "   vec3 d = fwidth( vCenter.xyz );",
        "   vec3 a3 = smoothstep( vec3( 0.0 ), d * _lineWidth , vCenter.xyz );",
        "   return min( min( a3.x, a3.y ), a3.z );",
        "}",

        "float edgeFactorQuad1() {",
        "   vec2 d = fwidth( vCenter.xy );",
        "   vec2 a2 = smoothstep( vec2( 0.0 ), d * _lineWidth , vCenter.xy );",
        "   return min( a2.x, a2.y );",
        "}",

        "float edgeFactorQuad2() {",
        "   vec2 d = fwidth( 1.0 - vCenter.xy );",
        "   vec2 a2 = smoothstep( vec2( 0.0 ), d * _lineWidth , 1.0 - vCenter.xy );",
        "   return min( a2.x, a2.y );",
        "}",

        "void main() {",
        "   _lineWidth=lineWidth * czm_pixelRatio;",
        "   vec4 _lineColor=vec4( lineColor , lineOpacity );",
        "   vec4 _faceColor=vec4( faceColor , faceOpacity );",
        // "   if(length(vCenter)==0.)gl_FragColor =_faceColor;",
        // "else",
        "   if ( vCenter.w == 0.0 ) {",
        "       gl_FragColor = mix( _lineColor , _faceColor , edgeFactorTri() );",
        "   } else {",
        "       gl_FragColor = mix( _lineColor, _faceColor, min( edgeFactorQuad1(), edgeFactorQuad2() ) );",
        "   }",
        "}",
    ].join('\n')
};

export default SolidWireframeMaterial;