// import MeshVisualizer from "../../../core/MeshVisualizer.js";
// import ExtrudeGradientMaterial from "../../../materials/ExtrudeGradientMaterial.js";
import BuildingVector3DLoader from  "../../../../renderable/Source/BuildingVector3DLoader.js";

// import Vector3DGradientProvider from "./Vector3DGradientProvider.js";
import Vector3DWireframeProvider from  "./Vector3DWireframeProvider.js";


import Earth from  "../../Earth.js";

import ILayer from  "../ILayer.js";

import SolidWireframeMaterial from  "../../materials/SolidWireframeMaterial.js";

import Vector3DStandardProvider from  "./Vector3DStandardProvider.js";


class Vector3dLayer extends ILayer {
    /**
     * 
     * @param {object} params 
     * @param {string} params.url 
     * @param {string} [params.property='Floor'] geojson
     * @param {string} [params.scale=4] 
     * @param {'gradient'|'standard'|'wireframe'} [params.style='gradient'] 
     * @param {THREE.Material} [params.material]
     * @param {THREE.Vec2|Cesium.Cartesian2} [params.uvUnit]
     * @param {number} [params.lodMinRange=8]
     * @param {boolean} [params.debugVolume]
     */
    constructor(params) {
        const { Color, defaultValue, Event } = Cesium;

        // if (params.clampToGround && !params.terrainProvider) {
        //     throw new Error('Vector3dLayer#constructor:terrainProvider is required when clampToGround is true')
        // }
        params.style = params.style || 'standard'
        super(params);
        this.url = params.url;
        this.useRtcCenter = defaultValue(params.useRtcCenter, params.style != 'standard');
        this._property = params.property || 'Floor'
        this._scale = typeof params.scale == 'number' ? params.scale : 4
        this.lodMinRange = params.lodMinRange || 8;
        this.bottomColor = params.bottomColor || Color.DARKBLUE.clone();
        this.topColor = params.topColor || Color.CYAN.clone();
        this.debugVolume = defaultValue(params.debugVolume, false);
        this.style = params.style;
        this._styleFilter = params.styleFilter;
        this._uvUnit = params.uvUnit;
        this._roundUv = defaultValue(params.roundUv, false);
        this._lodContentProvider = null;
        this._material = params.material;
        this._terrainProvider = null;//params.terrainProvider
        this._clampToGround = defaultValue(params.clampToGround, false);
        this._receiveShadow = defaultValue(params.receiveShadow, true);
        this._castShadow = defaultValue(params.castShadow, true);

        this.root = null;
        this.onBeforeLoad = new Event();
        this.onLoading = new Event()
        this.onLoaded = new Event();
        this.onDataLoaded = new Event()
    }
    get property() {
        return this._property
    }
    set property(val) {
        if (this._property != val) {
            this._property = val;
            this.needsUpdate = true;
        }
    }
    get scale() {
        return this._scale
    }
    set scale(val) {
        if (this._scale != val) {
            this._scale = val;
            this.needsUpdate = true;
        }
    }

    get receiveShadow() {
        return this._receiveShadow
    }
    set receiveShadow(val) {
        if (this._receiveShadow != val) {
            this._receiveShadow = val;
            if (this._lodContentProvider) {
                this._lodContentProvider.receiveShadow = val
            }
            this.needsUpdate = true;
        }
    }
    get castShadow() {
        return this._castShadow
    }
    set castShadow(val) {
        if (this._castShadow != val) {
            this._castShadow = val;
            if (this._lodContentProvider) {
                this._lodContentProvider.castShadow = val
            }
            this.needsUpdate = true;
        }
    }

    get material() {
        return this._material;
    }
    set material(val) {
        if (this._material != val) {
            this._material = val;
            this.needsUpdate = true;
        }
    }

    get contentProvider() {
        return this._lodContentProvider;
    }

    set clampToGround(val) {
        if (this._clampToGround != val) {
            this._clampToGround = val;
            if (this._lodContentProvider) {
                this._lodContentProvider.clampToGround = val
            }
            this.needsUpdate = true;
        }
    }
    get clampToGround() {
        return this._clampToGround;
    }

    set terrainProvider(val) {
        if (this._terrainProvider != val) {
            this._terrainProvider = val;
            if (this._lodContentProvider) {
                this._lodContentProvider.terrainProvider = val
            }
            this.needsUpdate = true;
        }
    }
    get terrainProvider() {
        return this._terrainProvider;
    }

    set roundUv(val) {
        if (val != this._roundUv) {
            this._roundUv = val
            if (this._lodContentProvider) {
                this._lodContentProvider.roundUv = this._roundUv;
            }
            this.needsUpdate = true;
        }
    }
    get roundUv() {
        return this._roundUv;
    }

    set uvUnit(val) {
        if (this._uvUnit != val) {
            this._uvUnit = val;
            if (this._lodContentProvider) {
                this._lodContentProvider.uvUnit = this.uvUnit;
            }
            this.needsUpdate = true;
        }

    }
    get uvUnit() {
        return this._uvUnit;
    }

    set styleFilter(val) {
        if (this.style != 'standard') return;
        if (typeof val == 'function' && val != this._styleFilter) {
            this._styleFilter = val;
            if (this._lodContentProvider) this._lodContentProvider.styleFilter = val;
            this.needsUpdate = true;
        }
    }
    get styleFilter() {
        return this._styleFilter;
    }

    set needsUpdate(val) {
        if (val && this.root) {
            this.root.needsUpdate = true;
        }
    }
    set show(val) {
        this._show = val;
        if (this.root) this.root.show = val
    }
    get show() {
        return this._show
    }
    get boundingSphere() {
        if (this.root) {
            return this.root.boundingSphereWC
        }
    }

    /**
     * 
     * @param {Earth|MeshVisualizer} earth 
     */
    load(earth) {
        this.onBeforeLoad.raiseEvent(this);

        var localScene = earth.isMeshVisualizer ? earth : earth.globalScene;

        let material = this.material,
            contentProviderClass = Vector3DStandardProvider;
        switch (this.style) {
            // case 'gradient':
            //     material = new ExtrudeGradientMaterial({
            //         useExtrudedUvNormal: true,
            //         uniforms: {
            //             bottomColor: this.bottomColor,
            //             topColor: this.topColor
            //         }
            //     })
            //     contentProviderClass = Vector3DGradientProvider
            //     break;
            case 'wireframe':
                material = new SolidWireframeMaterial({
                    lineColor: new THREE.Color('white'),
                    faceColor: new THREE.Color(0x84fa),
                    lineWidth: 1,
                    faceOpacity: 0.5,
                    transparent: true,
                    depthTest: false
                })
                contentProviderClass = Vector3DWireframeProvider
                break;
        }
        this.material = material;

        let styleFilter = this.style == 'standard' && typeof this.styleFilter == 'function' ? this.styleFilter : (feature, style) => {
            style.extrudedHeight = feature.properties[this.property] * this.scale;
            style.material = this.material;
        }

        let lodContentProvider = new contentProviderClass(null, styleFilter, localScene, this.uvUnit, this.roundUv)
        this._lodContentProvider = lodContentProvider;
        lodContentProvider.clampToGround = this.clampToGround;
        lodContentProvider.terrainProvider = this.terrainProvider;

        BuildingVector3DLoader.load(this.url, styleFilter, (progress) => {
            // console.log('Loading:' + progress.toFixed(1) + '%');
            this.onLoading.raiseEvent(this, progress);
        }, geojson => {
            this.onDataLoaded.raiseEvent(geojson);
        }, {
            minRange: this.lodMinRange,
            debugVolume: true,
            showDebugVolume: this.debugVolume,
            debugVolumeColor: Cesium.Color.BLACK,
            useRtcCenter: this.useRtcCenter,
            getLodContentProvider: (root, styleFilter) => {
                lodContentProvider.root = root;
                return lodContentProvider
            }
        }).then(root => {
            this.root = root;
            root.show = this.show
            localScene.add(root);
            this.onLoaded.raiseEvent(this);
        })

        /**
         * @type {Cesium.Scene}
         */
        var scene = localScene.scene
        this._removeBeforeUpdateCb = localScene.beforeUpdate.addEventListener(() => {
            if (scene.terrainProvider instanceof Cesium.EllipsoidTerrainProvider) {
                this.terrainProvider = null;
            } else if (this._terrainProvider != scene.terrainProvider) {
                this.terrainProvider = scene.terrainProvider;
            }
        })
    }

    /**
     * 
     * @param {Earth|MeshVisualizer} earth 
     */
    unload(earth) {
        let { _removeBeforeUpdateCb, root, _lodContentProvider } = this;
        if (_removeBeforeUpdateCb)
            _removeBeforeUpdateCb()
        if (!root) return;
        _lodContentProvider.destroy && _lodContentProvider.destroy()
        this.root = root.destroy();
    }
}
export default Vector3dLayer