
import pickPosition from  "./pickPosition.js";


/**
 * 
 * @param {Cesium.Viewer} viewer 
 * @param {(point:Cesium.Cartographic)=>void}callback
 * @param {object}[options]
 * @param {object}[options.pointSize=8]
 * @param {Cesium.Color}[options.pointColor=Cesium.Color.DARKORANGE]
 * @param {(point:Cesium.Cartographic)=>void}[options.onMove]
 * @returns {()=>void} cancelCallback
 */
export default function getPoint(viewer, callback, options) {
    if (!callback) throw new Error('callback')
    options = options || {};

    const {
        ScreenSpaceEventType, CallbackProperty,
        Color, Cartesian3, HeightReference, Cartographic
    } = Cesium;
    const { LEFT_CLICK, MOUSE_MOVE } = ScreenSpaceEventType;
    const handler = viewer.screenSpaceEventHandler;
    var pointSize = options.pointSize || 8;
    var pointColor = options.pointColor || Color.DARKORANGE
    var onMove = options.onMove;

    //
    var state = {
        [LEFT_CLICK]: handler.getInputAction(LEFT_CLICK),
        [MOUSE_MOVE]: handler.getInputAction(MOUSE_MOVE)
    }
    var position = new Cartesian3()
    var positionCarg = new Cartographic()
    var ellipsoid = viewer.scene.globe.ellipsoid;

    var entity = viewer.entities.add({
        position: new CallbackProperty(() => {
            return position
        }, false),
        show: false,
        point: {
            pixelSize: pointSize,
            color: Color.fromAlpha(pointColor, 1),
            outlineColor: pointColor,
            outlineWidth: 1.5,
            outline: true,
            heightReference: HeightReference.CLAMP_TO_GROUND
        }
    })

    handler.setInputAction(e => {
        var p = pickPosition(viewer.scene, e.position)
        if (!p) {
            return
        }
        position = p;
        end()
    }, LEFT_CLICK);

    handler.setInputAction(e => {
        var p = pickPosition(viewer.scene, e.endPosition)
        if (!p) {
            return;
        }
        position = p
        entity.show = true
        onMove && onMove(Cartographic.fromCartesian(position, ellipsoid, positionCarg));
    }, MOUSE_MOVE);

    //
    function end(cancel) {
        if (state[LEFT_CLICK]) handler.setInputAction(state[LEFT_CLICK], LEFT_CLICK);
        else handler.removeInputAction(LEFT_CLICK);
        if (state[MOUSE_MOVE]) handler.setInputAction(state[MOUSE_MOVE], MOUSE_MOVE);
        else handler.removeInputAction(MOUSE_MOVE);

        viewer.entities.remove(entity);

        if (!cancel) {
            callback(Cartographic.fromCartesian(position, ellipsoid, positionCarg), {
                position: position,
                point: {
                    pixelSize: pointSize,
                    color: Color.fromAlpha(pointColor, 1),
                    outlineColor: pointColor,
                    outlineWidth: 1.5,
                    outline: true,
                    heightReference: HeightReference.CLAMP_TO_GROUND
                }
            })
        }
        state = entity = position = positionCarg = null;
    }

    function cancel() {
        end(true)
    }

    return cancel;
}
