import { Node } from  "./Node.js";

import {CenterMode} from  "../Enum/CenterMode.js";

import {RangeMode} from  "../Enum/RangeMode.js";


class LOD extends Node {
    constructor() {
        super();

        this.CenterMode =  CenterMode.USE_BOUNDING_SPHERE_CENTER;
        this.RangeMode = RangeMode.DISTANCE_FROM_EYE_POINT;
        this.UserCenter = { center: [], radius: 0 };
        this.RangeList = [];
        this.Type = "Osg::LOD";
    }
}
 

export { LOD };