import getRactangleCornersWC from  "./getRactangleCornersWC.js";

import pickPosition from  "./pickPosition.js";


/**
 * 
 * @param {Cesium.Viewer} viewer 
 * @param {(rectangle:Cesium.Rectangle)=>void}callback
 * @param {object}[options]
 * @param {Cesium.Color}[options.areaColor=Color.DARKORANGE]
 * @param {Cesium.Color}[options.pointColor=Color.DARKORANGE]
 * @param {number}[options.pointSize=8]
 * @param {(rectangle:Cesium.Rectangle)=>void}[options.onMove]
 * @returns {()=>void} cancelCallback
 */
export default function getRactangle(viewer, callback, options) {
    if (!callback) throw new Error('callback')
    options = options || {};
    
    const {
        ScreenSpaceEventType, CallbackProperty,
        Color, Rectangle,
        Cartographic, HeightReference
    } = Cesium;
    const { LEFT_CLICK, MOUSE_MOVE, LEFT_DOUBLE_CLICK } = ScreenSpaceEventType;
    const handler = viewer.screenSpaceEventHandler;
    var areaColor = options.areaColor || Color.DARKORANGE;
    var pointColor = options.pointColor || Color.DARKORANGE;
    var pointSize = options.pointSize || 8;
    var onMove = options.onMove;

    //
    var state = {
        [LEFT_CLICK]: handler.getInputAction(LEFT_CLICK),
        [MOUSE_MOVE]: handler.getInputAction(MOUSE_MOVE) 
    }
    var rectangle = new Rectangle(), points = [], polyline = [];

    //entity
    var rectangleEntity = viewer.entities.add({
        show: false,
        rectangle: {
            coordinates: new CallbackProperty(() => {
                return rectangle
            }, false),
            outline: true,
            fill: true,
            material: Color.fromAlpha(areaColor, 0.15),
            show: new CallbackProperty(() => {
                return rectangleEntity.show && polyline.length > 2
            }, false)
        },
        polyline: {
            positions: new CallbackProperty(() => {
                return polyline
            }, false),
            show: new CallbackProperty(() => {
                return rectangleEntity.show && polyline.length > 2
            }, false),
            clampToGround: true,
            width: 1.5,
            material: areaColor
        }
    })

    function addVertEntity(idx) {
        return viewer.entities.add({
            position: new CallbackProperty(() => {
                return polyline[idx]
            }, false),
            point: {
                show: new CallbackProperty(() => {
                    if (idx == 0) return polyline.length > 0 || points.length > 0;
                    else return polyline.length > 1;
                }, false),
                pixelSize: pointSize,
                color: pointColor,
                outlineColor: pointColor,
                outlineWidth: 1.5,
                outline: true,
                heightReference: HeightReference.CLAMP_TO_GROUND
            }
        })
    }
    //
    var vertEntities = [
        addVertEntity(0), addVertEntity(1),
        addVertEntity(2), addVertEntity(3)
    ];

    function udpateRectangle() {
        polyline = getRactangleCornersWC(rectangle);
        polyline.push(polyline[0])
        Rectangle.fromCartographicArray(points, rectangle)
    }

    handler.setInputAction(e => {
        if (points.length == 2) {
            points.length = 0;
            return end()
        }

        var p = pickPosition(viewer.scene, e.position)
        if (!p
            || !(p = Cartographic.fromCartesian(p))
            || Cartographic.equals(p, points[1])
        ) {
            return
        }

        if (points.length == 0) {//
            polyline.length = 0;
            points[0] = p
            polyline[0] = Cesium.Cartesian3.fromRadians(p.longitude, p.latitude, p.height)
        } else {//
            points[1] = p
            udpateRectangle()
        }
    }, LEFT_CLICK);

    handler.setInputAction(e => {
        if (points.length == 0) return;
        var p = pickPosition(viewer.scene, e.endPosition)
        if (!p
            || !(p = Cartographic.fromCartesian(p))
            || Cartographic.equals(p, points[1])
        ) {
            return;
        }

        points[1] = p
        rectangleEntity.show = true
        udpateRectangle()
        if (onMove) {
            onMove(rectangle)
        }
    }, MOUSE_MOVE);

    //
    function end(cancel) {
        if (state[LEFT_CLICK]) {
            handler.setInputAction(state[LEFT_CLICK], LEFT_CLICK);
        }
        else {
            handler.removeInputAction(LEFT_CLICK);
        }
        if (state[MOUSE_MOVE]) {
            handler.setInputAction(state[MOUSE_MOVE], MOUSE_MOVE);
        }
        else {
            handler.removeInputAction(MOUSE_MOVE);
        }
        
        viewer.entities.remove(rectangleEntity);
        for (let i = 0; i < vertEntities.length; i++) {
            viewer.entities.remove(vertEntities[i]);
        }
        if (!cancel) {
            callback(rectangle)
        }
        vertEntities = rectangle = rectangleEntity = null;
        state = points = polyline = null;
    }

    function cancel() {
        end(true)
    }

    return cancel;

}
