import pickPosition from  "./pickPosition.js";


/**
 * 
 * @param {Cesium.Viewer} viewer 
 * @param {(line:Cesium.Cartographic[],entities:Cesium.Entity[])=>void}callback
 * @param {object}[options]
 * @param {Cesium.Color}[options.lineColor=Color.DARKORANGE]
 * @param {Cesium.Color}[options.pointColor=Color.DARKORANGE]
 * @param {number}[options.pointSize=8]
 * @param {(line:Cesium.Cartographic[])=>void}[options.onMove]
 * @param {(point:Cesium.Cartographic)=>void}[options.onStart]
 * @returns {()=>void} cancelCallback
 */
export default function getLine(viewer, callback, options) {
    if (!callback) throw new Error('callback')
    options = options || {};

    const {
        ScreenSpaceEventType, CallbackProperty,
        Color, Cartesian3, HeightReference, Cartographic
    } = Cesium;
    const { LEFT_CLICK, MOUSE_MOVE } = ScreenSpaceEventType;
    const handler = viewer.screenSpaceEventHandler;
    var lineColor = options.lineColor || Color.DARKORANGE
    var pointColor = options.pointColor || Color.DARKORANGE
    var pointSize = options.pointSize || 8
    var onMove = options.onMove;
    var onStart = options.onStart;

    //
    var state = {
        [LEFT_CLICK]: handler.getInputAction(LEFT_CLICK),
        [MOUSE_MOVE]: handler.getInputAction(MOUSE_MOVE)
    }
    var points = [];

    //entity
    var lineEntity = viewer.entities.add({
        show: false,
        polyline: {
            positions: new CallbackProperty(() => {
                return points
            }, false),
            show: new CallbackProperty(() => {
                return lineEntity.show && points.length > 1
            }, false),
            clampToGround: true,
            width: 1.5,
            material: lineColor
        }
    })

    function addPointEntity(idx) {
        return viewer.entities.add({
            position: new CallbackProperty(() => {
                return points[idx]
            }, false),
            point: {
                show: new CallbackProperty(() => {
                    if (idx == 0) return points.length > 0;
                    else return points.length > 1;
                }, false),
                pixelSize: pointSize,
                color: pointColor,
                heightReference: HeightReference.CLAMP_TO_GROUND
            }
        })
    }
    //
    var pointEntities = [
        addPointEntity(0), addPointEntity(1)
    ];

    handler.setInputAction(e => {
        if (points.length == 2) {
            return end()
        }

        var p = pickPosition(viewer.scene, e.position)
        if (!p || Cartesian3.equals(p, points[1])) {
            return
        }

        if (points.length == 0) {// 
            points[0] = p
            if (onStart) {
                onStart(Cartographic.fromCartesian(p))
            }
        } else {//
            points[1] = p
        }
    }, LEFT_CLICK);

    handler.setInputAction(e => {
        if (points.length == 0) return;
        var p = pickPosition(viewer.scene, e.endPosition)
        if (!p || Cartesian3.equals(p, points[1])) {
            return;
        }

        points[1] = p
        lineEntity.show = true
        if (onMove) {
            onMove(points.map(p => {
                return Cartographic.fromCartesian(p)
            }));
        }
    }, MOUSE_MOVE);

    //
    function end(cancel) {
        if(state[LEFT_CLICK])handler.setInputAction(state[LEFT_CLICK], LEFT_CLICK);
        else handler.removeInputAction(LEFT_CLICK);
        if (state[MOUSE_MOVE]) handler.setInputAction(state[MOUSE_MOVE], MOUSE_MOVE);
        else handler.removeInputAction(MOUSE_MOVE);

        viewer.entities.remove(lineEntity);
        for (let i = 0; i < pointEntities.length; i++) {
            viewer.entities.remove(pointEntities[i]);
        }
        if (!cancel) {
            var entities = [{
                polyline: {
                    positions: points,
                    clampToGround: true,
                    width: 1.5,
                    material: lineColor
                }
            }];

            points = points.map(p => {
                entities.push({
                    position: p,
                    point: {
                        pixelSize: pointSize,
                        color: pointColor,
                        heightReference: HeightReference.CLAMP_TO_GROUND
                    }
                })
                return Cartographic.fromCartesian(p)
            });
            callback(points, entities)
        }
        pointEntities = lineEntity = null;
        state = points = null;
    }
    function cancel() {
        end(true)
    }
    return cancel;
}
