export default class MathUtils {
    static add(arg1, arg2) {
        var r1, r2, m;
        try {
            r1 = arg1.toString().split(".")[1].length;
            r2 = arg2.toString().split(".")[1].length;
        } catch (e) {
            r1 = 0; r2 = 0;
        }
        m = Math.pow(10, Math.max(r1, r2));

        return (arg1 * m + arg2 * m) / m;
    }
    static sub(arg1, arg2) {
        return this.add(arg1, -arg2);
    }
    static mul(arg1, arg2) {
        var r1, r2, m;
        try {
            //
            r1 = arg1.toString().split(".")[1].length;
            r2 = arg2.toString().split(".")[1].length;
        } catch (e) {
            r1 = 0; r2 = 0;
        }
        m = Math.pow(10, Math.max(r1, r2));		//

        return (arg1 * m) * (arg2 * m) / (m * m);
    }
    static div(arg1, arg2) {
        if (arg2 == 0) return 0;
        var t1 = 0, t2 = 0, r1, r2;
        try {
            t1 = arg1.toString().split(".")[1].length;
            t2 = arg2.toString().split(".")[1].length;
        } catch (e) { }

        // with (Math) {
        r1 = Number(arg1.toString().replace(".", ""));
        r2 = Number(arg2.toString().replace(".", ""));
        // }

        return (r1 / r2) * Math.pow(10, t2 - t1);
    }
    /**
     * 
     * @param {number[]} arr 
     * @param {number} [offset=0]
     * @param {number} [stride=1]  
     * @returns {{min:number,max:number}} {min,max}
     */
    static minmax(arr, offset, stride) {
        offset = offset || 0;
        stride = stride || 1;
        let min = Number.MAX_VALUE, max = -Number.MAX_VALUE;
        for (let i = offset; i < arr.length; i += stride) {
            const v = arr[i];
            if (isNaN(v) || v == Infinity || v == -Infinity) continue;
            min = Math.min(min, v);
            max = Math.max(max, v);
        }
        return { min, max }
    }

    /**
     * 
     * @param {Cesium.Cartesian2} p1 
     * @param {Cesium.Cartesian2} p2 
     * @param {Cesium.Cartesian2} p3 
     * @param {Cesium.Cartesian2} p4 
     * @param {Cesium.Cartesian2} crossPoint 
     * @returns {number}
     * @example
     * p1    p3
     *   \   /
     *    \ /
     *  crossPoint
     *    / \
     *   /   \
     *  p4   p2
     * 
     */
    static findCrossPoint(p1, p2, p3, p4, crossPoint, onSegments) {
        //****************************************************************************************
        //  
        //   (x-x1)/(y-y1) = (x2-x1)/(y2-y1) ==> a1*x+b1*y=c1
        //            (x-x3)/(y-y3) = (x4-x3)/(y4-y3) ==> a2*x+b2*y=c2
        //  
        //                x= | c1 b1|  / | a1 b1 |      y= | a1 c1| / | a1 b1 |
        //                   | c2 b2|  / | a2 b2 |         | a2 c2| / | a2 b2 |
        //
        //   a1= y2-y1
        //   b1= x1-x2
        //   c1= x1*y2-x2*y1
        //   a2= y4-y3
        //   b2= x3-x4
        //   c2= x3*y4-x4*y3

        // if (typeof Decimal != 'undefined') {
        //     var x1 = Decimal(p1.x), x2 = Decimal(p2.x), x3 = Decimal(p3.x), x4 = Decimal(p4.x);
        //     var y1 = Decimal(p1.y), y2 = Decimal(p2.y), y3 = Decimal(p3.y), y4 = Decimal(p4.y);
        //     var a1 = y2.sub(y1)
        //     var b1 = x1.sub(x2)
        //     var c1 = x1.mul(y2).sub(x2.mul(y1))
        //     var a2 = y4.sub(y3)
        //     var b2 = x3.sub(x4)
        //     var c2 = x3.mul(y4).sub(x4.mul(y3))
        //     var det = a1.mul(b2).sub(a2.mul(b1));

        //     if (det.equals(0)) {
        //         return 0;
        //     }

        //     var x = (c1.mul(b2).sub(c2.mul(b1))).div(det);
        //     var y = (a1.mul(c2).sub(a2.mul(c1))).div(det);

        //     crossPoint.x = x.toNumber();
        //     crossPoint.y = y.toNumber();

        //     if (!onSegments) {
        //         return det.toNumber();
        //     }

        //     // Now this is cross point of lines
        //     // Do we need the cross Point of segments(need to judge x,y within 4 endpoints)
        //     //  
        //     if ( x.sub( x1.add(x2).div(2.) ).abs().lessThanOrEqualTo( ( x2.sub(x1) ).abs().div(2.) ) &&
        //          y.sub( y1.add(y2).div(2.) ).abs().lessThanOrEqualTo( ( y2.sub(y1) ).abs().div(2.) ) &&
        //          x.sub( x3.add(x4).div(2.) ).abs().lessThanOrEqualTo( ( x4.sub(x3) ).abs().div(2.) ) &&
        //          y.sub( y3.add(y4).div(2.) ).abs().lessThanOrEqualTo( ( y4.sub(y3) ).abs().div(2.) )) {
        //         return det.toNumber();
        //     } else {
        //         return 0;
        //     }
        // } else {
        var x1 = p1.x, x2 = p2.x, x3 = p3.x, x4 = p4.x;
        var y1 = p1.y, y2 = p2.y, y3 = p3.y, y4 = p4.y;
        var a1 = y2 - y1
        var b1 = x1 - x2
        var c1 = x1 * y2 - x2 * y1
        var a2 = y4 - y3
        var b2 = x3 - x4
        var c2 = x3 * y4 - x4 * y3
        var det = a1 * b2 - a2 * b1;

        if (det == 0) {
            return det;
        }

        crossPoint.x = (c1 * b2 - c2 * b1) / det;
        crossPoint.y = (a1 * c2 - a2 * c1) / det;

        if (!onSegments) {
            return det;
        }

        var abs = Math.abs;
        // Now this is cross point of lines
        // Do we need the cross Point of segments(need to judge x,y within 4 endpoints)
        //  
        if ((abs(crossPoint.x - (x1 + x2) / 2.) <= abs(x2 - x1) / 2.) &&
            (abs(crossPoint.y - (y1 + y2) / 2.) <= abs(y2 - y1) / 2.) &&
            (abs(crossPoint.x - (x3 + x4) / 2.) <= abs(x4 - x3) / 2.) &&
            (abs(crossPoint.y - (y3 + y4) / 2.) <= abs(y4 - y3) / 2.)) {
            return det;
        } else {
            return 0;
        }
        // }
    }

    static getDigits(num) {
        var str = num.toString().split('.')[1];
        return str ? str.length : 0;
    }
    static clampToLine(q, p1, p2) {
        const Cartesian2 = Cesium.Cartesian2;
        const v1 = Cartesian2.subtract(q, p1, new Cartesian2())
        const v2 = Cartesian2.subtract(p2, p1, new Cartesian2())
        const det = v1.x * v2.y - v2.x * v1.y;
        if (Math.abs(det) < 1e-16) return q;
        var d = Cartesian2.magnitude(v1);
        var angle = Cartesian2.angleBetween(v1, v2);
        if (angle < 1e-16) return q;
        // d = Math.cos(angle) * d;
        Cartesian2.normalize(v2, v2);
        Cartesian2.multiplyByScalar(v2, d, v2);
        return Cartesian2.add(p1, v2, q);
    }

    static distanceToSegment(q, p1, p2) {
        const Cartesian2 = Cesium.Cartesian2, _Math = Cesium.Math;
        let v1 = Cartesian2.subtract(q, p1, new Cartesian2())
        let v2 = Cartesian2.subtract(p2, p1, new Cartesian2());
        var d = Cartesian2.magnitude(v1);
        if (Math.abs(d) <= 1e-16) return d;
        let angle = Cartesian2.angleBetween(v1, v2);
        d = Math.sin(angle) * d;
        return d;
    }

    /**
     * 
     * @param {{x:number,y:number}} a 
     * @param {{x:number,y:number}} b 
     * @param {{x:number,y:number}} c 
     * @returns {{x:number,y:number,r:number,r2:number}} {x:number,y:number,r:number,r2:number}
     */
    static circumcircle(a, b, c) {
        var A = b.x - a.x;
        var B = b.y - a.y;
        var C = c.x - a.x;
        var D = c.y - a.y;
        var E = A * (a.x + b.x) + B * (a.y + b.y);
        var F = C * (a.x + c.x) + D * (a.y + c.y);
        var G = 2 * (A * (c.y - b.y) - B * (c.x - b.x));
        var dx;
        var dy;
        // If the points of the triangle are collinear, then just find the
        // extremes and use the midpoint as the center of the circumcircle.

        var x = (D * E - B * F) / G;
        var y = (A * F - C * E) / G;
        dx = x - a.x;
        dy = y - a.y;
        var r2 = dx * dx + dy * dy;
        var r = Math.sqrt(r2)
        return {
            x, y, r, r2
        }
    }

    /**
     * qp1,p2qp1p2q1
     * @param {{x:number,y:number}} q 
     * @param {{x:number,y:number}} p1 
     * @param {{x:number,y:number}} p2 
     * @param {number}[epsilon=1e-6]
     * @param {number}[epsilonAngle=1] in deg
     * @returns {boolean}
     */
    static pointOnSegment(q, p1, p2, epsilon, epsilonAngle) {

        const { min, max, abs } = Math;
        epsilon = (epsilon == undefined || epsilon == null) ? 1e-8 : epsilon;
        epsilonAngle = (epsilonAngle == undefined || epsilonAngle == null) ? 1 : epsilonAngle;

        var minX = min(p1.x, p2.x), maxX = max(p1.x, p2.x);
        var minY = min(p1.y, p2.y), maxY = max(p1.y, p2.y);

        //QP1P2
        if (minX - epsilon <= q.x && q.x <= maxX + epsilon &&
            minY - epsilon <= q.y && q.y <= maxY + epsilon
        ) {
            //
            const Cartesian2 = Cesium.Cartesian2, _Math = Cesium.Math;
            let v1 = Cartesian2.subtract(q, p1, new Cartesian2())
            let v2 = Cartesian2.subtract(p2, p1, new Cartesian2())
            const a = v1.x * v2.y, b = v2.x * v1.y, delt = abs(a - b);
            if (delt < 1e-16) return true;
            let angle = Cartesian2.angleBetween(v1, v2);
            angle = _Math.toDegrees(angle);
            return angle <= epsilonAngle;
        }
        return false
    }

    /**
     * 
     * @param {{x:number,y:number}} point 
     * @param {{x:number,y:number}[]} vs 
     * @returns {boolean}
     */
    static pointInPolygon(point, vs) {
        const l = vs.length;
        const x = point.x, y = point.y;
        let inside = false;
        for (let i = 0, j = l - 1; i < l; j = i++) {
            const xi = vs[i].x, yi = vs[i].y;
            const xj = vs[j].x, yj = vs[j].y;

            const intersect = ((yi > y) != (yj > y))
                && (x < (xj - xi) * (y - yi) / (yj - yi) + xi);
            if (intersect) inside = !inside;
        }

        return inside;
    }

    static pointOnTriangle(p, p1, p2, p3) {
        var onTriangle = MathUtils.pointOnSegment(p, p1, p2)
        if (!onTriangle) return false;
        onTriangle = MathUtils.pointOnSegment(p, p1, p3)
        if (!onTriangle) return false;
        onTriangle = MathUtils.pointOnSegment(p, p2, p3)
        return onTriangle;
    }

}