
/**
 * 
 * @param {Cesium.Rectangle} rectangle 
 */
export default function getRactangleCornersWC(rectangle) {
    var points = [
        Cesium.Rectangle.northwest(rectangle),
        Cesium.Rectangle.southwest(rectangle),
        Cesium.Rectangle.southeast(rectangle),
        Cesium.Rectangle.northeast(rectangle)
    ].map(p => {
        return Cesium.Cartesian3.fromRadians(p.longitude, p.latitude, p.height)
    });

    return points
}