import { BmpDecoder } from  "../bmp-js/lib/decoder.js";


import esExtends from  "../../../../utils/Source/esExtends.js";



var _BMPLoader;
function BMPLoader(manager) {
	if (!_BMPLoader) {
		const { FileLoader,
			Loader,
			Texture } = THREE;

		var _BMPLoaderPrototype = {

			// constructor: BMPLoader,

			load: function (url, onLoad, onProgress, onError) {

				var scope = this;

				var texture = new Texture();

				if (typeof url == 'string') {
					var loader = new THREE.FileLoader(this.manager);
					loader.setResponseType('arraybuffer');
					loader.setPath(this.path);

					loader.load(url, function (buffer) {

						texture.image = scope.parse(buffer);
						texture.needsUpdate = true;

						if (onLoad !== undefined) {

							onLoad(texture);

						}

					}, onProgress, onError);

				} else {

					texture.image = scope.parse(url);
					texture.needsUpdate = true;

					if (onLoad !== undefined) {

						onLoad(texture);

					}
				}
				return texture;

			},

			parse: function (buffer) {

				var decoder = new BmpDecoder(buffer);
				var cv = document.createElement('canvas');
				cv.width = decoder.width;
				cv.height = decoder.height;
				var ctx = cv.getContext('2d');
				var imgData = ctx.getImageData(0, 0, cv.width, cv.height);
				imgData.data.set(decoder.data);
				return cv;
			}

		}
		_BMPLoader = esExtends(Loader, {
			prototype: _BMPLoaderPrototype,
			esConstructor() {
				class _esCtor extends Loader {
					constructor(manager) {
						super(manager)
						constructor.call(this, manager);
					}
				}
				return _esCtor;
			},
			constructor(manager) { }
		})
		_BMPLoader.prototype.constructor.name = 'BMPLoader'
	}
	return new _BMPLoader(manager);
}
export { BMPLoader };
