
import ImageTileLayer from  "../imagery/ImageTileLayer.js";


class Vector2dLayer extends ImageTileLayer {
    constructor(params) {
        super(params);
        this.provider = new CesiumVectorTile.VectorTileImageryProvider(params);
        if (params.focusAdminNames) {
            this.provider.focusAdminNames = params.focusAdminNames;
            this.provider.focusPropertyName = params.focusPropertyName || 'NAME';
        }
    }
    get focusAdminNames() {
        return this.provider && this.provider.focusAdminNames
    }
    set focusAdminNames(val) {
        this.provider && (this.provider.focusAdminNames = val)
    }

    get focusPropertyName() {
        return this.provider && this.provider.focusPropertyName
    }
    set focusPropertyName(val) {
        this.provider && (this.provider.focusPropertyName = val)
    }
}

const DefaultParams = {
    Sealand: {
        zIndex: 0,
        removeDuplicate: false,
        minimumLevel: 0,
        defaultStyle: {
            fill: true,//
            outlineColor: 'rgba(138,138,138,1)',//
            fillColor: 'rgba(235,235,235,1)',//
            backgroundColor: 'rgba(89,129,188,1)'//
        }
    },
    SealandDark: {
        zIndex: 0,
        removeDuplicate: false,
        minimumLevel: 0,
        defaultStyle: {
            fill: true, //
            outlineColor: 'rgba(138,138,138,1)', //
            fillColor: 'rgb(3,3,30)', // 
            backgroundColor: 'rgb(10,13,44)'//
        }
    },
    ChinaBorder: {
        zIndex: 999,
        minimumLevel: 0,
        defaultStyle: {
            lineWidth: 1.25,
            outlineColor: 'rgba(88,88,88,1)'
        }
    },
    Province: {
        zIndex: 998,
        minimumLevel: 0,
        removeDuplicate: true,
        defaultStyle: {
            lineWidth: 1,
            //lineDash: [1, 4],
            fill: true,
            outline: true,
            shadowColor: 'white',
            outlineColor: 'rgba(118,118,118,1)',
            fillColor: 'rgba(225,225,225, .5)'
        },
        styleFilter: function (feature, style) {
            var highlight = true;
            if (this.focusAdminNames
                && this.focusAdminNames.length
            ) {
                for (var i = 0; i < this.focusAdminNames.length; i++) {
                    highlight = feature.properties[this.focusPropertyName]
                        && feature.properties[this.focusPropertyName].toString().indexOf(this.focusAdminNames[i]) >= 0;
                    if (highlight) break;
                }
            }
            if (highlight) {
                style.fill = false;
                style.outlineColor = Cesium.Color.BLACK;
                style.fillColor = Cesium.Color.WHITE;
                style.lineWidth = 1.25;
                style.lineDash = null;
            }
            return style;
        }
    },
    PlaceName: {
        minimumLevel: 3,
        zIndex: 999,
        defaultStyle: {
            showLabel: true,
            labelPropertyName: "name",
            showMaker: true,
            labelOffsetY: -12,
            fontFamily: 'heiti',
            pointColor: 'rgba(118,118,118,1)',
            labelStrokeWidth: 4,
            fontSize: 12,
            labelStroke: true,
            fontColor: 'white',
            labelStrokeColor: 'rgba(118,118,118,1)'//Cesium.Color.BLACK
        },
        styleFilter: function (fc, style, x, y, level) {
            var highlight = true;
            if (this.focusAdminNames
                && this.focusAdminNames.length
                && fc.properties.province) {

                for (var i = 0; i < this.focusAdminNames.length; i++) {
                    highlight = fc.properties.province.indexOf(
                        this.focusAdminNames[i]
                    ) >= 0;
                    if (highlight) break;
                    if (fc.properties.city) {
                        highlight = fc.properties.city.indexOf(
                            this.focusAdminNames[i]) >= 0;
                        if (highlight) break;
                    }
                    if (style.labelPropertyName
                        && typeof fc.properties[style.labelPropertyName] == 'string') {

                        highlight = fc.properties[style.labelPropertyName].indexOf(
                            this.focusAdminNames[i]) >= 0;
                        if (highlight) break;
                    }
                }
            }

            if (highlight) {
                style.pointColor = Cesium.Color.BLACK;
            }
            if (fc.properties.adminType == 'province') {
                if (level > 14) {
                    style.show = false;
                }
                style.showMaker = false;
                style.labelOffsetY = 0;
                style.fontSize = 16;
                if (highlight) {
                    style.labelStrokeColor = Cesium.Color.fromBytes(160, 99, 57);
                } else {
                    style.labelStrokeColor = Cesium.Color.fromBytes(140, 89, 47);
                }
            } else {

                if (fc.properties.adminType == 'city') {

                    if (!fc.properties.cityCode.endsWith("0100") && level < 6) {
                        style.show = false;
                    } else if (level < 5) {
                        style.show = false;
                    }
                }
                if (fc.properties.adminType == 'county' && level < 9) {
                    style.show = false;
                }
                if (fc.properties.adminType == 'country') {
                    style.show = false;
                }

                if (level > 14) {
                    style.fontSize = 18;
                }

                if (highlight) {
                    style.labelStrokeColor = Cesium.Color.BLACK;
                }
            }
            if (level < 6) {
                style.fontSize = 9;
            }
            if (level > 4) {
                style.labelPropertyName = "name";
            } else {
                style.labelPropertyName = "nameabbrevation";
            }

            return style;
        }
    }
}
Vector2dLayer.DefaultParams = DefaultParams

export class SealandVec2dLayer extends Vector2dLayer {
    /**
     * 
     * @param {{
     * source:string
     * theme?:'dark'|'light'
     * }} options 
     */
    constructor(options) {
        var theme = options.theme || 'light';
        var styleOptions = theme == 'dark' ? DefaultParams.SealandDark : DefaultParams.Sealand
        super(Object.assign({
            source: options.source
        }, styleOptions))
    }
}

export class HighlightPolygonLayer extends Vector2dLayer {
    /**
     * 
     * @param {{
     * source:string
     * focusAdminNames?:string[]
     * focusPropertyName?:string
     * theme?:'dark'|'light'
     * minimumLevel?:number
     * }} options 
     */
    constructor(options) {
        super({
            source: options.source,
            focusAdminNames: options.focusAdminNames,
            focusPropertyName: options.focusPropertyName,
            minimumLevel: options.minimumLevel || 1,
            removeDuplicate: true,
            zIndex: typeof options.zIndex != 'undefined' ? options.zIndex : 998,
            defaultStyle: {
                lineWidth: 1,
                // lineDash: [1, 4],
                fill: options.theme !== 'dark',
                outline: true,
                shadowColor: 'white',
                outlineColor: 'rgba(118,118,118,1)',
                fillColor: 'rgba(225,225,225, .5)'
            },
            styleFilter: function (feature, style) {
                var highlight = true;
                if (this.focusAdminNames
                    && this.focusAdminNames.length
                ) {
                    for (var i = 0; i < this.focusAdminNames.length; i++) {
                        highlight = feature.properties[this.focusPropertyName]
                            && feature.properties[this.focusPropertyName].toString().indexOf(this.focusAdminNames[i]) >= 0;
                        if (highlight) break;
                    }
                }
                if (highlight) {
                    style.fill = false;
                    if (options.theme == 'dark') {
                        style.outlineColor = 'rgb(245,245,245)';
                    } else {
                        style.outlineColor = Cesium.Color.BLACK;
                    }
                    style.fillColor = Cesium.Color.WHITE;
                    style.lineWidth = 1.25;
                    style.lineDash = null;
                }
                return style;
            }
        })
    }
}

export class HighlightMarkerLayer extends Vector2dLayer {
    /**
     * 
     * @param {{
     * source:string
     * focusAdminNames?:string[]
     * focusPropertyName?:string
     * theme?:'dark'|'light'
     * }} options 
     */
    constructor(options) {
        super({
            source: options.source,
            focusAdminNames: options.focusAdminNames,
            focusPropertyName: options.focusPropertyName,
            minimumLevel: 3,
            zIndex: typeof options.zIndex != 'undefined' ? options.zIndex : 999,
            defaultStyle: {
                showLabel: true,
                labelPropertyName: "name",
                showMaker: true,
                labelOffsetY: -12,
                fontFamily: 'heiti',
                pointColor: 'rgba(118,118,118,1)',
                labelStrokeWidth: 4,
                fontSize: 12,
                labelStroke: true,
                fontColor: 'white',
                labelStrokeColor: 'rgba(118,118,118,1)'//Cesium.Color.BLACK
            },
            styleFilter: function (fc, style, x, y, level) {
                var highlight = true;
                if (this.focusAdminNames
                    && this.focusAdminNames.length
                    && fc.properties.province) {

                    for (var i = 0; i < this.focusAdminNames.length; i++) {
                        highlight = fc.properties.province.indexOf(
                            this.focusAdminNames[i]
                        ) >= 0;
                        if (highlight) break;
                        if (fc.properties.city) {
                            highlight = fc.properties.city.indexOf(
                                this.focusAdminNames[i]) >= 0;
                            if (highlight) break;
                        }
                        if (style.labelPropertyName
                            && typeof fc.properties[style.labelPropertyName] == 'string') {

                            highlight = fc.properties[style.labelPropertyName].indexOf(
                                this.focusAdminNames[i]) >= 0;
                            if (highlight) break;
                        }
                    }
                }

                if (highlight) {
                    if (options.theme == 'dark') {
                        style.pointColor = Cesium.Color.WHITE;
                    } else {
                        style.pointColor = Cesium.Color.BLACK;
                    }
                }

                if (fc.properties.adminType == 'province') {
                    if (level > 14) {
                        style.show = false;
                    }
                    style.showMaker = false;
                    style.labelOffsetY = 0;
                    style.fontSize = 16;
                    if (highlight) {
                        style.labelStrokeColor = Cesium.Color.fromBytes(160, 99, 57);
                    } else {
                        style.labelStrokeColor = Cesium.Color.fromBytes(140, 89, 47);
                    }
                } else {

                    if (fc.properties.adminType == 'city') {

                        if (!fc.properties.cityCode.endsWith("0100") && level < 6) {
                            style.show = false;
                        } else if (level < 5) {
                            style.show = false;
                        }
                    }
                    if (fc.properties.adminType == 'county' && level < 9) {
                        style.show = false;
                    }
                    if (fc.properties.adminType == 'country') {
                        style.show = false;
                    }

                    if (level > 14) {
                        style.fontSize = 18;
                    }

                    if (highlight) {
                        if (options.theme == 'dark') {
                            style.labelStrokeColor = 'rgb(10,13,44)';
                        } else {
                            style.labelStrokeColor = Cesium.Color.BLACK;
                        }
                    }
                }
                if (level < 6) {
                    style.fontSize = 9;
                }
                if (level > 4) {
                    style.labelPropertyName = "name";
                } else {
                    style.labelPropertyName = "nameabbrevation";
                }

                return style;
            }
        })
    }
}

export class LabelVec2dLayer extends Vector2dLayer {
    /**
     * 
     * @param {{
     * source:string
     * minimumLevel?:number
     * maximumLevel?: number
     * }} options 
     */
    constructor(options) {
        super({
            source: options.source,
            minimumLevel: options.minimumLevel || 12,
            maximumLevel: options.maximumLevel || 20,
            zIndex: typeof options.zIndex != 'undefined' ? options.zIndex : 9999,
            defaultStyle: {
                showLabel: true,
                labelPropertyName: "NAME",
                showMarker: false,
                labelOffsetY: -12,
                fontFamily: 'heiti',
                pointColor: 'rgba(118,118,118,1)',
                labelStrokeWidth: 4,
                fontSize: 12,
                labelStroke: true,
                fontColor: 'white',
                labelStrokeColor: 'rgba(118,118,118,1)'
            }
        })
    }
}

export class CountryVec2dLayer extends Vector2dLayer {
    /**
     * 
     * @param {{
     * source:string
     * theme?:'dark'|'light'
     * }} options 
     */
    constructor(options) {
        super({
            source: options.source,
            zIndex: typeof options.zIndex != 'undefined' ? options.zIndex : 999,
            minimumLevel: 0,
            defaultStyle: {
                lineWidth: 2,
                outlineColor: options.theme == 'dark' ? 'white' : 'rgba(88,88,88,1)'
            }
        })
    }
}
export default Vector2dLayer;