
import GeometryUtils from  "../../core/Source/GeometryUtils.js";

// import { geojsonHelper, GenericWorker, traverse } from "@mesh-3d/utils";
import geojsonHelper from  "../../utils/Source/geojsonHelper.js";

import GenericWorker from  "../../utils/Source/GenericWorker.js";

import traverse from  "../../utils/Source/traverse.js";

import QuadtreeLODUtil from  "./QuadtreeLODUtil.js";

import QuadtreeLOD from  "./QuadtreeLOD.js";


function _getLodContentProvider(root, styleFilter) {

    return {
        requestContent(contentSet, rtcCenter) {
            let groups = [], materialMap = new WeakMap();

            function createContent(content) {

                let group, style = {}, feature = content.uri;

                let featureId = feature.properties.featureId;
                styleFilter(feature, style);
                let geometry = BuildingVector3DLoader.parseFeature(feature, featureId, style);
                let material = style.material;

                if (rtcCenter) {
                    if (!(geometry instanceof Cesium.Geometry)) {
                        geometry = geometry.constructor.createGeometry(geometry);
                    }
                    let positions = geometry.attributes.position.values;
                    for (let i = 0; i < positions.length; i += 3) {
                        positions[i] -= rtcCenter.x;
                        positions[i + 1] -= rtcCenter.y;
                        positions[i + 2] -= rtcCenter.z;
                    }
                }

                if (materialMap.has(material)) {
                    group = materialMap.get(material);
                } else {
                    group = {
                        material: material,
                        geometries: []
                    };
                    groups.push(group)
                    materialMap.set(material, group);
                }
                group.geometries.push(geometry);
            }

            let batchList = [], batch = [], batchSize = 100;
            batchList.push(batch);
            contentSet.contain.forEach(content => {
                if (batch.length > batchSize) {
                    batch = [];
                    batchList.push(batch);
                }
                batch.push(content)
            });
            contentSet.intersect.forEach(content => {
                if (batch.length > batchSize) {
                    batch = [];
                    batchList.push(batch);
                }
                batch.push(content)
            });

            return new GenericWorker(batchList, batch => {
                batch.forEach(createContent);
            }).run().then(() => {

                let object3d = new THREE.Object3D();
                groups.forEach(group => {
                    let geometry = GeometryUtils.mergeGeometries(group.geometries);
                    geometry = GeometryUtils.toBufferGeometry3js(geometry)
                    let material = group.material;
                    let mesh = new THREE.Mesh(geometry, material);

                    mesh.pickObject = {
                        id: mesh,
                        mesh: mesh,
                        primitive: root
                    }
                    object3d.add(mesh);

                    mesh.matrixAutoUpdate = false;
                    mesh.modelMatrixNeedsUpdate = false;
                    mesh.frustumCulled = false;

                    materialMap.delete(material)
                });
                object3d.matrixAutoUpdate = false;
                object3d.modelMatrixNeedsUpdate = false;
                object3d.frustumCulled = false;
                groups.splice(0);
                contentSet.contain.splice(0)
                contentSet.intersect.splice(0)
                return object3d;
            });
        }
    };
}

export default class BuildingVector3DLoader {

    /**
     * 
     * @param {string} url 
     * @returns {Promise<geojson.PolygonFeatureCollection>}
     */
    static loadGeojson(url) {
        return Cesium.Resource.fetchJson(url)
    }

    /**
     * 
     * @param {string} url 
     * @param {(feature:geojson.Feature<geojson.Polygon|geojson.MultiPolygon>,style:{
            height?: number;
            extrudedHeight?: number;
            vertexFormat?: VertexFormat;
            stRotation?: number;
            ellipsoid?: Ellipsoid;
            granularity?: number;
            perPositionHeight?: boolean;
            closeTop?: boolean;
            closeBottom?: boolean;
            arcType?: ArcType;
            material?:MeshMaterial
     })=>void}styleFilter
     * @param {(progress:number)=>void}onProgress
     * @param {(geojson:geojson.FeatureCollection<geojson.Polygon|geojson.MultiPolygon>)=>void}[onDataLoad]
     * @param {object}[options]
     * @param {number}[options.minRange=2]
     * @param {number}[options.maxLevel=5]
     * @param {number}[options.zSliceLevel=6]
     * @returns {Promise<QuadtreeLOD>}
     */
    static load(url, styleFilter, onProgress, onDataLoad, options) {
        if (typeof onDataLoad != 'function') {
            options = onDataLoad
            onDataLoad = null;
        }
        return this.loadGeojson(url).then((geojson) => {
            console.time('calcBBox')
            geojsonHelper.calcBBox(geojson);
            console.timeEnd('calcBBox')
            // console.log(geojson);
            if (typeof onDataLoad == 'function') {
                onDataLoad(geojson)
            }

            return this.parse(geojson, styleFilter, onProgress, options);

        })
    }

    static parse(geojson, styleFilter, onProgress, options) {
        if (onProgress && typeof onProgress != 'function') {
            options = onProgress, onProgress = null
        }
        if (!onProgress) onProgress = function (progress) { };

        options = options || {};
        options.useRtcCenter = typeof options.useRtcCenter == 'boolean' ? options.useRtcCenter : true;
        console.time('process contentSet')
        const getLodContentProvider = options.getLodContentProvider || _getLodContentProvider;
        let contentSet = [], rootRectangle, rootMaximumHeight = 0.0, minimumHeight = 0.1;;

        let invalidCount = 0;
        let batchList = [], batch = [],
            batchSize = geojson.features && geojson.features.length > 100 ? geojson.features.length / 100 : 100;
        batchList.push(batch);

        geojsonHelper.featureEach(geojson, (feature, featureId) => {
            if (!feature.geometry || !feature.geometry.coordinates.length) {
                invalidCount++
                return;
            }
            if (batch.length > batchSize) {
                batch = [];
                batchList.push(batch)
            }

            batch.push({
                feature: feature,
                id: featureId
            })
        });

        const { Rectangle, TileBoundingRegion, Color } = Cesium;

        let processWorker = new GenericWorker(batchList, batch => {

            for (let i = 0; i < batch.length; i++) {
                const taskData = batch[i];

                let feature = taskData.feature,
                    featureId = taskData.id;

                try {
                    let bbox = feature.geometry.bbox;
                    let maximumHeight = feature.properties.Floor * 10;

                    let rectangle = Rectangle.fromDegrees(bbox[0], bbox[1], bbox[2], bbox[3])
                    let region = new TileBoundingRegion({
                        rectangle: rectangle,
                        minimumHeight: minimumHeight,
                        maximumHeight: maximumHeight
                    });

                    feature.properties = feature.properties || {};
                    feature.properties.featureId = featureId;
                    let content = {
                        uri: feature,
                        boundingVolume: {
                            region: region,
                            sphere: region.boundingSphere
                        },
                        showDebugVolume: false
                    };
                    contentSet.push(content);

                    rootMaximumHeight = Math.max(rootMaximumHeight, maximumHeight);
                    if (!rootRectangle) {
                        rootRectangle = rectangle.clone()
                    } else {
                        Rectangle.union(rootRectangle, rectangle, rootRectangle);
                    }

                } catch (err) {
                    console.error(err);
                    throw err
                }
            }

        })

        let totalProgress = 0;
        return processWorker.run((progress) => {
            totalProgress = progress * 0.4;
            onProgress(totalProgress);

        }).then(() => {
            totalProgress = 40;

            onProgress(totalProgress);
            // console.timeEnd('process contentSet');

            try {
                let rootRegion = new TileBoundingRegion({
                    rectangle: rootRectangle,
                    minimumHeight: minimumHeight,
                    maximumHeight: rootMaximumHeight
                });

                // console.time('QuadtreeLODUtil.create');

                let scale = rootRectangle.width / rootRectangle.height;
                let xSize = 4, ySize = 4, zSize = 2;
                if (scale > 1) {
                    xSize = parseInt(Math.ceil(ySize * scale));
                } else {
                    ySize = parseInt(Math.ceil(xSize / scale));
                }

                var root = QuadtreeLODUtil.create({
                    rootBoundingVolume: {
                        region: rootRegion
                    },
                    maxLevel: options.maxLevel || 5,
                    zSliceLevel: options.zSliceLevel || 6,
                    xSize: xSize,
                    ySize: ySize,
                    zSize: zSize,
                    minRange: options.minRange || 2,
                    debugVolume: options.debugVolume,
                    showDebugVolume: options.showDebugVolume,
                    debugVolumeColor: options.debugVolumeColor || Color.CYAN.clone(),
                    rtcCenter: options.useRtcCenter && rootRegion.boundingSphere.center,
                    recursive: false
                })

                let rootTiles = root.children.slice();
                let createWorker = new GenericWorker(rootTiles, rootTile => {
                    rootTile._createLODTreeDelay(true);
                });

                return createWorker.run((progress) => {
                    onProgress(progress * 0.2 + totalProgress);

                }).then(() => {

                    totalProgress = 60;
                    onProgress(totalProgress);

                    // console.timeEnd('QuadtreeLODUtil.create')

                    // console.time('QuadtreeLODUtil.filterContents');

                    //L1
                    rootTiles = root.children.slice();
                    let filterL1Worker = new GenericWorker(rootTiles, rootTile => {
                        QuadtreeLODUtil.filterContents(contentSet, rootTile, false);
                    })

                    return filterL1Worker.run((progress) => {
                        onProgress(progress * 0.1 + totalProgress);

                    }).then(() => {//L2~LN

                        totalProgress = 70;
                        onProgress(totalProgress);

                        let tileList = [];
                        root.children.forEach(rootTile => {
                            traverse(rootTile.children, tile => {
                                tile.refine = 'ADD';
                                if (tile.level > 0) tileList.push(tile);
                            })
                        })

                        tileList.sort((a, b) => {
                            return a.level - b.level;
                        })

                        batchList = [];
                        batch = [];
                        batchList.push(batch);

                        batchSize = 100;//tileList.length > 20 ? tileList.length / 100 : 20;

                        tileList.forEach(tile => {
                            if (batch.length > batchSize) {
                                batch = [];
                                batchList.push(batch);
                            }
                            batch.push(tile);
                        });

                        let filterWorker = new GenericWorker(batchList, batch => {

                            for (let i = 0; i < batch.length; i++) {
                                let tile = batch[i];
                                let currentContentSet = //tile.parent && tile.parent._getContentSetBeforeFilter ? 
                                    tile.parent._getContentSetBeforeFilter() //: contentSet
                                QuadtreeLODUtil.filterContents(currentContentSet, tile, false);
                            }
                        });

                        return filterWorker.run((progress) => {
                            onProgress(progress * 0.3 + totalProgress);

                        }).then(() => {
                            totalProgress = 100;

                            // console.timeEnd('QuadtreeLODUtil.filterContents');

                            let lodContentProvider = getLodContentProvider(root, styleFilter)

                            root.children.forEach(child => {
                                traverse(child, (node) => {
                                    node.contentProvider = lodContentProvider;
                                })
                            });
                            onProgress(totalProgress);
                            return root;
                        })
                    })

                })

            } catch (e) {
                console.error(e);
                throw e
            }
        })

    }

    /**
     * 
     * @param {geojson.Feature<geojson.Polygon|geojson.MultiPolygon>} feature
     * @param {number}featureId
     * @param {{
            height?: number;
            extrudedHeight?: number;
            vertexFormat?: VertexFormat;
            stRotation?: number;
            ellipsoid?: Ellipsoid;
            granularity?: number;
            perPositionHeight?: boolean;
            closeTop?: boolean;
            closeBottom?: boolean;
            arcType?: ArcType;
     }}style
     * @returns {Cesium.Geometry} result 
     */
    static parseFeature(feature, featureId, style) {
        if (!feature.geometry) return;

        let geometry = this.parseGeometry(feature.geometry, style);
        let ptCount = geometry.attributes.position.values.length;
        let batchIds = new Float32Array(ptCount);
        batchIds.fill(featureId);

        const _Cesium = Cesium;
        let batchId = new _Cesium.GeometryAttribute({
            componentDatatype: _Cesium.ComponentDatatype.FLOAT,
            componentsPerAttribute: 1,
            values: batchIds
        });
        geometry.attributes.batchId = batchId;
        return geometry;
    }

    /**
     * 
     * @param {geojson.Polygon|geojson.MultiPolygon|geojson.MultiGeometry<geojson.Polygon|geojson.MultiPolygon>} geometry 
     */
    static parseGeometry(geometry, options) {
        let type = geometry.type;

        if (type == 'Polygon') {
            geometry = this.parsePolygon(geometry.coordinates, options);

        } else if (type == 'MultiPolygon') {
            let geometries = geometry.coordinates.map(polygon => {
                return this.parsePolygon(polygon, options);
            });
            geometry = GeometryUtils.mergeGeometries(geometries);

        } else if (type == 'MultiGeometry') {
            let geometries = geometry.geometry.map(geometry => {
                return this.parseGeometry(geometry, options);
            })
            geometry = GeometryUtils.mergeGeometries(geometries);

        }

        return geometry;
    }

    /**
     * 
     * @param {geojson.Polygon} coordinates 
     * @param {{
        height?: number;
        extrudedHeight?: number;
        vertexFormat?: VertexFormat;
        stRotation?: number;
        ellipsoid?: Ellipsoid;
        granularity?: number;
        perPositionHeight?: boolean;
        closeTop?: boolean;
        closeBottom?: boolean;
        arcType?: ArcType;
     * }}options
     * @returns {Cesium.Geometry} result 
     */
    static parsePolygon(coordinates, options) {
        const { PolygonHierarchy, PolygonGeometry } = Cesium;
        let positions = this.transformCoordinates(coordinates[0]), holes = [];
        for (let i = 1; i < coordinates.length; i++) {
            var contour = coordinates[i];
            contour = new PolygonHierarchy(this.transformCoordinates(contour))
            holes.push(contour);
        }

        let geometryCls = PolygonGeometry
        //ExtrudedPolygonGeometry;

        let geometry = new geometryCls(
            Object.assign({
                polygonHierarchy: new PolygonHierarchy(
                    positions, holes
                ),
                generateExtrudedUvNormal: true
            }, options)
        );
        geometry = geometryCls.createGeometry(geometry);

        return geometry;
    }

    static transformCoordinates(contour) {

        var cw = geojsonHelper.isClockWise(contour)
        contour = contour.map(pt => {
            return Cesium.Cartesian3.fromDegrees(pt[0], pt[1], pt[2] || 0)
        })
        if (!cw) {
            contour.reverse();
        }
        return contour;
    }
}
