

/**
 * for
* @example
    let dataList=[];

    function workFunc(data,index){
        //do something here
    }

    //1.for
    for(let index=0;index<dataList.length;index++){
        let data=dataList[index];
        workFunc(data,index);
    }

    //2.Use GenericWorker instead

    let worker=new GenericWorker(dataList,workFunc);
    worker.run((progress,currentData)=>{
        //update progress ui here
    }).then(()=>{
        //done
    }).catch(err=>{
        //error
    });
 */
export default class GenericWorker {
    /**
     *  for
     * @param {Array} dataList 
     * @param {(data:any,index:number)=>Promise<any>} workFunc 
     * @example
        let dataList=[];
        
        function workFunc(data,index){
            //do something here
        }

        //for
        for(let index=0;index<dataList.length;index++){
          let data=dataList[index];
          workFunc(data,index);
        }

        //GenericWorker

        let worker=new GenericWorker(dataList,workFunc);
        worker.run((progress,currentData)=>{
            //update progress ui here
        }).then(()=>{
            //done
        }).catch(err=>{
            //error
        });
     */
    constructor(dataList, workFunc) {
        if (!dataList || !dataList.length) {
            throw new Error('dataList is required')
        }
        if (typeof workFunc != 'function') {
            throw new Error('workFunc is required')
        }

        this.dataList = dataList;
        this.workFunc = workFunc;
    }

    run(onProgress) {

        return new Promise((resolve, reject) => {
            let taskCount = this.dataList.length,
                taskIndex = 0;
            let resultList = [];

            let next = () => {

                try {

                    if (this.dataList.length == 0) {
                        if (onProgress) onProgress(100, null);
                        resolve(resultList);

                    } else {

                        let loaded = (taskCount - this.dataList.length)
                        let progress = 100 * loaded / taskCount;
                        let currentData = this.dataList.shift();
                        if (onProgress) onProgress(progress, currentData, loaded, taskCount);

                        let promises = this.workFunc(currentData, taskIndex);
                        let isArray = Array.isArray(promises);
                        if (!isArray) promises = [promises];

                        Promise.all(promises).then((result) => {
                            if (isArray || !result) resultList.push(result);
                            else resultList.push(result[0]);

                            taskIndex++;

                            requestAnimationFrame(next);

                        }).catch((err) => {
                            resultList.splice(0);
                            reject(err);
                        })

                    }

                } catch (err) {
                    resultList.splice(0);
                    reject(err)
                }
            }
            next();
        })

    }
}