
import Path from  "./Path.js";

import GenericWorker from  "./GenericWorker.js";

// import { MaxRectsPacker, Rectangle, Bin } from "../../App/Mesh3DV/lib/maxrects-packer/dist/maxrects-packer.js";

export default class ImageUtils {

    /**
     * Based on MaxRectsPacker: https://www.npmjs.com/package/maxrects-packer
     * @param {{width:number,height:number}[]} maps 
     * @param {boolean} [forcePowerOfTwo=false] 
     * @returns {{rects:{id:number,x:number,y:number,rot:number,width:number,height:number,image:CanvasImageSource}[],width:number,height:number}[]}
     */
    static layoutMap(maps, forcePowerOfTwo) {
        if (typeof MaxRectsPacker == 'undefined') {
            throw new Error('MaxRectsPacker is required. https://www.npmjs.com/package/maxrects-packer');
        }
        var input = [];
        maps.forEach((map, id) => {
            input.push({
                width: map.width, height: map.height, image: map, id: id
            });
        });
        const options = {
            smart: true,
            pot: forcePowerOfTwo != undefined ? forcePowerOfTwo : true,
            square: false,
            allowRotation: false,
            tag: false,
            border: 0,
            // logic:MaxRectsPacker.PACKING_LOGIC.MAX_AREA
        }; // Set packing options
        let packer = new MaxRectsPacker.MaxRectsPacker(4096 * 2, 4096 * 2, 0, options); // width, height, padding, options
        packer.addArray(input); // Start packing with input array  
        return packer.bins;
    }

    /**
     * 
     * @param {{rects:{id:number,x:number,y:number,rot:number,width:number,height:number,image:CanvasImageSource}[],width:number,height:number}} packedBin 
     * @returns {HTMLCanvasElement}
     */
    static drawPackedMap(packedBin, bgColor) {
        const { rects, width, height } = packedBin;
        var cv = document.createElement('canvas')
        cv.width = width
        cv.height = height
        var ctx = cv.getContext('2d');
        if (bgColor) {
            ctx.fillStyle = bgColor;
            ctx.fillRect(0, 0, width, height);
        }
        for (let i = 0; i < rects.length; i++) {
            const rect = rects[i];
            ctx.drawImage(rect.image, rect.x, rect.y)
        }
        return cv;
    }

    /**
     * 
     * @param {CanvasImageSource} map 
     * @param {number[]} uvBox 
     * @param {boolean} [flipY=false] 
     * @param {boolean} [forceClip=false] 
     * @returns {HTMLCanvasElement}
     */
    static clipMap(map, uvBox, flipY, forceClip) {
        var sw = map.width, sh = map.height;
        var w = parseInt(sw * (uvBox[2] - uvBox[0]))
        var h = parseInt(sh * (uvBox[3] - uvBox[1]))
        if (w == map.width && h == map.height && !forceClip) return map;
        var cv = document.createElement('canvas')
        cv.width = w;
        cv.height = h;
        var ctx = cv.getContext('2d');
        var x = uvBox[0] * sw,
            y = uvBox[1] * sh;
        if (flipY) {
            y = map.height - y - h;
        }
        ctx.drawImage(map, -x, -y);
        return cv;
    }

    /**
     * 
     * @param {HTMLImageElement|HTMLCanvasElement} img 
     * @param {number} angle in degrees
     * @returns {HTMLCanvasElement}
     */
    static rotate(img, angle) {
        var c = document.createElement("canvas");
        c.width = img.width
        c.height = img.height;

        var ctx = c.getContext("2d");
        ctx.translate(c.width / 2, c.height / 2);
        ctx.rotate(angle * Math.PI / 180);
        ctx.translate(-c.width / 2, -c.height / 2);

        ctx.drawImage(img, 0, 0)
        return c;
    }

    /**
     * 
     * @param {HTMLImageElement|HTMLCanvasElement} img 
     * @param {string} text 
     * @param {object} [options] 
     * @param {string} [options.font] 
     * @param {string} [options.color]
     * @param {number} [options.x]
     * @param {number} [options.y]
     * @returns {HTMLCanvasElement}
     */
    static drawText(img, text, options) {
        options = options || {};

        let cv = document.createElement('canvas')
        cv.width = img.width;
        cv.height = img.height;

        let x = typeof options.x == 'number' ? options.x : cv.width / 2,
            y = typeof options.y == 'number' ? options.y : cv.height / 2;

        let ctx = cv.getContext('2d')
        ctx.drawImage(img, 0, 0);
        ctx.fillStyle = options.color;
        ctx.font = options.font
        ctx.fillText(text, x, y);
        return cv;
    }

    /**
     * 
     * @param {string} atlasImgUrl 
     * @param {(images:HTMLImageElement[])=>void} onLoad 
     */
    static getTexturesFromAtlasFile(atlasImgUrl, onLoad) {

        var textures = [];

        var imageObj = new Image();

        imageObj.onload = function () {

            var canvas, context;
            var tileWidth = imageObj.height, tilesNum = imageObj.width / tileWidth;

            for (var i = 0; i < tilesNum; i++) {

                canvas = document.createElement('canvas');
                context = canvas.getContext('2d');
                canvas.height = tileWidth;
                canvas.width = tileWidth;
                context.drawImage(imageObj, tileWidth * i, 0, tileWidth, tileWidth, 0, 0, tileWidth, tileWidth);
                // textures[i].image = canvas;
                // textures[i].needsUpdate = true;
                textures[i] = canvas
            }
            if (onLoad) onLoad(textures)
        };

        imageObj.src = atlasImgUrl;

        return textures
    }

    /**
     * 
     * @param {string[]} urls 
     * @param {string[]} names 
     * @param {({
     * positiveX:HTMLImageElement
     * negativeX:HTMLImageElement
     * positiveY:HTMLImageElement
     * negativeY:HTMLImageElement
     * positiveZ:HTMLImageElement
     * negativeZ:HTMLImageElement
     * })=>void} onLoad 
     * @param {(msg:string)=>void} onError 
     */
    static getCubemapSources(urls, names, onLoad, onError) {
        let images = urls;
        if (!images.length) {
            onError('')
        }
        else {

            let props = ['positiveX', 'negativeX', 'positiveY', 'negativeY', 'positiveZ', 'negativeZ']

            if (images.length == 1) {
                this.getTexturesFromAtlasFile(images[0], (images) => {
                    if (images.length == 1) {
                        let sources = {}
                        props.forEach((prop, i) => {
                            sources[prop] = images[0];
                        })
                        onLoad(sources);
                    }
                    else if (images.length < 6) {
                        onError('6')
                    } else {
                        let sources = {}
                        images.forEach((img, i) => {
                            sources[props[i]] = img;
                        })
                        onLoad(sources);
                    }
                })
            }
            else if (images.length < 6) {
                onError('6');
            } else {
                let sources = {};
                images.forEach((img, i) => {
                    let name = Path.ChangeExtension(names[i], '').toLowerCase()

                    if (/(nx|negx)/.test(name)) {
                        sources.negativeX = img;
                    }
                    else if (/(px|posx)/.test(name)) {
                        sources.positiveX = img

                    }
                    else if (/(ny|negy)/.test(name)) {
                        sources.negativeY = img
                    }
                    else if (/(py|posy)/.test(name)) {
                        sources.positiveY = img

                    }
                    else if (/(nz|negz)/.test(name)) {
                        sources.negativeZ = img
                    }
                    else if (/(pz|posz)/.test(name)) {
                        sources.positiveZ = img
                    }

                })
                if (!sources.positiveX) {
                    onError('px.<>')
                    return
                }
                if (!sources.negativeX) {
                    onError('nx.<>')
                    return
                }
                if (!sources.negativeZ) {
                    onError('nz.<>')
                    return
                }
                if (!sources.positiveZ) {
                    onError('pz.<>')
                    return
                }
                if (!sources.positiveY) {
                    onError('py.<>')
                    return
                }
                if (!sources.negativeY) {
                    onError('ny.<>')
                    return
                }

                new GenericWorker(props.slice(), prop => {
                    return new Promise((resolve, reject) => {
                        let img = new Image();
                        img.crossOrigin = '';
                        img.onload = function () {
                            resolve(img)
                        }
                        img.onerror = reject;
                        img.src = sources[prop]
                    })
                }).run().then(images => {

                    for (let i = 0; i < props.length; i++) {
                        const prop = props[i], img = images[i];
                        sources[prop] = img;
                    }
                    onLoad(sources);
                })

            }
        }
    }
}