
export default class StringUtil {

    /**
     * 
     * @param {string} input
     * @param {string|RegExp}[pattern]
     * @returns  {string} 
     */
    static trim(input, pattern) {

        var output = "";
        var start = 0, end = input.length;
        pattern = pattern || '(\\s|\\t)+';
        pattern = new RegExp(pattern);

        while (pattern.test(input[start]) && start < input.length) start++;

        var ch = input[end - 1];
        while (pattern.test(ch) && end > 0) {
            end--;
            ch = input[end - 1];
        }
        output = input.substring(start, end);
        return output
    }

    static paddingStart(num, length) {
        var sign = num >= 0 ? '+' : '-';
        num = Math.abs(num);
        num = num + "";
        var deltLen = length - num.length;
        var arr = new Array(deltLen).fill('0').join('')
        num = sign + arr + num;
        return num;
    }

}