            parseTexture(texture, path, texturesByUri) {
                return new Promise((resolve, reject) => {

                    var image = texture.Image
                    if (!image) return;

                    var fileName = image.Name;
                    if (texturesByUri[fileName]) {
                        resolve(texturesByUri[fileName]);
                        return;
                    }
                    const isJPEG = fileName.search(/\.jpe?g($|\?)/i) > 0 || fileName.search(/^data\:image\/jpeg/) === 0;

                    var ext = this.getExtension(fileName).toLowerCase();
                    if (ext[0] == '.') ext = ext.slice(1);

                    var imageUri, texture3js, isObjectURL;
                    function onLoad() {
                        texture3js.anisotropy = texture.MaxAnisotropy
                        texture3js.format = isJPEG ? RGBFormat : RGBAFormat;
                        texture3js.wrapS = wrappingMap[texture.WrapS.value];
                        texture3js.wrapT = wrappingMap[texture.WrapT.value];
                        texture3js.magFilter = filterMap[texture.MagFilter.value];
                        texture3js.minFilter = filterMap[texture.MinFilter.value];

                        if (isObjectURL) URL.revokeObjectURL(imageUri);

                        resolve(texture3js);
                    }

                    function onError(e) {
                        reject(e)
                    }

                    if (image.ImageType == Osg.ImageType.IMAGE_EXTERNAL) {

                        imageUri = this.resolveURL(fileName, path);

                        if (isNodeEnv) {//nodejs  env
                            imageUri = {
                                src: fileName,
                                fileName: imageUri
                            }
                        }
                    }
                    else if (image.ImageType == Osg.ImageType.IMAGE_INLINE_FILE) {

                        if (ext == 'bmp' || ext == 'tga') {

                            imageUri = texture.Image.Data;
                        }
                        else {
                            var mimeType = 'image/' + ext
                            if (isNodeEnv || !this.options.loadTexture) {//nodejs  env
                                imageUri = {
                                    src: fileName,//this.resolveURL(fileName, path),
                                    fileName: this.resolveURL(fileName, path),
                                    fileBuffer: image.Data
                                }
                            }
                            else {
                                var blob = new Blob([texture.Image.Data], { type: mimeType });
                                imageUri = blob;
                            }
                        }

                    } else {

                        texture3js = new DataTexture(image.Data, texture.TextureWidth, texture.TextureHeight)

                        console.warn('image file ' + image.Name + 'unsupport imageType ' + Osg.EnumToString(Osg.ImageType, image.ImageType));

                    }

                    if (isNodeEnv || !this.options.loadTexture) {//nodejs

                        if (!texture3js) texture3js = new Texture(imageUri);
                        texturesByUri[fileName] = texture3js;
                        onLoad()
                    }
                    else {//browser

                        if (ext == 'dds') {

                            texture3js = this.ddsLoader.load(imageUri, onLoad, null, onError);
                        }
                        else if (ext == 'rgb') {

                            texture3js = this.rgbLoader.load(imageUri, onLoad, null, onError);

                        }
                        else if (ext == 'tga') {

                            texture3js = this.tgaLoader.load(imageUri, onLoad, null, onError);

                        }
                        else if (ext == 'bmp') {

                            texture3js = this.bmpLoader.load(imageUri, onLoad, null, onError);

                        }

                        if (!texture3js) {

                            if (imageUri instanceof Blob) {

                                if (isCreateImageBitmapSupported) {

                                    createImageBitmap(imageUri, {
                                        imageOrientation: "flipY",
                                        premultiplyAlpha: "none",
                                    }).then(img => {
                                        texture3js.image = img;
                                        onLoad();
                                    }).catch(onError);

                                } else {

                                    imageUri = URL.createObjectURL(imageUri)
                                    let img = new Image();
                                    img.onload = function () {
                                        img.a_src = fileName;
                                        texture3js.image = img;
                                        try {
                                            image.Data = null;
                                            URL.revokeObjectURL(imageUri);
                                            onLoad();
                                        } catch (err) {
                                            onError(err);
                                        }
                                        image = null;
                                    }
                                    img.onerror = onError
                                    img.src = imageUri;

                                }

                                texture3js = new Texture();

                            }
                            else {
                                texture3js = new TextureLoader(this.manager).load(imageUri, onLoad, null, onError)

                            }
                        }

                        texturesByUri[fileName] = texture3js;
                    }

                })
            }

