import IRenderable from  "../../../../core/Source/IRenderable.js";

import geojsonHelper from  "../../../../utils/Source/geojsonHelper.js";



var scratchTranslationRtc;
var CESIUM_RTC_MODELVIEW = function (uniformState, model) {
    // CESIUM_RTC extension
    const { Matrix4, defined, Cartesian3 } = Cesium;

    var mvRtc = new Matrix4();

    if (!scratchTranslationRtc) scratchTranslationRtc = new Matrix4();
    return function () {
        if (defined(model._rtcCenter)) {
            Matrix4.getTranslation(uniformState.model, scratchTranslationRtc);
            Cartesian3.add(
                scratchTranslationRtc,
                model._rtcCenter,
                scratchTranslationRtc
            );
            Matrix4.multiplyByPoint(
                uniformState.view,
                scratchTranslationRtc,
                scratchTranslationRtc
            );
            return Matrix4.setTranslation(
                uniformState.modelView,
                scratchTranslationRtc,
                mvRtc
            );
        }
        return uniformState.modelView;
    };
}

export default class GeoMesh extends IRenderable {

    constructor(options) {
        const { when, Resource, Cartesian3, Rectangle, BoundingSphere, defaultValue } = Cesium

        if (!options.url) {
            throw new Error('GeoMesh#constructor:url is required')
        }

        super(options);

        this._rtcCenter = null;

        this.url = options.url;
        this.rectangle = new Rectangle();
        this.boundingSphere = new BoundingSphere()
        this.bbox = [-180, -90, 180, 90];
        this.source = null;
        this.ready = false;
        this.show = defaultValue(options.show, true);
        this._deferred = when.defer()
        this.readyPromise = this._deferred.promise;

        this._pickIdMap = new Map();
        this._pickIds = [];
        this._lazyLoad = defaultValue(options.lazyLoad, false);
        this._isLoading = false;

        if (!this._lazyLoad) {
            if (typeof this.url == 'string') {
                this._isLoading = true;
                Resource.fetchJson(this.url).then((json) => {
                    this.onLoad(json)
                })
            }
            else {
                this.onLoad(this.url);
            }
        }

    }

    get pickIds() {
        return this._pickIds
    }
    /**
    * 
    * @param {Earth|MeshVisualizer} earth 
    */
    load(earth) {
        if (this.visualizer) return;
        earth = earth.isMeshVisualizer ? earth : earth.globalScene;
        earth.add(this);
    }
    /**
    * 
    * @param {Earth|MeshVisualizer} earth 
    */
    unload(earth) {
        if (!this.visualizer) return;
        earth = earth.isMeshVisualizer ? earth : earth.globalScene;
        earth.remove(this);
    }
    onAdded() {
        if (this.lazyLoad && !this.ready && !this._isLoading) {
            if (typeof this.url == 'string') {
                this._isLoading = true;
                Resource.fetchJson(this.url).then((json) => {
                    this.onLoad(json)
                })
            }
            else {
                this.onLoad(this.url);
            }
        }
    }
    onRemoved() {
        const { mesh, visualizer } = this
        if (mesh && visualizer) {
            visualizer.remove(mesh, true);
            this._pickIds.length = 0;
            this._pickIdMap.clear()
        }
    }

    onLoad(json) {
        const { Rectangle } = Cesium
        const deferred = this._deferred
        var bbox = geojsonHelper.calcBBox(json);
        for (let i = 0; i < 4; i++) {
            this.bbox[i] = bbox[i];
        }
        Rectangle.fromDegrees(bbox[0], bbox[1], bbox[2], bbox[3], this.rectangle);

        try {

            this.source = json;
            this.initMesh();
            deferred.resolve(this)
            this.ready = true;
        } catch (err) {
            deferred.reject(err);
        }
    }

    setPickId(feature, geometry) {

        const { GeometryAttribute, ComponentDatatype } = Cesium
        const pickIdMap = this._pickIdMap;

        var pickId = pickIdMap.get(feature);
        if (!pickId) {
            var context = this.visualizer.frameState.context
            pickId = context.createPickId({
                feature: feature,
                primitive: this,
                boundingSphere: geometry.boundingSphere
            })
            pickIdMap.set(feature, pickId);
            this._pickIds.push(pickId);
        }
        var positions = geometry.attributes.position;
        positions = positions.values || positions.array;
        var ptCount = positions.length / 3;

        var pickColors = [], { red, green, blue, alpha } = pickId.color;
        for (let i = 0; i < ptCount; i++) {
            pickColors.push(red, green, blue, alpha)
        }
        pickColors = new Float32Array(pickColors);

        if (geometry.isBufferGeometry)
            geometry.setAttribute('pickColor', new THREE.BufferAttribute(pickColors, 4, false));
        else {
            geometry.attributes.pickColor = new GeometryAttribute({
                componentDatatype: ComponentDatatype.FLOAT,
                componentsPerAttribute: 4,
                normalize: false,
                values: pickColors
            })
        }
        return geometry;
    }

    initMesh() {
        var mesh = this.createMesh(this.source);

        if (this._rtcCenter) {
            mesh.uniformMapLoadedCallback = (uniformMap, frameState, mesh) => {
                var cesiumRtcModelViewUniform = CESIUM_RTC_MODELVIEW(frameState.context.uniformState, this);
                uniformMap.modelView = cesiumRtcModelViewUniform;
                uniformMap.u_modelView = cesiumRtcModelViewUniform;
                uniformMap.modelViewMatrix = cesiumRtcModelViewUniform;
                uniformMap.u_modelViewMatrix = cesiumRtcModelViewUniform;
            }
        }

        this.mesh = mesh;
    }

    rtcCenterTransform(geometry, rtcCenter) {
        var postionAtt = geometry.attributes.position
        var positions = postionAtt.array || postionAtt.values;
        for (let i = 0; i < positions.length; i += 3) {
            var x = positions[i], y = positions[i + 1], z = positions[i + 2];
            positions[i] = x - rtcCenter.x
            positions[i + 1] = y - rtcCenter.y
            positions[i + 2] = z - rtcCenter.z
        }
        return geometry;
    }
    transformGeometry(geometry, rtcCenter) {
        if (rtcCenter) {
            this.rtcCenterTransform(geometry, rtcCenter);
        } else {
            var localScene = this.visualizer;
            let positions = geometry.attributes.position.array;
            var cartesian3 = { x: 0, y: 0, z: 0 };
            for (let i = 0; i < positions.length; i += 3) {
                cartesian3.x = positions[i]
                cartesian3.y = positions[i + 1]
                cartesian3.z = positions[i + 2]
                localScene.worldCoordinatesToLocal(cartesian3, cartesian3, { y: 1 })
                positions[i] = cartesian3.x
                positions[i + 1] = cartesian3.y
                positions[i + 2] = cartesian3.z
            }

            geometry.computeVertexNormals()
            geometry.computeBoundingSphere();
        }
        return geometry;
    }
    createMesh(json) {
        throw new Error('GeoMesh#createMesh')
    }
    createHighlightGeometry(feature) {
        throw new Error('GeoMesh#createHighlightGeometry')
    }
    createHighlightMesh(feature, material) {

        let geometry = this.createHighlightGeometry(feature)
        if (!geometry.boundingSphere) geometry.computeBoundingSphere();

        var mesh = new THREE.Mesh(geometry, material);

        if (this._rtcCenter) {
            mesh.uniformMapLoadedCallback = (uniformMap, frameState, mesh) => {
                var cesiumRtcModelViewUniform = CESIUM_RTC_MODELVIEW(frameState.context.uniformState, this);
                uniformMap.modelView = cesiumRtcModelViewUniform;
                uniformMap.u_modelView = cesiumRtcModelViewUniform;
                uniformMap.modelViewMatrix = cesiumRtcModelViewUniform;
                uniformMap.u_modelViewMatrix = cesiumRtcModelViewUniform;
            }

            mesh.up.set(0, 0, 1);
            mesh.modelMatrixNeedsUpdate = false;
            mesh.matrixAutoUpdate = false
        }
        mesh.receiveShadow = false;
        mesh.castShadow = false;

        this.transformGeometry(geometry, this._rtcCenter);
        return mesh;
    }
    /**
     * 
     * @param {Cesium.FrameState} frameSate 
     * @param {(THREE.Object3D|Object3D)[]} renderList 
     * @override IRenderable#render
     * @returns 
     */
    render(frameSate, renderList) {
        if (!this.ready || !this.mesh || !this.show) return;
        this.pickId = this.mesh.pickId;
        renderList.push(this.mesh);
    }

}

GeoMesh.prototype.isGeoMesh = true;
