function checkShouldCancel(previousLod, lod) {
    var shouldCancel = previousLod.root && !previousLod.root._visible
    if (!shouldCancel) {
        shouldCancel = previousLod.parent && !previousLod.parent._visible
    }
    if (!shouldCancel) {
        shouldCancel = !previousLod._visible
    }
    if (!shouldCancel) {
        shouldCancel = pendingSort(previousLod, lod) > 0
    }
    return shouldCancel
}

/**
 * 
 * @param {PagedLOD} lod 
 * @param {string} rootPath
 * @private
 */
function toJSON(lod, rootPath) {
    var json = {
        uri: lod.uri.replace(rootPath, '.')
    }
    if (lod.localBoundingSphere) {
        json.sphere = packBoundingSphere(lod.localBoundingSphere)
    }
    if (lod.aabbLocal) {
        json.box = packBoundingBox(lod.aabbLocal)
    }
    return json;
}

var scratchMatrix3;

function updateContentModelMatrix(lod) {
    if (!lod.boundingSphere) return;

    const { Matrix3, Matrix4, BoundingSphere } = Cesium;

    const tileset = lod.tileset;
    var modelMatrix = tileset ? tileset.modelMatrix : lod.modelMatrix;

    //boundingSphere

    let uniformScale = Matrix4.getMaximumScale(modelMatrix)
    const boundingSphere = lod.boundingSphere, localBoundingSphere = lod.localBoundingSphere;
    boundingSphere.radius *= uniformScale;
    Matrix4.multiplyByPoint(modelMatrix, localBoundingSphere.center, boundingSphere.center);
    lod.boundingSphere2d = BoundingSphere.projectTo2D(boundingSphere, null, lod.boundingSphere2d);

    //boudingBox
    const obb = lod.obb, aabbLocal = lod.aabbLocal;
    if (obb) {
        Matrix4.multiplyByPoint(modelMatrix, obb.center, obb.center);
        if (!scratchMatrix3) scratchMatrix3 = new Matrix3();
        var rotationScale = Matrix4.getMatrix3(modelMatrix, scratchMatrix3);
        Matrix3.multiply(rotationScale, obb.halfAxes, obb.halfAxes);
    }
    if (lod.aabbLocal) {
        Matrix4.multiplyByPoint(modelMatrix, aabbLocal.center, lod.aabb.center);
        lod.region = boundingBoxToRegion(aabbLocal, modelMatrix);
    }

    //content

    if (!lod.content) return;

    lod.content.traverse((node) => {
        if (node.isMesh) {
            node.frustumCulled = false;
            Matrix4.multiply(modelMatrix, node.matrixWorld.elements, node.modelMatrix)
        }
        node.matrixAutoUpdate = false;
        node.modelMatrixNeedsUpdate = false;
    });

    if (lod.debugVolume && lod.debugVolume.isObject3D) {
        lod.debugVolume._modelMatrix = modelMatrix;
    }
}

//parse
//2
function parseOsgb(lod) {

    let osgRoot = lod.osgRoot, promise;
    if (lod._osgBuffer) {

        osgRoot = Osg.readBuffer(lod._osgBuffer);

        if (!osgRoot) {//osg
            let osg = new Uint8Array(lod._osgBuffer)
            osg = THREE.LoaderUtils.decodeText(osg);
            osgRoot = readOsg(osg);
            lod.content = parseOsg(osgRoot, false);
            traverse(lod.content, obj => {
                obj.castShadow = lod.castShadow
                obj.receiveShadow = lod.receiveShadow
            })
        }
        lod.osgRoot = osgRoot;
        lod._osgBuffer = null
    }

    if (osgRoot) {

        // lod.rangeMode = osgRoot.RangeMode;
        lod.rangeDataList = osgRoot.RangeDataList;
        lod.rangeList = osgRoot.RangeList;
        // lod.centerMode = osgRoot.CenterMode;

        var minHeight = lod.tileset.minHeight
        if (typeof lod.tileset.minHeight == 'number') {
            Osg.traverse(osgRoot, osgNode => {
                if (osgNode.Type !== 'Osg::Geometry') return
                var vertices = osgNode.VertexArray.Value
                for (let i = 0; i < vertices.length; i += 3) {
                    if (vertices[i + 2] < minHeight) {
                        vertices[i + 2] = minHeight;
                    }
                }
            })
        }

        if (!lod._isExpired) {
            computeBoundingVolume(lod);
        }

        //empty lod
        if (lod._isEmptyLOD) {
            promise = Cesium.when.defer()
            promise.resolve(lod)
        }
        else if (osgRoot.Type == 'Osg::PagedLOD' || osgRoot.Type == 'Osg::Geometry') {
            if (!lod.children[0]) {//first load
                createRangeDataChild(lod);
            }
            promise = parseContent(lod);
        } else {
            lod.hasEmbedLOD = true;
            lod._contentMemorySize = 0;
            promise = createOrUpdateEmbedLodChildren(lod)
        }

    } else {
        promise = Cesium.when.defer()
        promise.resolve(lod)
    }

    return promise;
}
/**
 * 
 * @param {PagedLOD} lod 
 * @returns 
 */
function parseContent(lod) {
    const tileset = lod.tileset;
    const df = Cesium.when.defer();
    if (lod.content) {

        debugger
        var mem = memeoryOf(lod.content)
        tileset._memorySize += mem;
        lod._contentMemorySize = mem;

        df.resolve(lod);

    }
    else {

        const loader = lod.loader, tileset = lod.tileset;
        loader.parse(lod.osgRoot, lod.path, (model) => {

            model.traverse((node) => {
                if (node.isMesh) {
                    node.frustumCulled = false;
                    node.pickObject = {
                        id: lod,
                        mesh: node,
                        primitive: tileset
                    }
                    node.material.autoRefresh = false;
                    node.modelMatrix = Cesium.Matrix4.multiply(tileset.modelMatrix, node.matrixWorld.elements, node.modelMatrix)
                }
                node.matrixAutoUpdate = false;
                node.modelMatrixNeedsUpdate = false;
            })

            var mem = memeoryOf(model)
            tileset._memorySize += mem;
            lod._contentMemorySize = mem;

            lod.content = model;
            lod.content.osgRoot = null;
            lod.osgRoot = null;

            updateContentModelMatrix(lod);

            df.resolve(lod);

        }, function (e) {
            console.error(e);
            df.reject(e);
        }, {
            loadTexture: tileset.loadTexture,
            materialType: tileset.materialType
        });

    }

    return df;
}


//2-1
/**
 * 
 * @param {PagedLOD} lod 
 */
function computeBoundingSphere(lod) {
    const { BoundingSphere, Cartesian3, Matrix4 } = Cesium;
    lod._isEmptyLOD = false;

    if (!lod.tileset) {
        let boundingSpheres = []
        lod.children.forEach(child => {
            if (child._isEmptyLOD) return
            boundingSpheres.push(child.localBoundingSphere)
        })
        lod.localBoundingSphere = BoundingSphere.fromBoundingSpheres(boundingSpheres)
        lod.boundingSphere = lod.localBoundingSphere.clone();

        return;
    }

    let osgRoot = lod.osgRoot;
    if (osgRoot) {
        let children = osgRoot.Children;
        var userCenter = osgRoot.UserCenter;

        if (osgRoot.Type == 'Osg::PagedLOD') {

            lod.localBoundingSphere = new BoundingSphere(
                Cartesian3.fromArray(userCenter.center),
                userCenter.radius
            );

        } else if (children && children[0].Type == 'Osg::PagedLOD') {

            let boundingSpheres = [];
            osgRoot.Children.forEach((osgChild) => {
                userCenter = osgChild.UserCenter;
                if (!userCenter) {
                    return
                }
                boundingSpheres.push(new BoundingSphere(
                    Cartesian3.fromArray(userCenter.center),
                    userCenter.radius
                ));

            });
            if (boundingSpheres.length) {
                lod.localBoundingSphere = BoundingSphere.fromBoundingSpheres(boundingSpheres)
            }
        } else {

            let boundingSpheres = [];

            Osg.traverse(osgRoot, (osgNode) => {
                if (osgNode.Type == 'Osg::Geometry') {
                    var vertices = osgNode.VertexArray.Value
                    boundingSpheres.push(BoundingSphere.fromVertices(vertices));
                }
            });

            if (boundingSpheres.length == 0) {
                lod._isEmptyLOD = true;
                // throw new Error('cannot compute boundingSphere for node ' + osgRoot);
            } else {
                lod.localBoundingSphere = BoundingSphere.fromBoundingSpheres(boundingSpheres)
            }
        }
    }

    if (!lod._isEmptyLOD) {
        lod.boundingSphere = lod.localBoundingSphere.clone();

        let uniformScale = Matrix4.getMaximumScale(lod.tileset.modelMatrix)
        lod.boundingSphere.radius *= uniformScale;
        Matrix4.multiplyByPoint(lod.tileset.modelMatrix, lod.localBoundingSphere.center, lod.boundingSphere.center);
        lod.boundingSphere2d = BoundingSphere.projectTo2D(lod.boundingSphere);
    }

}

function computeBoundingVolume(lod) {
    const { Color, AxisAlignedBoundingBox, OrientedBoundingBox, Matrix3, Cartesian3, BoxOutlineGeometry, Transforms, Matrix4 } = Cesium;

    try {

        computeBoundingSphere(lod);

        if (lod._isEmptyLOD) return;

        let osgRoot = lod.osgRoot, transform = lod.tileset.modelMatrix;
        if (osgRoot) {
            var vertices = [];
            Osg.traverse(osgRoot, (node) => {
                if (node.Type == 'Osg::Geometry') {
                    var pts = Cartesian3.unpackArray(node.VertexArray.Value);
                    vertices = vertices.concat(pts);
                }
            });
            if (!vertices.length) return;

            lod.obbLocal = OrientedBoundingBox.fromPoints(vertices);
            lod.aabbLocal = AxisAlignedBoundingBox.fromPoints(vertices)
        } else if (!lod.aabbLocal && !lod.obbLocal) {
            return
        }

        if (lod.aabbLocal) {
            lod.aabb = lod.aabbLocal.clone();
            Matrix4.multiplyByPoint(transform, lod.aabbLocal.center, lod.aabb.center);

            lod.region = boundingBoxToRegion(lod.aabbLocal, transform);

            if (lod.debugVolume) {
                var boxGeometry = BoxOutlineGeometry.fromAxisAlignedBoundingBox(lod.aabbLocal);
                boxGeometry = BoxOutlineGeometry.createGeometry(boxGeometry);
                boxGeometry = GeometryUtils.toBufferGeometry3js(boxGeometry)

                var aabBoxMaterial = new THREE.LineBasicMaterial({
                    color: 'cyan'
                });
                aabBoxMaterial.allowPick = false

                lod.debugVolume = new THREE.LineSegments(boxGeometry, aabBoxMaterial);
                lod.debugVolume.modelMatrixNeedsUpdate = false;
                lod.debugVolume._modelMatrix = lod.tileset.modelMatrix;
            }
        }

        if (lod.obbLocal) {
            lod.obb = lod.obbLocal.clone();
            Matrix4.multiplyByPoint(transform, lod.obb.center, lod.obb.center);
            if (!scratchMatrix3) scratchMatrix3 = new Matrix3();
            var rotationScale = Matrix4.getMatrix3(transform, scratchMatrix3);
            Matrix3.multiply(rotationScale, lod.obb.halfAxes, lod.obb.halfAxes);
        }

    } catch (err) {
        debugger
        throw err
    }
}

//2-2
/**
 * 
 * @param {PagedLOD} lod 
 */
function createRangeDataChild(lod) {

    var rangeDataList = lod.rangeDataList;

    if (rangeDataList && rangeDataList.length > 0) {//prepare next level 

        var childFileName = rangeDataList[1];

        const parent = lod;
        var childTile = new PagedLOD({
            tileset: parent.tileset,
            tileId: childFileName,
            manager: parent.manager,
            loader: parent.loader,
            parent: parent,
            root: parent.root || parent,
            uri: parent.path + '/' + childFileName,
            debugVolume: parent.debugVolume,
            showDebugVolume: parent.showDebugVolume,
            minRange: parent.minRange,
            receiveShadow: parent.receiveShadow,
            castShadow: parent.castShadow
        });

        lod.children.push(childTile);

    }
}

//2-3 
/**
 *
 * @param {PagedLOD}lod
 * @returns {boolean}
 * @private
 */
function createOrUpdateEmbedLodChildren(lod) {

    var childReadyPromises = [];
    var osgRoot = lod.osgRoot;
    osgRoot.Children.forEach((osgChild, index) => {
        var childTile = lod.children[index];
        var promise;
        if (childTile) {//reload 

            childTile.osgRoot = osgChild;
            childTile.state = PagedLODState.LOADING;
            promise = parseContent(childTile);

        } else {//first load

            var tileId = lod.tileId + "." + index;
            const parent = lod;
            childTile = new PagedLOD({
                uri: parent.uri + index,
                tileId: tileId,
                parent: parent,
                manager: parent.manager,
                loader: parent.loader,
                tileset: parent.tileset,
                root: parent.root || parent,
                debugVolume: parent.debugVolume,
                showDebugVolume: parent.showDebugVolume,
                minRange: parent.minRange,
                isEmbedLOD: true,
                receiveShadow: parent.receiveShadow,
                castShadow: parent.castShadow
            });
            childTile.state = PagedLODState.LOADING;
            childTile.osgRoot = osgChild;
            promise = parseOsgb(childTile)

            lod.children.push(childTile);
        }

        promise = promise.then(function () {
            childTile.state = PagedLODState.LOADED;
            childTile.osgRoot = null;
            childTile.ready = true;
            childTile._isExpired = false;
            childTile.readyPromise.resolve(childTile);
            return childTile;
        });
        childReadyPromises.push(promise);
    });

    return Cesium.when.all(childReadyPromises, function () {
        lod.osgRoot = null;
        return lod;
    });
}

function initializePagedLODSet(tileset, frameState, renderer, currentWebglRenderState, camera3jsLocal) {
    tileset.children.forEach(lod => {
        var content = lod.content;
        if (!content) {
            lod._isEmptyLOD = true;
            return
        }

        content.traverse(obj => {
            var material = obj.material;
            if (material) {
                var map = material.map;
                if (map) renderer._textures.get(map, frameState);
                renderer._webglRenderItems.getAndUpdate(obj, frameState, false, currentWebglRenderState, camera3jsLocal);
            }
        });
        lod.state = PagedLODState.READY;
    });
    tileset._initialized = true;
}

function processPagedLODState(lod, frameState, renderer, camera3jsLocal, currentWebglRenderState) {
    const state = lod.state, tileset = lod.tileset;
    const webglTextures = renderer._textures, webglRenderItems = renderer._webglRenderItems;

    if (tileset._lodInitializing) {
        return;
    }

    if (state == PagedLODState.UNLOADED || state == PagedLODState.EXPIRED) {

        var previousLod = tileset._lodLoading;
        if (previousLod) {
            if (previousLod == lod) return;

            if (tileset.optimizeType != 'SpeedFirst') {
                if (!checkShouldCancel(previousLod, lod)) return
                previousLod.cancelRequest()
            }
        }

        tileset._lodLoading = lod;
        lod.requestTile().then(() => {
            if (tileset.optimizeType == 'SpeedFirst') {
                lod.state = PagedLODState.READY;
                tileset._shouldRaiseTileLoaded = true;
            }
            tileset._lodLoading = null;
        }).otherwise(err => {
            console.warn('Failed to load tile:' + tileToLoad.uri);
            console.error(err);
            tileset._lodLoading = null;
        });

    } else {
        var previousLod = tileset._lodInitializing;
        if (previousLod) {
            return
        }

        const content = lod.content;
        if (!content) {
            return
        } else if (tileset.optimizeType == 'SpeedFirst') {
            lod.state = PagedLODState.READY;
            tileset._shouldRaiseTileLoaded = true;
            tileset._lodInitializing = null;
            return
        }
        tileset._lodInitializing = lod;

        if (state == PagedLODState.LOADED) {
            lod.state = PagedLODState.TEXTRURE_INITIALIZING;
            content.traverse(obj => {
                var material = obj.material;
                if (material && material.map) {
                    var map = material.map;
                    webglTextures.get(map, frameState);
                }
            })
            Cesium.requestAnimationFrame(function () {
                lod.state = PagedLODState.TEXTRURE_INITIALIZED
                tileset._lodInitializing = null;
            })

        } else if (state == PagedLODState.TEXTRURE_INITIALIZED) {
            lod.state = PagedLODState.RENDER_ITEM_INITIALIZING;
            content.traverse(obj => {
                const { geometry, material } = obj
                if (!geometry || !material) return
                webglRenderItems.getAndUpdate(obj, frameState, false, currentWebglRenderState, camera3jsLocal);

                //
                if (lod.tileset.releaseGeometry) {
                    var attrs = geometry.attributes
                    for (const name in attrs) {
                        if (Object.hasOwnProperty.call(attrs, name)) {
                            const attr = attrs[name];
                            attr.array = new attr.array.constructor(0)
                        }
                    }
                }
            })
            Cesium.requestAnimationFrame(function () {

                lod.state = PagedLODState.READY;
                tileset._shouldRaiseTileLoaded = true;
                tileset._lodInitializing = null;
            })
        }
    }

}


/**
 * 
 */
export default class PagedLOD extends IRenderable {
    constructor(options) {
        super(options);

        this.isPagedLOD = true;
        this.isEmbedLOD = !!options.isEmbedLOD;
        this.hasEmbedLOD = !!options.hasEmbedLOD;

        this.uri = options.uri;
        this.path = Path.GetDirectoryName(this.uri);

        this.debugVolume = options.debugVolume;
        this.showDebugVolume = options.showDebugVolume;

        this.rangeDataList = options.rangeDataList || [];
        this.rangeList = options.rangeList || [];
        this.minRange = options.minRange || 8;

        this.boundingSphere = null;
        this.localBoundingSphere = null;
        this.aabb = null;
        this.aabbLocal = null;
        this.obb = null;
        this.obbLocal = null;
        /**
         * @type {Cesium.TileBoundingRegion}
         */
        this.region = null;

        this.state = PagedLODState.UNLOADED;

        /**
         * @type  {THREE.LoadingManager}
         */
        this.manager = options.manager;

        const _Cesium = Cesium, { when, defaultValue, Matrix4, Event } = _Cesium;

        var uriLower = this.uri.toLowerCase()
        if (uriLower.endsWith('.xml') || uriLower.endsWith('.json')) {

            this.onInitProgress = options.onInitProgress;
            this.debugVolumeOnly = defaultValue(options.debugVolumeOnly, false)
            this.ext = options.ext || 'osgb';
            this.dataDir = options.dataDir || '/Data';
            this.tilePattern = options.tilePattern || 'Tile_{x}_{y}';

            this.metadata = {};

            this.minX = options.minX || 0;
            this.maxX = options.maxX || 8;
            this.minY = options.minY || 0;
            this.maxY = options.maxY || 8;

            this._transform = options.transform || Matrix4.IDENTITY.clone();
            this._srsTransform = Matrix4.IDENTITY.clone();
            this._srsMatrix = Matrix4.IDENTITY.clone();
            this._computedTransform = Matrix4.IDENTITY.clone();

            this.modelMatrix = Matrix4.IDENTITY.clone();

            this.modelMatrixNeedsUpdate = true;
            this._memorySize = 0;
            this.geometryErrorScale = options.geometryErrorScale || 0.75;
            this.maximumMemorySize = options.maximumMemorySize || 1024 * 1024 * 500;

            this.tileLoaded = new Event();
            this._shouldRaiseTileLoaded = false;
            this.queue = new PagedLODQueue(this);

            this.show = defaultValue(options.show, true)
            this.castShadow = defaultValue(options.castShadow, false)
            this.receiveShadow = defaultValue(options.receiveShadow, false)
            this.loadTexture = defaultValue(options.loadTexture, true)
            this.materialType = defaultValue(options.materialType, 'basic')
            this.minHeight = options.minHeight;
            this.releaseGeometry = defaultValue(options.releaseGeometry, true)
            this.sortType = defaultValue(options.sortType, 'RangeFirst')
            this.optimizeType = defaultValue(options.optimizeType, 'PerformanceFirst')

            this._level = -1;
            this.debugSuspendVisit = false

            this.loadMetaData();
        } else {
            var { parent, root, tileset, tileId } = options;
            this._level = (parent || root || tileset)._level + 1;
            this.parent = parent;
            this.root = root;
            this.tileId = tileId;
            this.tileset = tileset;
            this._timeStamp = Date.now();
            this._contentMemorySize = 0;
            this.castShadow = options.castShadow
            this.receiveShadow = options.receiveShadow
        }
        /**
         * @type {OSGLoader}
         */
        this.loader = options.loader || new OSGLoader(this.manager)

        // this.fileLoader = new THREE.FileLoader(this.manager);

        /**
         * @type {PagedLOD[]}
         */
        this.children = options.children || [];

        this.ready = false;
        this.readyPromise = when.defer();

    }

    get transform() {
        return this._transform;
    }
    set transform(transform) {
        if (this.center && !Cesium.Matrix4.equals(transform, this._transform)) {
            this._transform = transform;
            this.modelMatrixNeedsUpdate = true;
        }
    }

    //initialize

    loadMetaData() {

        let url = this.uri;
        if (this.manager) url = this.manager.resolveURL(this.uri);

        const { Resource, Cartesian3, Cartographic, Transforms, Matrix4 } = Cesium;

        let tileset = this;
        function onMetadataLoad() {
            var metadata = tileset.metadata;
            tileset.center = Cartesian3.fromDegrees(
                metadata.srs[0], metadata.srs[1], metadata.srs[2] || 0
            );
            tileset.centerCartographic = Cartographic.fromDegrees(
                metadata.srs[0], metadata.srs[1], metadata.srs[2] || 0
            );

            tileset._srsModelMatrix = Transforms.eastNorthUpToFixedFrame(tileset.center, Cesium.Ellipsoid.WGS84, tileset._srsMatrix);

            if (metadata.srsOrigin) {
                let translation = Cartesian3.fromArray(metadata.srsOrigin);
                tileset._srsTransform = Matrix4.fromTranslation(translation)
            }

            Matrix4.multiply(tileset._transform, tileset._srsTransform, tileset._computedTransform);
            Matrix4.multiply(tileset._srsMatrix, tileset._computedTransform, tileset.modelMatrix);

        }

        var promise, metaUri = this.uri.toLowerCase()
        if (metaUri.endsWith('.xml')) {
            promise = Resource.fetchXML(url).then((metaDoc) => {

                var modelMetadatas = metaDoc.getElementsByTagName('ModelMetadata');
                if (modelMetadatas && modelMetadatas.length > 0) {
                    var modelMetadata = modelMetadatas[0];

                    // 
                    var srsEls = modelMetadata.getElementsByTagName('SRS');
                    if (srsEls && srsEls.length) {

                        var srsEl = srsEls[0];
                        var srsText = srsEl.textContent;
                        var start = srsText.indexOf(':');

                        let parts = srsText.split(':'), name = parts[0];

                        if (name == 'ENU') {

                            var lonlat = srsText.substr(start + 1);
                            this.metadata.srs = JSON.parse('[' + lonlat + ']');
                            this.metadata.srs.reverse();

                        } else
                        // if (name == 'EPSG') {
                        // } else 
                        {
                            throw new Error('Unsupport SRS:' + srsText)
                        }
                    }

                    //

                    var srsOriginEls = modelMetadata.getElementsByTagName('SRSOrigin');
                    if (srsOriginEls && srsOriginEls.length) {

                        var srsOriginEl = srsOriginEls[0];
                        var srsOriginText = srsOriginEl.textContent;

                        if (srsOriginText) {
                            this.metadata.srsOrigin = JSON.parse('[' + srsOriginText + ']');
                        }
                    }
                } else {
                    console.warn('metadata  ' + this.url + ' is empty.')
                }

                onMetadataLoad()

                return this.loadRootTiles()
            }).otherwise((e) => {
                console.trace(e)
                console.warn('metadata ' + url + ' load failed.', e);

                return this.loadRootTiles()
            })
        } else {
            promise = Resource.fetchJson(url).then(tilesetJson => {
                this.metadata = tilesetJson.metadata
                this.localBoundingSphere = unpackBoundingSphere(tilesetJson.sphere)
                this.boundingSphere = this.localBoundingSphere.clone()
                if (tilesetJson.box) {
                    this.aabbLocal = unpackBoundingBox(tilesetJson.box)
                    this.aabb = this.aabbLocal.clone()
                }
                if (tilesetJson.transform) {
                    this.transform = Matrix4.fromArray(tilesetJson.transform, 0, this.transform)
                }
                onMetadataLoad()

                updateContentModelMatrix(this);

                this.minX = tilesetJson.minX
                this.minY = tilesetJson.minY
                this.maxX = tilesetJson.maxX
                this.maxY = tilesetJson.maxY

                return this.createRootTiles(tilesetJson);
            }).otherwise((e) => {
                console.warn('metadata ' + url + ' load failed.', e);
                console.error(e)
            })
        }

        return promise;
    }

    /**
     * 
     * @param {*} tilesetJson 
     * @private
     */
    createRootTiles(tilesetJson) {
        var tileset = this;
        var parent = tileset, batch = [], batches = [batch];
        tilesetJson.children.forEach(rootTileJson => {
            var tileId = rootTileJson.uri.split(/\//).pop()
            tileId = tileId.split('.')
            tileId.pop();
            tileId = tileId.join('.');

            var uri = rootTileJson.uri
            if (uri.startsWith('.')) {
                uri = uri.slice(1)
            };
            var rootTile = new PagedLOD({
                uri: this.path + uri,
                tileId: tileId,
                tileset: parent,
                loader: parent.loader,
                manager: parent.manager,
                debugVolume: parent.debugVolume,
                showDebugVolume: parent.showDebugVolume,
                minRange: parent.minRange,
                receiveShadow: parent.receiveShadow,
                castShadow: parent.castShadow
            })
            rootTile.state = PagedLODState.UNLOADED

            rootTile.localBoundingSphere = unpackBoundingSphere(rootTileJson.sphere)
            rootTile.aabbLocal = unpackBoundingBox(rootTileJson.box)

            if (batch.length > 10) {
                batch = []
                batches.push(batch)
            }
            batch.push(rootTile)
            parent.children.push(rootTile)
        });

        let worker = new GenericWorker(batches, batch => {
            for (let i = 0; i < batch.length; i++) {
                const rootTile = batch[i];
                computeBoundingVolume(rootTile)
                updateContentModelMatrix(rootTile)
            }
        });

        return worker.run(this.onInitProgress).then(() => {

            console.time('first rendering')

            this._initialized = true;
            this.state = PagedLODState.READY;
            this.ready = true;
            this.readyPromise.resolve(this);
            return this;
        })
    }
    /**
     * minX,minY,maxX,maxY
     * @private
     */
    loadRootTiles() {

        let children = this.children;
        let allTiles = []

        for (let x = this.minX; x <= this.maxX; x++) {
            var xNo = StringUtil.paddingStart(x, 3);

            for (let y = this.minY; y <= this.maxY; y++) {

                var yNo = StringUtil.paddingStart(y, 3);

                var tileId = this.tilePattern.replace('{x}', xNo).replace('{y}', yNo);
                var filePath = this.dataDir + '/' + tileId + '/' + tileId + '.' + this.ext;
                var url = this.path + filePath;
                const parent = this;
                var tile = new PagedLOD({
                    uri: url,
                    tileId: tileId,
                    tileset: parent,
                    loader: parent.loader,
                    manager: parent.manager,
                    debugVolume: parent.debugVolume,
                    showDebugVolume: parent.showDebugVolume,
                    minRange: parent.minRange,
                    receiveShadow: parent.receiveShadow,
                    castShadow: parent.castShadow
                });

                allTiles.push(tile);
            }
        }

        let worker = new GenericWorker(allTiles, tile => {
            return new Promise((resolve, reject) => {
                tile.requestTile().then(tile => {
                    if (tile && !tile._isEmptyLOD) {
                        children.push(tile)
                    }
                    resolve()
                }).otherwise((e) => {
                    console.warn('tile ' + tile.tileId + ' load failed.', e)
                    resolve()
                });
            })
        });

        return worker.run(this.onInitProgress).then(() => {
            computeBoundingSphere(this);
            updateContentModelMatrix(this);

            this.state = PagedLODState.READY;
            this.ready = true;
            this.readyPromise.resolve(this);
            return this;
        })
    }

    //1
    requestTile() {
        const { when, Resource } = Cesium;

        const lod = this;
        if (lod.osgRoot) {
            debugger
            var df = when.defer();
            df.resolve(lod);
            return df;
        }

        var request = new Cesium.Request()
        lod._request = request;
        lod.cancelRequest = function () {
            // if (request.cancelFunction) {
            //     request.cancelFunction()
            //     lod.state = PagedLODState.UNLOADED;
            // } else {
            lod.state = PagedLODState.CANCELLED;
            // }
        }
        let url = lod.uri;
        if (lod.manager) url = lod.manager.resolveURL(lod.uri);

        lod.state = PagedLODState.LOADING;

        function doRequest() {
            if (lod._osgBuffer) {
                return Cesium.when(lod._osgBuffer, () => {
                    return parseOsgb(lod).then(() => {
                        lod.state = PagedLODState.LOADED
                        return lod
                    })
                })
            }

            return Resource.fetchArrayBuffer({
                url: url,
                request: request
            }).then((buffer) => {
                lod._osgBuffer = buffer;
                lod._osgBufferSize = buffer.byteLength;
                if (lod.state == PagedLODState.CANCELLED) {
                    lod.state = PagedLODState.UNLOADED
                    return lod;
                }
                return parseOsgb(lod);
            })
        }

        return doRequest().then(() => {
            if (lod.state == PagedLODState.CANCELLED) {
                lod.unload()
                return lod;
            }
            if (lod.state == PagedLODState.UNLOADED) return lod;

            if (lod.hasEmbedLOD) {
                lod.state = PagedLODState.READY;
            }
            else {
                lod.state = PagedLODState.LOADED;
            }
            lod.ready = true;
            lod.readyPromise.resolve(lod);
            return lod;
        }).otherwise((e) => {
            if (lod.state == PagedLODState.UNLOADED) return lod;

            console.error(e);
            lod.state = PagedLODState.ERROR;
            lod.ready = false;
            lod.readyPromise.reject(e);
            // throw e;
        });

    }

    //render

    computeDistanceToCamera(frameState) {
        const { region, boundingSphere } = this;
        const camera = frameState.camera;
        var distanceToCamera;
        if (region) {
            distanceToCamera = region.distanceToCamera(frameState)
        } else if (boundingSphere) {
            distanceToCamera = camera.distanceToBoundingSphere(boundingSphere);
        } else {
            return
        }
        if (distanceToCamera == 0) {
            distanceToCamera = Cesium.Cartesian3.distance(boundingSphere.center, camera.positionWC)
        }
        this._distanceToCamera = distanceToCamera
    }

    computeOrderParams(frameState) {

        const { Cartesian3 } = Cesium;
        /**
         * @type {Cesium.Camera}
         */
        var camera = frameState.camera;
        var scene = camera._scene
        var { region, boundingSphere, tileset } = this;
        tileset = tileset || this;

        var centerWC
        if (region) {
            centerWC = Cartesian3.add(region.southwestCornerCartesian, region.northeastCornerCartesian, {})
            Cartesian3.divideByScalar(centerWC, 2, centerWC)
        } else if (boundingSphere) {
            centerWC = boundingSphere.center
        } else {
            return
        }

        if (tileset._cameraHeight < 50 || tileset._cameraPitch > -0.1) {
            this._radiusPixel = undefined;
            return
        }
        var centerPixel = Cesium.SceneTransforms.wgs84ToWindowCoordinates(scene, centerWC, {})
        if (centerPixel) {
            var { x, y } = centerPixel
            var w = scene.canvas.width / frameState.pixelRatio;
            var h = scene.canvas.height / frameState.pixelRatio;
            x = x - w / 2
            y = y - h / 2
            if (y < 0) y = - y;
            this._radiusPixel = (x * x + y * y)
        }
    }
    /**
     * 
     * @param {Cesium.FrameState} frameState  
     * @returns {boolean}
     * @protected
     */
    computeVisibility(frameState) {

        const { Intersect } = Cesium;
        const parent = this.parent, cullingVolume = frameState.cullingVolume;
        var visibility;
        if (parent) {
            if (parent._visibility == Intersect.INSIDE) {
                visibility = parent._visibility;
            } else {
                visibility = cullingVolume.computeVisibility(
                    this.region ? this.region : this.boundingSphere);
                // this.boundingSphere);
            }
        }
        else {
            visibility = cullingVolume.computeVisibility(this.boundingSphere);
        }

        this._visibility = visibility;
        this._visible = visibility > Intersect.OUTSIDE;

        if (this._visible) {

            var geometryErrorScale = (this.tileset || this).geometryErrorScale;
            this.range = RendererUtils.computeRange(this._distanceToCamera, this.boundingSphere.radius, frameState, geometryErrorScale)

            this._visible = this.range > this.minRange;
        }

        return this._visible;
    }

    /**
     * 
     * @param {Cesium.FrameState} frameState 
     * @param {PagedLODQueue}renderQueue
     * @private
     */
    visit(frameState, renderQueue) {

        if (this._isEmptyLOD) return;

        let tileset = this.tileset;
        if (tileset) {
            this.minRange = tileset.minRange;
            this.showDebugVolume = tileset.showDebugVolume;
        }

        if (this.frameNumber != frameState.frameNumber) {
            this.computeDistanceToCamera(frameState)
            this.computeVisibility(frameState);
        }

        if (this._visible) {

            this.computeOrderParams(frameState)

            const tileset = this.tileset;
            this._timeStamp = tileset._currentTimeStamp;

            let needLoadChild = this.rangeList && this.rangeList.length > 0;
            if (needLoadChild) {
                let nextRange = this.rangeList[1];
                needLoadChild = this.range > nextRange[0] && this.range <= nextRange[1]
                if (!needLoadChild) {
                    if (this.state == PagedLODState.READY) {
                        return renderQueue.pushRendering(this);
                    } else {
                        return renderQueue.pushPending(this);
                    }
                }
            }

            let childrenReady = this.children.length > 0;
            this.children.forEach(child => {
                child._timeStamp = tileset._currentTimeStamp;

                if (child.state !== PagedLODState.READY) {
                    childrenReady = false;
                    child.computeDistanceToCamera(frameState)
                    child.computeOrderParams(frameState)
                    return renderQueue.pushPending(child);
                }

                var contentReady = true;
                if (child.hasEmbedLOD) {
                    child.children.forEach(embedLOD => {
                        if (embedLOD.state !== PagedLODState.READY) {
                            contentReady = false;
                            embedLOD.computeDistanceToCamera(frameState)
                            embedLOD.computeOrderParams(frameState)
                            renderQueue.pushPending(embedLOD);
                        }
                    });
                }
                if (contentReady) {
                    child.visit(frameState, renderQueue);
                } else {
                    childrenReady = false;
                }
            });

            if (!childrenReady) {
                if (this.state == PagedLODState.READY) {
                    renderQueue.pushRendering(this);
                }
                else {
                    renderQueue.pushPending(this)
                }
            }
        }

        this.frameNumber = frameState.frameNumber;
    }

    /**
     * 
     * @param {Cesium.FrameState} frameState 
     * @param {THREE.Object3D[]} renderList 
     * @param {Mesh3D.WebGLRenderer}renderer
     * @param {THREE.Camera} camera3jsLocal 
     * @param {Mesh3D.WebGLRenderState} currentWebglRenderState
     */
    render(frameState, renderList, renderer, camera3jsLocal, currentWebglRenderState) {
        if (!this.ready || !this.show) return;

        var renderQueue = this.queue, debugSuspendVisit = this.debugSuspendVisit;

        if (!debugSuspendVisit) {

            this._currentTimeStamp = Date.now();
            if (frameState.frameNumber != this.frameNumber) {

                if (this.modelMatrixNeedsUpdate) {
                    Cesium.Matrix4.multiply(this._transform, this._srsTransform, this._computedTransform);
                    Cesium.Matrix4.multiply(this._srsMatrix, this._computedTransform, this.modelMatrix);

                    traverse(this, lod => {
                        updateContentModelMatrix(lod);
                    });
                    this.modelMatrixNeedsUpdate = false;
                }

                this.computeDistanceToCamera(frameState);
                this.computeVisibility(frameState);
                this.computeOrderParams(frameState)

                if (!this._visible) return;

                if (this._shouldRaiseTileLoaded) {
                    Cesium.requestAnimationFrame(() => {
                        this.tileLoaded.raiseEvent(this);
                        this._shouldRaiseTileLoaded = false;
                    })
                }

                if (!this._initialized) {
                    initializePagedLODSet(this, frameState, renderer, currentWebglRenderState, camera3jsLocal);
                }
                renderQueue.init();

                //visit

                this._cameraHeight = frameState.camera.positionCartographic.height
                this._cameraPitch = frameState.camera.pitch

                this.children.forEach(root => {
                    root.visit(frameState, renderQueue);
                });

                //
                renderQueue.sortPendings();
                var nextPending = renderQueue.shiftPending();
                if (nextPending) {
                    processPagedLODState(nextPending, frameState, renderer, camera3jsLocal, currentWebglRenderState);
                }
            }

            //unload
            this._memorySize0 = this._memorySize0 || 0;
            var maximumMemorySize = this.maximumMemorySize;
            var memIncreased = this._memorySize - this._memorySize0
            if (this._memorySize > maximumMemorySize && memIncreased > maximumMemorySize * 0.25) {
                this.unload();
                this._memorySize0 = this._memorySize
            }

        }

        renderQueue.getRenderList(renderList);

        if (renderList.length && !this._rendering) {
            this._rendering = true
            console.timeEnd('first rendering')
        }

        // console.log('renderlist', renderQueue.renerList.length);

        this.frameNumber = frameState.frameNumber;

        // if (!debugSuspendVisit) renderQueue.reset();
    }

    unload() {

        const tileset = this.tileset;
        if (!tileset) {
            let expiredTiles = this.queue.getExpiredList();
            let stack = expiredTiles.splice(0);
            while (stack.length > 0) {
                let expiredTile = stack.pop();
                expiredTile.unload();
            };
            return;
        }

        if (this._request) {
            this._request.cancelFunction && this._request.cancelFunction()
        }

        //
        if (this.hasEmbedLOD) {
            var needsUnloadChildren = true;
            this.children.forEach(child => {
                if (child._timeStamp == this.tileset._currentTimeStamp) {
                    needsUnloadChildren = false;
                }
            });
            if (needsUnloadChildren) {
                this.children.forEach(child => {
                    child.unload()
                });
                this._isExpired = true;
                this._loading = false;
                this.state = PagedLODState.EXPIRED;
            }
        }

        //
        var content = this.content;
        if (!content) return;
        tileset.visualizer.remove(content, true);
        releaseResources(content);
        if (content.osgRoot) {
            Osg.traverse(content.osgRoot, (node) => {
                if (node.Type == 'Osg::Geometry') {
                    destroyObject(node);
                }
            });
            delete content.osgRoot;
        }
        this.content = null;

        this._osgBuffer = null;
        this._isExpired = true;
        this._loading = false;
        this.state = PagedLODState.EXPIRED;

        //
        if (tileset._memorySize > 0) {
            tileset._memorySize -= this._contentMemorySize;
        }
        this._contentMemorySize = 0;
    }

    destroy() {

        let visualizer = this.visualizer;
        const destroyObject = Cesium.destroyObject;
        traverse(this.children, node => {
            node.unload()
            if (node.debugVolume) {
                node.debugVolume = visualizer.remove(node.debugVolume, true);
            }
            if (node.osgRoot) {
                Osg.traverse(node.osgRoot, (node) => {
                    if (node.Type == 'Osg::Geometry') {
                        destroyObject(node);
                    }
                });
            }
        });

        this._renderList && this._renderList.splice(0);

        this.boundingSphere = null;
        this.localBoundingSphere = null;
        this.aabb = null;
        this.aabbLocal = null;
        this.obb = null;
        this.obbLocal = null;
        this.region = null;

        visualizer.remove(this, true);
        this.visualizer = null;
        return destroyObject(this)
    }

    /**
     * @protected
     */
    isCommandsReady() {
        if (!this.tileset) return false;
        if (this.state !== PagedLODState.READY) return false;
        let content = this.content;
        let ready = undefined;
        traverse(content, obj => {
            if (ready == false) return;
            if (obj.isMesh && obj.visible) {
                if (ready == undefined) ready = true;
                let renderItems = obj.renderItems
                if (!renderItems) ready = false;
                else {
                    renderItems.forEach(renderItem => {
                        if (!renderItem.loaded) ready = false
                    })
                }
            }
        });
        return ready;
    }

    toIndexJSON() {
        if (this.root) {
            console.warn('Mesh3D.PagedLOD');
            return;
        }

        var jsonRoot = toJSON(this, this.path)
        jsonRoot.metadata = Object.assign({
            exporter: 'Mesh3D.PagedLOD',
            datetime: new Date().toLocaleDateString()
        }, this.metadata);
        jsonRoot.children = [];
        delete jsonRoot.uri

        jsonRoot.ext = this.ext
        jsonRoot.dataDir = this.dataDir
        jsonRoot.tilePattern = this.tilePattern;

        jsonRoot.minX = this.minX
        jsonRoot.minY = this.minY
        jsonRoot.maxX = this.maxX
        jsonRoot.maxY = this.maxY

        jsonRoot.transform = Cesium.Matrix4.toArray(this.transform)

        this.children.forEach(level0 => {
            if (level0._isEmptyLOD) return;
            level0 = toJSON(level0, this.path)
            jsonRoot.children.push(level0)
        })
        return jsonRoot;
    }

    selectTileLoaded(position) {
        return selectTileLoaded(this, position);
    }
    static updatePositionClamped(lodSet, entities, viewer, foreUpdate) {
        return updatePositionClamped(lodSet, entities, viewer, foreUpdate)
    }

}