export function traverse(osgNode, callback) {
    if (callback) callback(osgNode);

    let Children = osgNode.Children;
    if (Children && Children.length) {

        for (let i = 0; i < Children.length; i++) {
            const node = Children[i];
            traverse(node, callback);
        }

    }
}

