import Cesium3DTileExtension from  "../../../../renderable/Source/3dtiles/Cesium3DTileExtension.js";

// import MeshVisualizer from "@mesh-3d/core/Source/MeshVisualizer.js";
import Earth from  "../../Earth.js";

import ILayer from  "../ILayer.js";


export default class C3DTilesLayer extends ILayer {
    constructor(params) {
        super(params);
        this.renderer = params.renderer || 'Cesium';
        this.tileset = new Cesium.Cesium3DTileset(params)
    }
    /**
     * 
     * @param {Earth|MeshVisualizer} earth 
     */
    load(earth) {
        if (this.renderer == 'Three') {
            Cesium3DTileExtension.init();
            var localScene = earth.isMeshVisualizer ? earth : earth.globalScene;
            localScene.add(this.tileset);
        } else if (!earth.isMeshVisualizer) {
            earth.primitives.add(this.tileset);
        } else {
            throw new Error('3d tilesCesium')
        }
    }
    /**
     * 
     * @param {Earth|MeshVisualizer} earth 
     */
    unload(earth) {
        if (this.renderer == 'Three') {
            Cesium3DTileExtension.init();
            var localScene = earth.isMeshVisualizer ? earth : earth.globalScene;
            localScene.remove(this.tileset, true);
        } else if (!earth.isMeshVisualizer) {
            earth.primitives.remove(this.tileset, true);
        }
    }
}