
/**
 * 
 * @param {THREE.Object3D} scene 
 */
export default function releaseResources(scene) {
    if (!scene || !scene.traverse) return

    scene.traverse(node => {
        if (node.isDestroyed && node.isDestroyed()) return;

        //runtime's meshes

        if (node.destroy) {
            node.pickObject && (delete node.pickObject)
            node.pickId && (delete node.pickId)

            node.destroy();
            return;
        }

        //three.js`s objects

        if (!node.isScene && node.dispose) {
            node.dispose()
        }
        if (node.dispatchEvent) node.dispatchEvent({ type: 'dispose' }, node);

        node.pickObject && (delete node.pickObject)
        node.pickId && (delete node.pickId)
        if (node._actualMesh) {
            if (!node._actualMesh.isDestroyed) node._actualMesh.destroy()
            delete node._actualMesh.mesh3js
            delete node._actualMesh
        }

        if (node.geometry) {
            node.geometry.dispose();
            let attrs = node.geometry.attributes
            for (const key in attrs) {
                if (attrs.hasOwnProperty(key) && attrs[key]) {
                    const att = attrs[key];
                    delete att.array
                    attrs[key];
                }
            }
            delete node.geometry.attributes
            delete node.geometry;
        }

        if (node.material) {
            if (Array.isArray(node.material)) {
                node.material.forEach((mtl) => {
                    mtl.dispose();
                })
            } else {
                node.material.dispose();
            }
            delete node.material;
        }

    });
}