import { fromByteArray, toByteArray } from  "../../../base64-js/index.js";

import GenericWorker from  "./GenericWorker.js";

import isTypedArray from  "./isTypedArray.js";

import mergeBuffers from  "./mergeBuffers.js";


/* 
 jsonjson
*/

function storeString(str, strMap, strDic) {
    let code = strDic.length;
    if (strMap.has(str)) {
        code = strMap.get(str)
    } else {
        strMap.set(str, code);
        strDic.push(str)
    }
    return code;
}

export function encodeObjectValues(obj, strMap, strDic) {

    for (const code in obj) {
        if (obj.hasOwnProperty(code)) {
            if (!Object.isExtensible(obj)) {
                debugger
                continue;
            }
            var val = obj[code];
            let name = strDic[code];
            let newCode = strDic.length.toString();

            if (
                (typeof val == 'string' && val.length > newCode.length)
                || name == 'color' || name == 'matrix'
            ) {
                if (name == 'matrix') {
                    val = val.join(',')
                }
                if (strMap.has(val)) {
                    val = '$' + strMap.get(val);
                } else {
                    val = '$' + storeString(val, strMap, strDic);
                }
            }
            else if (Array.isArray(val)) {
                for (let i = 0; i < val.length; i++) {
                    let v = val[i];
                    if (typeof v == 'object') {
                        encodeObjectValues(v, strMap, strDic)
                    }
                }

            } else if (typeof val == 'object') {
                encodeObjectValues(val, strMap, strDic);

            }

            obj[code] = val;
        }
    }

}

function encodeArrayTypeValue(arr, strMap, strDic) {
    for (let i = 0; i < arr.length; i++) {
        let v = arr[i];
        if (Array.isArray(v)) {
            if (typeof v[0] == 'number') continue;
            encodeArrayTypeValue(v, strMap, strDic)
        }
        else if (typeof v == 'object') {
            encodeObject(v, strMap, strDic)
        }
    }
}

function decodeArrayTypeValue(arr, codeBook) {
    for (let i = 0; i < arr.length; i++) {
        let v = arr[i];
        if (Array.isArray(v)) {
            if (typeof v[0] == 'number') continue;
            decodeArrayTypeValue(v, codeBook)
        }
        else if (typeof v == 'object') {
            decodeObject(v, codeBook)
        }
    }
}

/**
 * 
 * @param {object} obj  
 * @returns {object}
 */
export function encodeObject(obj, strMap, strDic) {
    let release = false;
    if (!strMap) {
        strMap = new Map();
        strDic = [];
        release = true;
    }

    for (const name in obj) {
        if (obj.hasOwnProperty(name)) {
            var val = obj[name];
            if (isTypedArray(val)) {
                val = Array.from(val);
            }
            else if (Array.isArray(val)) {
                encodeArrayTypeValue(val, strMap, strDic);
            } else if (typeof val == 'object') {
                encodeObject(val, strMap, strDic)
            } else if (name == 'type' || name == 'uuid') {
                val = storeString(val, strMap, strDic)
            }

            let code = storeString(name, strMap, strDic)

            obj[code] = val;
            delete obj[name];
        }
    }

    if (release) {
        encodeObjectValues(obj, strMap, strDic);

        obj.codeBook = strDic;
        strMap.clear()
    }
    return obj;
}

export function decodeObject(obj, codeBook) {

    if (!codeBook) {
        codeBook = obj.codeBook;
        delete obj.codeBook;
    }

    for (const code in obj) {
        if (obj.hasOwnProperty(code)) {
            var val = obj[code];
            let name = codeBook[code];

            if (name == 'uuid' || name == 'type') {
                val = codeBook[val];
            }
            else if (typeof val == 'string' && val[0] == '$') {

                val = codeBook[val.slice(1)];
                if (name == 'matrix') {
                    val = JSON.parse('[' + val + ']');

                } else if (Array.isArray(val)) {
                    decodeArrayTypeValue(val, codeBook);

                } else if (typeof val == 'object') {
                    decodeObject(val, codeBook);

                }
            }
            else if (Array.isArray(val)) {
                decodeArrayTypeValue(val, codeBook);

            } else if (typeof val == 'object') {
                decodeObject(val, codeBook);

            }

            obj[name || code] = val;
            if (name) delete obj[code];
        }
    }

    return obj;
}


/**
 * 
 * @param {Array} coordinates 
 * @param {ArrayBuffer} bufferList 
 */
export function encodeGeojsonCoords(coordinates, bufferList) {

    if (typeof coordinates[0][0] == 'number') {
        if (bufferList.length == 0) {
            bufferList.byteLength = 0;
        }

        let count = coordinates.length, buffer = [];
        coordinates.forEach(coord => {
            buffer.push(coord[0], coord[1]);
        });

        buffer = new Float32Array(buffer).buffer;
        let byteLength = buffer.byteLength,
            byteOffset = bufferList.byteLength;

        bufferList.push(buffer);
        bufferList.byteLength += byteLength;

        return {
            byteOffset, count
        }
    } else {
        for (let i = 0; i < coordinates.length; i++) {
            coordinates[i] = encodeGeojsonCoords(coordinates[i], bufferList)
        }
        return coordinates;
    }
}

/**
 * 
 * @param {{byteOffset,count}[]} coordinates 
 * @param {Uint8Array|ArrayBuffer} buffer 
 */
export function decodeGeojsonCoords(coordinates, buffer) {
    if (!coordinates) {
        debugger
    }
    if (typeof coordinates.byteOffset == 'number') {
        let { byteOffset, count } = coordinates;
        let array = new Float32Array(buffer.buffer || buffer, byteOffset, count * 2);

        coordinates = [];
        for (let i = 0; i < array.length; i += 2) {
            coordinates.push([array[i], array[i + 1]]);
        }
    } else {
        for (let i = 0; i < coordinates.length; i++) {
            coordinates[i] = decodeGeojsonCoords(coordinates[i], buffer)
        }
    }
    return coordinates;
}

/**
 * 
 * @param {geojson.Feature|geojson.FeatureCollection} geojson  
 */
export function encodeGeojson(geojson) {

    console.time('encodeGeojsonCoords');

    let bufferList = [];
    if (geojson.type == 'FeatureCollection') {
        geojson.features.forEach(feature => {
            if (!feature.geometry) return;
            encodeGeojsonCoords(feature.geometry.coordinates, bufferList);
        })
    } else if (geojson.type == 'Feature') {
        if (geojson.geometry) {
            encodeGeojsonCoords(geojson.geometry.coordinates, bufferList);
        }
    }
    console.timeEnd('encodeGeojsonCoords');

    console.time('encodeObject');

    geojson = encodeObject(geojson);

    console.timeEnd('encodeObject');

    console.time('mergeBuffers');
    geojson.buffer = mergeBuffers(bufferList);
    console.timeEnd('mergeBuffers');

    console.time('fromByteArray');
    geojson.buffer = fromByteArray(new Uint8Array(geojson.buffer))
    console.timeEnd('fromByteArray');
    return geojson;
}


export function decodeGeojson(geojson) {

    console.time('decodeObject');
    if (geojson.codeBook) {
        let codeBook = geojson.codeBook
        for (const code in geojson) {
            if (geojson.hasOwnProperty(code)) {
                let name = codeBook[code];
                if (name) {
                    geojson[name] = geojson[code];
                    delete geojson[code];
                }
            }
        }

        decodeObject(geojson);
    }
    console.timeEnd('decodeObject');

    let buffer = geojson.buffer;
    delete geojson.buffer;
    if (buffer) {

        console.time('toByteArray');
        buffer = toByteArray(buffer);
        console.timeEnd('toByteArray');

        console.time('decodeGeojsonCoords');

        if (geojson.type == 'FeatureCollection') {
            let features = geojson.features;

            features.forEach((feature, id) => {
                let geometry = feature.geometry
                if (geometry) {
                    geometry.coordinates = decodeGeojsonCoords(geometry.coordinates, buffer);
                }
            })
        } else if (geojson.type == 'Feature') {
            let geometry = geojson.geometry
            if (geometry) {
                geometry.coordinates = decodeGeojsonCoords(geometry.coordinates, buffer);
            }
        }

        console.timeEnd('decodeGeojsonCoords');

        delete geojson.buffer
    }

    return geojson;
}

/**
 * 
 * @param {geojson.Feature|geojson.FeatureCollection} geojson 
 * @param {(progress)=>void} onProress  
 */
export function decodeGeojsonCoordsAsync(geojson, onProress, totalProgress) {
    totalProgress = totalProgress || 0;

    let buffer = geojson.buffer;
    delete geojson.buffer;

    console.time('toByteArray');
    buffer = toByteArray(buffer);
    console.timeEnd('toByteArray');

    console.time('decodeGeojsonCoords');

    if (geojson.type == 'FeatureCollection') {
        let features = geojson.features;
        let batchList = [], batch = [],
            batchSize = features.length > 10000 ? features.length / 80 : features.length / 40;

        batchList.push(batch);

        features.forEach((feature, id) => {
            let geometry = feature.geometry
            if (!geometry) return;
            if (batch.length > batchSize) {
                batch = [];
                batchList.push(batch);
            }
            batch.push(feature);
        });

        return new GenericWorker(batchList, batch => {
            batch.forEach(feature => {
                let geometry = feature.geometry
                geometry.coordinates = decodeGeojsonCoords(geometry.coordinates, buffer);
            });
        }).run(progress => {
            if (onProress) onProress(totalProgress + progress * 0.5);

        }).then(() => {
            console.timeEnd('decodeGeojsonCoords');

            totalProgress = 100;
            if (onProress) onProress(totalProgress);
            return geojson;
        });

    } else if (geojson.type == 'Feature') {
        let geometry = geojson.geometry
        if (geometry) {
            geometry.coordinates = decodeGeojsonCoords(geometry.coordinates, buffer);
        }
    }

    console.timeEnd('decodeGeojsonCoords');
    return Promise.resolve(geojson);
}

/**
 * 
 * @param {geojson.Feature|geojson.FeatureCollection} geojson 
 * @param {(progress)=>void} onProress  
 */
export function decodeGeojsonAsync(geojson, onProress) {

    if (geojson.codeBook) {
        console.time('decodeObject');
        let codeBook = geojson.codeBook;
        delete geojson.codeBook;

        for (const code in geojson) {
            if (geojson.hasOwnProperty(code)) {
                let name = codeBook[code],
                    vCode = geojson[code],
                    v = codeBook[vCode];

                if (typeof v != 'undefined') geojson[code] = v;

                if (name) {
                    geojson[name] = geojson[code];
                    delete geojson[code];
                }
            }
        }

        if (geojson.type == 'Feature') {
            decodeObject(geojson);
        } else {

            let features = geojson.features,
                batchList = [], batch = [], totalProgress = 0,
                batchSize = features.length > 10000 ? features.length / 80 : features.length / 40;

            batchList.push(batch);

            let geometryCode = codeBook.indexOf('geometry');
            features.forEach((feature, id) => {
                let geometry = feature[geometryCode]
                if (geometry) {
                    if (batch.length > batchSize) {
                        batch = [];
                        batchList.push(batch);
                    }
                    batch.push(feature);
                }
            });

            return new GenericWorker(batchList, batch => {
                batch.forEach(feature => {
                    decodeObject(feature, codeBook);
                });

            }).run(progress => {
                totalProgress = progress * 0.5;
                if (onProress) onProress(totalProgress);
            }).then(() => {
                console.timeEnd('decodeObject');

                totalProgress = 50;
                if (onProress) onProress(totalProgress);

                return decodeGeojsonCoordsAsync(geojson, onProress, totalProgress)
            });
        }
    }

    if (geojson.buffer) return decodeGeojsonCoordsAsync(geojson);

    return Promise.resolve(geojson);
}