
import GeometryUtils from  "../../../../core/Source/GeometryUtils.js";

import geojsonHelper from  "../../../../utils/Source/geojsonHelper.js";

import GeoPolylineGeometry from  "../../geometries/GeoPolylineGeometry.js";

import GeoMesh from  "./GeoMesh.js";


export default class GeoPolylineMesh extends GeoMesh {

    constructor(options) {
        const { MeshBasicMaterial } = THREE;
        const { defaultValue } = Cesium;
        super(options);


        this._lineWidth = options.lineWidth || 9;//
        this._material = options.material || new MeshBasicMaterial({ color: 'yellow', opacity: 0.5 })
        this._union = defaultValue(options.union, true);
        this._unionEpsilon = defaultValue(options.unionEpsilon, 0.01)//
        this._uvUnit = options.uvUnit;
        this._needsUpdate = false;
        this._clampToGround = defaultValue(options.clampToGround, false)
        this.useRtcCenter = defaultValue(options.useRtcCenter, true);
    }

    set clampToGround(val) {
        if (this._clampToGround != val) {
            this._clampToGround = val;
            this._needsUpdate = true;
        }
    }
    get clampToGround() {
        return this._clampToGround;
    }

    set material(val) {
        this._material = val;
        if (this.mesh) this.mesh.material = val;
    }
    get material() {
        return this._material;
    }

    set lineWidth(val) {
        if (this._lineWidth != val) {
            this._lineWidth = val;
            this._needsUpdate = true;
        }
    }
    get lineWidth() {
        return this._lineWidth
    }


    get uvUnit() {
        return this._uvUnit
    }

    set uvUnit(val) {
        if (this._uvUnit != val) {
            this._uvUnit = val;
            this._needsUpdate = true;
        }
    }

    get union() {
        return this._union
    }

    set union(val) {
        if (this._union != val) {
            this._union = val;
            this._needsUpdate = true;
        }
    }


    get unionEpsilon() {
        return this._unionEpsilon
    }

    set unionEpsilon(val) {
        if (this._unionEpsilon != val) {
            this._unionEpsilon = val;
            this._needsUpdate = true;
        }
    }

    createHighlightGeometry(feature) {
        const { uvUnit, clampToGround, lineWidth } = this;
        var geometry = GeoPolylineGeometry.fromFeature(feature, lineWidth, uvUnit, clampToGround);
        return geometry;
    }

    createMesh(json) {
        const { Cartesian3, BoundingSphere } = Cesium
        const { ShapeUtils, Mesh, BufferGeometry, BufferAttribute } = THREE;

        var features = [];
        geojsonHelper.featureEach(json, feature => {
            var geometry = feature.geometry
            var geomType = geometry.type
            if (geomType == 'MultiLineString') {
                geometry.coordinates.forEach(coordinates => {
                    var newFeature = {
                        type: "Feature",
                        geometry: {
                            type: 'LineString',
                            coordinates: coordinates
                        }
                    }
                    geojsonHelper.calcBBox(newFeature)
                    features.push(newFeature);
                })
                return
            }
            if (geomType != 'LineString') return;
            features.push(feature);
        })

        if (features.length == 0) return;
        const { visualizer, useRtcCenter, material, union, unionEpsilon, uvUnit, clampToGround, lineWidth } = this;

        if (union) {
            features = GeoPolylineGeometry.union(features, unionEpsilon);
        }

        const needsCreatePickId = !!visualizer._frameState && !!visualizer._frameState.context

        let geometries = [], geometry;
        for (let i = 0; i < features.length; i++) {
            const feature = features[i];
            if (!feature.geometry) {
                continue;
            }
            var tempGeometry = GeoPolylineGeometry.fromFeature(feature, lineWidth, uvUnit, clampToGround);
            tempGeometry.computeBoundingSphere()
            needsCreatePickId && this.setPickId(feature, tempGeometry);
            geometries.push(tempGeometry);
        }
        geometry = GeometryUtils.mergeGeometries3js(geometries);

        geometry.computeBoundingSphere();
        BoundingSphere.clone(geometry.boundingSphere, this.boundingSphere);

        material.pickColorQualifier = 'varying';
        material.pickColorName = 'pickColor';

        var mesh = new Mesh(geometry, material);

        if (useRtcCenter) {
            this._rtcCenter = this.boundingSphere.center;
            mesh.up.set(0, 0, 1);
            mesh.modelMatrixNeedsUpdate = false;
            mesh.matrixAutoUpdate = false
        } else {
            this._rtcCenter = null
        }
        mesh.receiveShadow = true;

        this.transformGeometry(geometry, this._rtcCenter);

        return mesh;
    }


    render(frameSate, renderList) {
        if (this._needsUpdate && this.mesh) {
            this.visualizer.remove(this.mesh, true);
            this.initMesh()
            this._needsUpdate = false
        }
        super.render(frameSate, renderList)
    }
}