import GeometryUtils from  "../../../core/Source/GeometryUtils.js";


export default class ExtrudedPolygonGeometry extends Cesium.PolygonGeometry {

    constructor(options) {
        super(options);
        this.options = options;
    }

    get generateExtrudedUvNormal() {
        return this.options.generateExtrudedUvNormal;
    }
    set generateExtrudedUvNormal(val) {
        this.options.generateExtrudedUvNormal = val;
    }

    /**
     * 
     * @param {ExtrudedPolygonGeometry} geometry 
     * @returns {Cesium.Geometry}
     */
    static createGeometry(geometry) {

        let PolygonGeometry = Cesium.PolygonGeometry;
        let options = geometry.options;
        let generateExtrudedUvNormal = options.generateExtrudedUvNormal;
        if (generateExtrudedUvNormal) {
            geometry = this.createGeometryWithExtrudedUvNormal(geometry);

        } else {
            geometry = PolygonGeometry.createGeometry(geometry);
        }
        return geometry;
    }

    static createGeometryWithExtrudedUvNormal(extrudedPolygonGeometry) {
        const _Cesium = Cesium;
        const {
            PolygonGeometry, GeometryAttribute, ComponentDatatype,
            Cartesian3, Cartesian2, Geometry,
            Cartographic, Ellipsoid, BoundingSphere, PrimitiveType
        } = _Cesium;
        const EPSILON5 = _Cesium.Math.EPSILON5;

        let minHeight = extrudedPolygonGeometry.options.height || 0;

        let geometry = PolygonGeometry.createGeometry(extrudedPolygonGeometry);
        geometry = this.createExtrudedUvNormal(geometry, minHeight)
        return geometry;
    }

    /**
     * 
     * @param {*} geometry 
     * @param {*} minHeight 
     * @param {THREE.Vector2|Cesium.Cartesian2} [uvUnit] 
     * @param {boolean}[roundUv]
     * @param {boolean}[createTopExtrudedUvNormal]
     * @returns 
     */
    static createExtrudedUvNormal(geometry, minHeight, uvUnit, roundUv, createTopExtrudedUvNormal, createBottomExtrudedUvNormal) {
        minHeight = minHeight || 0;
        const _Cesium = Cesium;
        const {
            GeometryAttribute, ComponentDatatype,
            Cartesian3, Cartesian2, Geometry,
            Cartographic, Ellipsoid, BoundingSphere, PrimitiveType, defaultValue
        } = _Cesium;
        const EPSILON5 = _Cesium.Math.EPSILON5;
        let newAttributes = GeometryUtils.cloneGeometryAttributes(geometry.attributes);
        let indices = geometry.indices;
        let attributes = geometry.attributes;
        let positions = Cartesian3.unpackArray(attributes.position.values);
        let normals = Cartesian3.unpackArray(attributes.normal.values);
        let sts = Cartesian2.unpackArray(attributes.st.values);
        createTopExtrudedUvNormal = defaultValue(createTopExtrudedUvNormal, true);
        createBottomExtrudedUvNormal = defaultValue(createBottomExtrudedUvNormal, true);

        let carg0 = new Cartographic(),
            carg1 = new Cartographic(),
            carg2 = new Cartographic();
        let ellipsoid = Ellipsoid.WGS84;

        function makeRoundUv(num) {
            if (Math.abs(num) < 1 || num % 1 == 0) {
                return num
            }
            if (Math.abs(num) % 1 < 0.5) {
                return Math.floor(num)
            } else {
                return Math.ceil(num)
            }
        }

        let newPositions = [], newNormals = [], newSts = [];
        let extrudedUvs = [], extrudedNormals = [];

        for (let i = 0; i < indices.length; i += 3) {
            let i0 = indices[i], i1 = indices[i + 1], i2 = indices[i + 2];
            let p0 = positions[i0], p1 = positions[i1], p2 = positions[i2];
            let n0 = normals[i0], n1 = normals[i1], n2 = normals[i2];
            let st0 = sts[i0], st1 = sts[i1], st2 = sts[i2];

            Cartographic.fromCartesian(p0, ellipsoid, carg0);
            Cartographic.fromCartesian(p1, ellipsoid, carg1);
            Cartographic.fromCartesian(p2, ellipsoid, carg2);

            newPositions.push(p0, p1, p2);
            newNormals.push(n0, n1, n2);
            newSts.push(st0, st1, st2);

            st0 = st0.clone(), st1 = st1.clone(), st2 = st2.clone();
            extrudedUvs.push(st0, st1, st2);

            let tempVertices = {
                cartographic: [carg0, carg1, carg2],
                position: [p0, p1, p2],
                st: [st0, st1, st2],
                normal: [0, 0, 0]
            }


            let tops = [], bottoms = [];
            if (carg0.height - minHeight >= EPSILON5) {
                tops.push(0);
            } else {
                bottoms.push(0);
            }

            if (carg1.height - minHeight >= EPSILON5) {
                tops.push(1);
            } else {
                bottoms.push(1);
            }

            if (carg2.height - minHeight >= EPSILON5) {
                tops.push(2);
            } else {
                bottoms.push(2);
            }


            if (tops.length == 2) {
                /**    
                 *   --
                 *   | /
                 *   |/ 
                 *   
                 */

                let t0 = tops[0], t1 = tops[1], b0 = bottoms[0];

                st0 = tempVertices.st[t0], st1 = tempVertices.st[t1],
                    st2 = tempVertices.st[b0];

                if (uvUnit) {
                    carg0 = tempVertices.cartographic[t0], carg1 = tempVertices.cartographic[t1],
                        carg2 = tempVertices.cartographic[b0];

                    p0 = tempVertices.position[t0], p1 = tempVertices.position[t1],
                        p2 = tempVertices.position[b0];

                    var h = carg0.height - minHeight, d = Cartesian3.distance(p0, p1);

                    st0.x = 0; st1.x = uvUnit.x ? 1 * d / uvUnit.x : 1; st2.x = 0;
                    st0.y = uvUnit.y ? 1 * h / uvUnit.y : 1; st1.y = uvUnit.y ? 1 * h / uvUnit.y : 1; st2.y = 0;
                } else {
                    st0.x = 0; st1.x = 1; st2.x = 0;
                    st0.y = 1; st1.y = 1; st2.y = 0;
                }
            } else if (bottoms.length == 2) {
                /**    
                 *      
                 *     /|
                 *    / |
                 *   --
                 */

                let b0 = bottoms[0], b1 = bottoms[1], t0 = tops[0];
                st0 = tempVertices.st[b0], st1 = tempVertices.st[b1],
                    st2 = tempVertices.st[t0];

                if (uvUnit) {
                    carg0 = tempVertices.cartographic[b0], carg1 = tempVertices.cartographic[b1],
                        carg2 = tempVertices.cartographic[t0];

                    p0 = tempVertices.position[b0], p1 = tempVertices.position[b1],
                        p2 = tempVertices.position[t0];

                    var h = carg2.height - minHeight, d = Cartesian3.distance(p0, p1);
                    st0.x = 0; st1.x = uvUnit.x ? 1 * d / uvUnit.x : 1; st2.x = uvUnit.x ? 1 * d / uvUnit.x : 1;
                    st0.y = 0; st1.y = 0; st2.y = uvUnit.y ? 1 * h / uvUnit.y : 1;
                } else {
                    st0.x = 0; st1.x = 1; st2.x = 1;
                    st0.y = 0; st1.y = 0; st2.y = 1;
                }

            } else if (createBottomExtrudedUvNormal && bottoms.length == 3) {//bottom

                tempVertices.normal.fill(-1);
                st0.x = 0; st1.x = 0; st2.x = 0;
                st0.y = 0; st1.y = 0; st2.y = 0;

            } else if (createTopExtrudedUvNormal) {//top 
                tempVertices.normal.fill(1);

                if (uvUnit) {
                    var topY = uvUnit.y ? (carg0.height - minHeight) / uvUnit.y : 1;
                    st0.x = 1; st1.x = 1; st2.x = 1;
                    st0.y = 1 * topY; st1.y = 1 * topY; st2.y = 1 * topY;
                } else {
                    st0.x = 1; st1.x = 1; st2.x = 1;
                    st0.y = 1; st1.y = 1; st2.y = 1;
                }
            }

            extrudedNormals.push(
                tempVertices.normal[0], tempVertices.normal[1],
                tempVertices.normal[2])

        }

        newPositions = Cartesian3.packArray(newPositions, new Float32Array(
            newPositions.length * 3
        ));
        newNormals = Cartesian3.packArray(newNormals, new Float32Array(
            newNormals.length * 3
        ));
        newSts = Cartesian2.packArray(newSts, new Float32Array(
            newSts.length * 2
        ));

        extrudedNormals = new Int8Array(extrudedNormals);
        extrudedUvs = Cartesian2.packArray(extrudedUvs, new Float32Array(
            extrudedUvs.length * 2
        ));
        if (uvUnit && roundUv) {
            for (let i = 0; i < extrudedUvs.length; i++) {
                extrudedUvs[i] = makeRoundUv(extrudedUvs[i]);
            }
        }

        newAttributes.position.values = newPositions
        newAttributes.normal.values = newNormals
        newAttributes.st.values = newSts

        newAttributes.extrudedNormal = new GeometryAttribute({
            componentDatatype: ComponentDatatype.BYTE,
            componentsPerAttribute: 1,
            values: extrudedNormals,
            normalize: false
        })
        newAttributes.extrudedUv = new GeometryAttribute({
            componentDatatype: ComponentDatatype.FLOAT,
            componentsPerAttribute: 2,
            values: extrudedUvs,
            normalize: false
        })

        geometry = new Geometry({
            attributes: newAttributes,
            primitiveType: PrimitiveType.TRIANGLES,
            boundingSphere: BoundingSphere.fromVertices(newPositions)
        })
        return geometry;
    }

}
