import pickPosition from  "./pickPosition.js";


/**
 * 
 * @param {Cesium.Viewer} viewer 
 * @param {(line:Cesium.Cartographic[],entities:Cesium.Entity[])=>void}callback
 * @param {object}[options]
 * @param {Cesium.Color}[options.lineColor=Color.DARKORANGE]
 * @param {Cesium.Color}[options.pointColor=Color.DARKORANGE]
 * @param {number}[options.pointSize=8]
 * @param {(line:Cesium.Cartographic[])=>void}[options.onMove]
 * @returns {()=>void} cancelCallback
 */
export default function getPolyline(viewer, callback, options) {
    if (!callback) throw new Error('callback')
    options = options || {};

    const {
        ScreenSpaceEventType, CallbackProperty,
        Color, Cartesian3, HeightReference, Cartographic
    } = Cesium;
    const { LEFT_CLICK, MOUSE_MOVE, LEFT_DOUBLE_CLICK } = ScreenSpaceEventType;
    const handler = viewer.screenSpaceEventHandler;
    var lineColor = options.lineColor || Color.DARKORANGE
    var pointColor = options.pointColor || Color.DARKORANGE
    var pointSize = options.pointSize || 8
    var onMove = options.onMove;

    //
    var state = {
        [LEFT_CLICK]: handler.getInputAction(LEFT_CLICK),
        [MOUSE_MOVE]: handler.getInputAction(MOUSE_MOVE),
        [LEFT_DOUBLE_CLICK]: handler.getInputAction(LEFT_DOUBLE_CLICK)
    }
    var points = [], polyline = [];

    //entity
    var lineEntity = viewer.entities.add({
        show: false,
        polyline: {
            positions: new CallbackProperty(() => {
                return points
            }, false),
            show: new CallbackProperty(() => {
                return lineEntity.show && points.length > 1
            }, false),
            clampToGround: true,
            width: 1.5,
            material: lineColor
        }
    })
    //
    var pointEntities = [];
    function addPoint(point, isEnding) {
        var pointEntity = viewer.entities.add({
            position: point,
            point: {
                pixelSize: pointSize,
                color: pointColor,
                heightReference: HeightReference.CLAMP_TO_GROUND
            }
        })
        pointEntities.push(pointEntity)
        points.push(point)
        if (isEnding) {
            polyline.push(point)
        }
    }

    handler.setInputAction(e => {
        var p = pickPosition(viewer.scene, e.position)
        if (!p
            || (points.length == 1 && Cartesian3.equals(p, points[points.length - 1]))
        ) {
            return
        }
        addPoint(p, true)
    }, LEFT_CLICK);

    handler.setInputAction(e => {
        if (points.length == 0) return;
        var p = pickPosition(viewer.scene, e.endPosition)
        if (!p || (points.length == 1 && Cartesian3.equals(p, points[points.length - 1]))) {
            return;
        }
        if (polyline.length == points.length) {
            addPoint(p, false)
        } else {
            points[points.length - 1] = p
        }
        if (onMove) {
            onMove(points.map(p => {
                return Cartographic.fromCartesian(p)
            }));
        }
        lineEntity.show = true
    }, MOUSE_MOVE);

    handler.setInputAction(() => end(), LEFT_DOUBLE_CLICK)

    //
    function end(cancel) {
        if (state[LEFT_CLICK]) {
            handler.setInputAction(state[LEFT_CLICK], LEFT_CLICK);
        }
        else {
            handler.removeInputAction(LEFT_CLICK);
        }
        if (state[MOUSE_MOVE]) {
            handler.setInputAction(state[MOUSE_MOVE], MOUSE_MOVE);
        }
        else {
            handler.removeInputAction(MOUSE_MOVE);
        }
        if (state[LEFT_DOUBLE_CLICK]) {
            handler.setInputAction(state[LEFT_DOUBLE_CLICK], LEFT_DOUBLE_CLICK);
        } else {
            handler.removeInputAction(LEFT_DOUBLE_CLICK);
        }

        viewer.entities.remove(lineEntity);
        for (let i = 0; i < pointEntities.length; i++) {
            viewer.entities.remove(pointEntities[i]);
        }
        if (!cancel) {
            //
            var entities = [{
                polyline: {
                    positions: points,
                    clampToGround: true,
                    width: 1.5,
                    material: lineColor
                }
            }];
            for (const point of points) {
                entities.push({
                    position: point,
                    point: {
                        pixelSize: pointSize,
                        color: pointColor,
                        heightReference: HeightReference.CLAMP_TO_GROUND
                    }
                })
            }
            //
            callback(polyline.map(p => {
                return Cartographic.fromCartesian(p)
            }), entities)
        }
        pointEntities = lineEntity = null;
        state = points = null;
    }
    function cancel() {
        end(true)
    }

    return cancel;
}
