import IRenderable from  "../../core/Source/IRenderable.js";

import QuadtreeLODUtil from  "./QuadtreeLODUtil.js";

import QuadtreeNode from  "./QuadtreeNode.js";

import QuadtreeLOD from  "./QuadtreeLOD.js";

import OSGLoader from  "./model/OSGLoader.js";

import VISMLoader from  "./model/VISMLoader.js";

import releaseResources from  "./releaseResources.js";

import { Path, traverse } from  "../../utils/index.js";



function processContentObject3d(contentNode, content, useLightMap, translucent) {
    delete content.osgRoot;
    contentNode.content = content;
    if (!content.userData) {
        content.userData = {
            uri: contentNode.uri
        }
    }
    content.traverse(obj => {
        obj.matrixAutoUpdate = false;
        obj.frustumCulled = false;
        obj.up.set(0, 0, 1);
        if (obj.isMesh) {
            obj.pickObject = {
                id: obj,
                mesh: obj,
                primitive: content
            }
            var objMaterial = obj.material
            /**
             * @type {THREE.Material[]}
             */
            var materials = Array.isArray(objMaterial) ? objMaterial : [objMaterial];
            materials.forEach(material => {
                material.autoRefresh = false;

                if (material.transparent) {
                    if (translucent) {
                        // material.depthWrite = false;
                        // material.opacity = 0.5;
                        // material.depthTest = false
                        // material.depthFunc = THREE.LessDepth
                        // material.premultipliedAlpha = true;
                        material.translucent = translucent;

                    }
                    //  material.transparent = transparent;
                }
                if (!useLightMap) material.lightMap = null;

            })

        }
    })
}

function reClassContentSet(contentSet, maxLevel) {
    maxLevel = maxLevel || 5;

    let newContentSet = [], maxPower = 0, minPower = Number.MAX_VALUE, powers = [];
    contentSet.forEach(contentNode => {
        if (!contentNode.content) return;
        let content = contentNode.content

        content.updateMatrixWorld();
        content.traverse(obj => {
            if (obj.isMesh) {
                let matrixWorld = obj.matrixWorld;
                let geometry = obj.geometry.clone();
                geometry.applyMatrix4(matrixWorld);
                geometry.computeBoundingBox();
                geometry.computeBoundingSphere();


                if (!obj._power) {
                    let power = QuadtreeLODUtil.computeLODPower(obj);
                    obj._power = power;
                    if (!isNaN(power) && power !== Infinity) {

                        maxPower = Math.max(maxPower, power);
                        minPower = Math.min(minPower, power);
                    }
                    powers.push(power);
                } else {
                    debugger
                }

                let box = geometry.boundingBox, sphere = geometry.boundingSphere
                let points = [box.min, box.max];
                geometry = null;

                const _Cesium = Cesium;
                newContentSet.push({
                    boundingVolume: {
                        box: new _Cesium.AxisAlignedBoundingBox.fromPoints(points),
                        sphere: _Cesium.BoundingSphere.clone(sphere)
                    },
                    content: {
                        uuid: obj.uuid,
                        mesh: obj,
                        root: content
                    },
                    power: obj._power
                });
            }
        })
    });

    newContentSet.sort((a, b) => {
        return b.power - a.power
    });
    powers.sort((a, b) => {
        return b - a
    });

    let newChildren = [], lastPower,
        levels = [], level = 1,
        factor = Math.sqrt(maxPower / 3);

    levels.push(newChildren);
    newContentSet.forEach(contentNode => {

        let a = maxPower / contentNode.power,
            b = lastPower / contentNode.power,
            c = factor / (level * 2),
            d = factor * Math.pow(2, level - 1);

        let b1 = a < d, b2 = b < c;

        if (!lastPower || (b1 && b2) || newChildren.length < 3
            || level >= maxLevel
        ) {
            contentNode.level = level;
            newChildren.push(contentNode);

        } else {
            level++;

            newChildren = [];
            levels.push(newChildren);
            contentNode.level = level;
            newChildren.push(contentNode);
        }

        lastPower = contentNode.power;
    })

    newChildren = [];

    newContentSet.maxPower = maxPower;
    newContentSet.minPower = minPower;
    newContentSet.levels = levels;

    return newContentSet;
}

export default class QuatreeContentSet extends IRenderable {

    /**
     * 
     * @param {object} options 
     * @param {Cesium.Matrix4} options.modelMatrix
     * @param {string} [options.url]
     * @param {QuadtreeNode[]|{uri:string,content:THREE.Group|THREE.Scene}[]} [options.contentSet]
     * @param {number} [options.minRange=32]
     * @param {number} [options.radiusPower=4] 
     * @param {{minRadius:number}[]} [options.lods] 
     * @param {boolean} [options.show=true]
     * @param {boolean} [options.useLightMap=true]
     * @param {boolean} [options.preloadTexture=false]
     * @param {boolean} [options.translucent=true]
     * @param {boolean} [options.contentCull=true]
     * @param {boolean} [options.debugVolume=true]
     * @param {boolean} [options.showDebugVolume=true]
     * @param {string} [options.dracoDecoderPath] 
     * @param {number} [options.maximumTextureSize] 
     */
    constructor(options) {
        super(options);

        const { defaultValue, when, Event } = Cesium;

        this.maximumTextureSize = options.maximumTextureSize;
        this.isQuatreeContentSet = true;
        this.url = options.url;
        this.contentSet = options.contentSet;
        this.show = defaultValue(options.show, true);
        this.modelMatrix = options.modelMatrix;
        this.debugVolume = options.debugVolume;
        this.showDebugVolume = options.showDebugVolume;
        this.minRange = options.minRange || 32;
        this.radiusPower = options.radiusPower || 4;
        this.lods = options.lods || [];
        this.maxLevel = options.maxLevel || 5;

        this.refine = options.refine || 'ADD';
        this.useLightMap = defaultValue(options.useLightMap, true);
        this.preloadTexture = defaultValue(options.preloadTexture, false);
        this.translucent = defaultValue(options.translucent, false);
        this.format = options.format || 'osg';
        this.dracoDecoderPath = options.dracoDecoderPath;

        this.contentCull = defaultValue(options.contentCull, true);
        /**
         * @type {QuadtreeLOD}
         */
        this.lodRoot = null;
        /**
         * @type {THREE.Loader}
         */
        this.loader = options.loader;

        /**
         * @type {QuadtreeNode[]}
         */
        this.children = [];

        /**
         * @type {THREE.Scene}
         * @private
         */
        this.scene = new THREE.Scene();

        this.onLoadProgress = new Event();
        this.ready = false;
        this.readyPromise = when.defer();
    }

    get boundingSphere() {
        if (!this.lodRoot) return
        return this.lodRoot.boundingSphere
    }
    get boundingSphereWC() {
        if (!this.lodRoot) return
        return this.lodRoot.boundingSphereWC
    }

    get boundingBoxWC() {
        if (!this.lodRoot) return
        return this.lodRoot.boundingBoxWC;
    }

    get region() {
        if (!this.lodRoot) return
        return this.lodRoot.region
    }

    /**
     * @public
     */
    init() {

        let useLightMap = this.useLightMap;
        let preloadTexture = this.preloadTexture;
        let translucent = this.translucent;

        if (this.contentSet) {
            this.contentSet.forEach((contentNode) => {
                processContentObject3d(contentNode, contentNode.content, useLightMap, translucent)
            })
            this.onload(this.contentSet);

            return;
        }
        if (!this.url) {
            throw new Error('url is required')
        }

        this.onLoadProgress.raiseEvent({
            currentPercent: 0,
            totalPercent: 0,
            currentFile: Path.GetFileName(this.url)
        });
        const _Cesium = Cesium;
        let path = THREE.LoaderUtils.extractUrlBase(this.url);
        return _Cesium.Resource.fetchJson(this.url).then((contentSet) => {
            let df = _Cesium.when.defer();
            let stack = contentSet.slice();
            let loader = this.loader;
            let imageCache = {};
            let total = 0;

            let loadNext = (done) => {
                if (stack.length == 0) {

                    done();
                    return;
                }
                let contentNode = stack.pop();

                let box = QuadtreeLODUtil.unpackBoundingBox(contentNode.boundingVolume.box);
                let sphere = QuadtreeLODUtil.unpackBoundingSphere(contentNode.boundingVolume.sphere);
                contentNode.boundingVolume.box = box;
                contentNode.boundingVolume.sphere = sphere;
                let fileName = Path.GetFileName(contentNode.uri)
                contentNode.uri = path + contentNode.uri;

                if (!loader) {
                    let format = Path.GetExtension(contentNode.uri).toLowerCase()
                    if (format == '.vism') {
                        loader = new VISMLoader()
                    } else {
                        loader = new OSGLoader()
                    }
                }

                if (loader.setDracoDecoderPath && !loader.dracoLoader && this.dracoDecoderPath) {
                    loader.setDracoDecoderPath(this.dracoDecoderPath)
                }

                this.onLoadProgress.raiseEvent({
                    currentPercent: 0,
                    totalPercent: total,
                    currentFile: fileName
                });

                loader.load(contentNode.uri, (content) => {

                    processContentObject3d(contentNode, content, useLightMap, translucent)

                    total = 100 * (contentSet.length - stack.length) / contentSet.length;
                    this.onLoadProgress.raiseEvent({
                        currentPercent: 100,
                        totalPercent: total,
                        currentFile: fileName
                    });

                    loadNext(done)
                }, (evt) => {
                    let current = 100 * evt.loaded / evt.total;
                    this.onLoadProgress.raiseEvent({
                        currentPercent: current,
                        totalPercent: total,
                        currentFile: fileName
                    });
                }, (err) => {
                    stack = null;
                    imageCache = {};
                    throw err
                }, { loadTexture: preloadTexture, imageCache: imageCache })
            }

            loadNext(() => {
                this.onLoadProgress.raiseEvent({
                    currentPercent: 100,
                    totalPercent: 100,
                    currentFile: ''
                })
                imageCache = {};
                this.onload(contentSet);
                df.resolve(this);
            });

            return df;
        })
    }

    onload(contentSet) {
        let oldContentSet = contentSet;
        contentSet = reClassContentSet(contentSet, this.max);

        let boxVertices = [];
        contentSet = contentSet.map((content) => {

            let box = content.boundingVolume.box;
            // let sphere = content.boundingVolume.sphere;
            boxVertices.push(box.minimum, box.maximum);

            content.modelMatrix = this.modelMatrix;
            content.debugVolume = this.debugVolume;
            content.showDebugVolume = this.showDebugVolume;
            content.minRange = this.minRange;
            content.frustumCulled = this.contentCull;
            let power = content.power, level = content.level;
            content = new QuadtreeNode(content)
            content.power = power;
            content.level = level;
            return content
        });

        this.boundingBox = Cesium.AxisAlignedBoundingBox.fromPoints(boxVertices);

        this.children.push.apply(this.children, contentSet);

        oldContentSet.forEach(oldContentNode => {
            let contentRoot = oldContentNode.content;

            this.scene.add(contentRoot);
            // oldContentNode.content = null;
        });

        this.createLOD();

        this.ready = true;
        this.readyPromise.resolve(this);


    }

    createLOD() {
        let radiusArr;
        if (this.lods.length > 0) {

            radiusArr = this.lods.map(lod => {
                return lod.minRadius;
            });
            radiusArr.sort((a, b) => {
                return b - a;
            });
            console.log(radiusArr);

            let radiusStatics = [], radiusMap = new Map();
            this.children.forEach((content) => {
                let sphere = content.boundingVolume.sphere;

                let r = parseInt(sphere.radius);
                if (!radiusMap.has(r)) {
                    let item = {
                        radius: r,
                        count: 1
                    }
                    radiusMap.set(r, item);
                    radiusStatics.push(item)
                } else {
                    let item = radiusMap.get(r)
                    item.count++;
                }
            });

            radiusStatics.sort((a, b) => {
                return a.radius - b.radius;
            });
            console.log(radiusStatics);

            let sums = new Array(radiusArr.length).fill(0);
            let radiusArr2 = radiusArr.slice().sort((a, b) => {
                return a - b;
            })
            radiusArr2.push(Infinity)
            radiusStatics.forEach(item => {

                for (let i = 1; i < radiusArr2.length; i++) {
                    if (item.radius >= radiusArr2[i - 1] && item.radius <= radiusArr2[i]) {
                        sums[i - 1] += item.count;
                    }
                }
            })
            sums.reverse();
            console.log(sums);
        }

        //

        let rootBoundingBox = this.boundingBox;
        console.time('QuadtreeLODUtil.create');
        const { Cartesian3, Color } = Cesium;
        let dimension = Cartesian3.subtract(
            rootBoundingBox.maximum, rootBoundingBox.minimum, new Cartesian3()
        );
        let scale = dimension.x / dimension.y, xSize = 4, ySize = 4;
        if (scale > 1) {
            xSize = parseInt(Math.ceil(ySize * scale));
        } else {
            ySize = parseInt(Math.ceil(xSize / scale));
        }

        var root = QuadtreeLODUtil.create({
            rootBoundingVolume: { box: rootBoundingBox },
            maxLevel: this.lods.length || this.maxLevel,
            xSize: xSize,
            ySize: ySize,
            zSize: this.zSize || 1,
            minRange: this.minRange,
            debugVolume: this.debugVolume,
            showDebugVolume: this.showDebugVolume,
            debugVolumeColor: Color.CYAN,
            modelMatrix: this.modelMatrix,
            maximumTextureSize: this.maximumTextureSize
        })
        console.timeEnd('QuadtreeLODUtil.create');

        console.time('QuadtreeLODUtil.filterContents');

        traverse(root, node => {
            // node.refine = this.refine || 'ADD';
            if (radiusArr) node.minRadius = radiusArr[node.level - 1];
        });

        let filterFunc = (lodNode, contentNode) => {
            // let levels = contentNode.level;
            return lodNode.level == contentNode.level;
        }

        traverse(root, node => {
            QuadtreeLODUtil.filterContents(this.children, node, this.radiusPower, null, filterFunc);
        });
        console.timeEnd('QuadtreeLODUtil.filterContents');

        root.generateLOD = false;
        console.log(root);

        this.lodRoot = root;
    }

    render(frameState, renderList) {
        if (!this.ready || !this.show) return;

        // this.scene.updateMatrixWorld(true);
        this.lodRoot.visualizer = this.visualizer;
        this.lodRoot.render(frameState, renderList);
    }

    destroy() {

        if (this.visualizer) {
            this.visualizer.remove(this.scene, true);
            this.visualizer.remove(this);
        }

        releaseResources(this.scene);

        this.children.splice(0);
        this.ready = false;
        this.contentSet.forEach(contentNode => {
            contentNode.content = null;
        })
        this.contentSet.splice(0);
        this.lodRoot.destroy();
        this.lodRoot = null;
        this.loader = null;
        if (!this.scene.isScene && scene.dispose) this.scene.dispose()
        this.scene = null;
        this.visualizer = null;
    }
}