import GeometryUtils from  "../../core/Source/GeometryUtils.js";

import computeArea from  "../../utils/Source/computeArea.js";

import QuadtreeLOD from  "./QuadtreeLOD.js";

import QuadtreeNode from  "./QuadtreeNode.js";


let _quadtreeLODBox1, _quadtreeLODBox2;

function setBoxCache(a, b) {
    if (!_quadtreeLODBox1) _quadtreeLODBox1 = new THREE.Box3();
    if (!_quadtreeLODBox2) _quadtreeLODBox2 = new THREE.Box3();

    _quadtreeLODBox1.min.copy(a.minimum || a.min);
    _quadtreeLODBox1.max.copy(a.maximum || a.max);

    _quadtreeLODBox2.min.copy(b.minimum || b.min);
    _quadtreeLODBox2.max.copy(b.maximum || b.max);
}

/**
 * @example
    var root = QuadtreeLODUtil.create({
        rootBoundingBox: model.aabb,
        maxLevel: 6,
        zSliceLevel: 5,
        minRange: 64,
        debugVolume: true,
        showDebugVolume: true,
        debugVolumeColor: Cesium.Color.CYAN,
        modelMatrix: meshVisualizer.modelMatrix
    })
    console.log('QuadtreeLOD', root);
    let rootJson = root.toJSON()
    console.log('QuadtreeLOD json', rootJson);

    root = QuadtreeLODUtil.fromJSON(rootJson, {
        debugVolume: true,
        showDebugVolume: true,
        debugVolumeColor: Cesium.Color.CYAN,
        modelMatrix: meshVisualizer.modelMatrix
    })
    console.log('fromJSON LOD', root);
    meshVisualizer.add(root);
 */
export default class QuadtreeLODUtil {

    /**
     * 
     * @param {THREE.Mesh} mesh 
     */
    static computeLODPower(mesh) {

        const Cartesian3 = Cesium.Cartesian3;

        let geometry = mesh.geometry;
        let positions = geometry.attributes.position;
        positions = positions.array || positions.values;

        if (!geometry.boundingBox) geometry.computeBoundingBox()

        let box = geometry.boundingBox;
        let x0 = box.min.x, y0 = box.min.y,
            x1 = box.max.x, y1 = box.max.y;

        let a = new Cartesian3(x0, y1, 0),
            b = new Cartesian3(x0, y0, 0),
            c = new Cartesian3(x1, y0, 0),
            d = new Cartesian3(x1, y1, 0);
        let boxArea = 0;

        boxArea += computeArea(a, b, c);
        boxArea += computeArea(a, c, d);

        let area = GeometryUtils.computeSurfaceArea(geometry);
        let power = area * (area / boxArea)

        // let vCount = positions.length / 3, faceCount = geometry.index ? geometry.index.count / 3 : vCount / 3;
        // let msg = 'a:' + area + ';\n:' + vCount + ";\n:" + faceCount + ";\nA:" + boxArea + ";\n(a/A):" + (area / boxArea).toFixed(2) + ";\n:" + power;
        // console.log(msg);

        return power;
    }


    /**
     *  
     * @example
     *  
     *  |         |
     *  |         |
     *  |         |
     *  
     * 
     *       |
     *       |                ------------
     *       v               /      /      /|
     *                      ------------ |
     *          /      /      /|/|
     *  | 01 | 02 |       ------------ / |
     *    or             /|/
     *  | 03 | 04 |       ------------ / 
     *                   / 
                          ------------   
     *  sliceZ=false        sliceZ=true
     *                      
     * @param {
      {
        box: Cesium.AxisAlignedBoundingBox,
        region:Cesium.TileBoundingRegion
      }
    } boundingVolume    
     * @param {number} [xSize=2] 
     * @param {number} [ySize=2] 
     * @param {number} [zSize=1] 
     * @return {
      {
        box: Cesium.AxisAlignedBoundingBox,
        region:Cesium.TileBoundingRegion
      }[]
    }
     */
    static splitBoundingVolume(boundingVolume, xSize, ySize, zSize) {
        let boundingVolumeList = [], boxList, regionList;
        if (boundingVolume.box) {
            boxList = this.splitBoundingBox(boundingVolume.box, xSize, ySize, zSize)
        }
        if (boundingVolume.region) {
            regionList = this.splitBoundingRegion(boundingVolume.region, xSize, ySize, zSize)
        }

        if (boxList || regionList) {
            let length = regionList ? regionList.length : boxList.length
            for (let i = 0; i < length; i++) {
                let newBoundingVolume = {};
                if (regionList) newBoundingVolume.region = regionList[i];
                if (boxList) newBoundingVolume.box = boxList[i];
                boundingVolumeList.push(newBoundingVolume);
            }

        }

        return boundingVolumeList;
    }

    /**
     * 
     * @param {Cesium.AxisAlignedBoundingBox} aabb  
     * @param {number} [xSize=2] 
     * @param {number} [ySize=2] 
     * @param {number} [zSize=1] 
     * @return {Cesium.AxisAlignedBoundingBox[]}
     * @private
     */
    static splitBoundingBox(aabb, xSize, ySize, zSize) {
        const { Cartesian3, AxisAlignedBoundingBox } = Cesium;

        let boxList = []
        let size = Cartesian3.subtract(aabb.maximum, aabb.minimum, new Cartesian3());

        let minimum = new Cartesian3(0, 0, 0);
        let maximum = new Cartesian3(0, 0, 0);
        let vertices = [minimum, maximum];

        xSize = xSize || 2;
        ySize = ySize || 2;
        zSize = zSize || 1;

        let xStep = 1 / xSize;
        let yStep = 1 / ySize;
        let zStep = 1 / zSize;

        for (let i = 0; i < xSize; i++) {
            let x = i * xStep, x1 = (i + 1) * xStep;
            for (let j = 0; j < ySize; j++) {
                let y = j * yStep, y1 = (j + 1) * yStep;

                for (let k = 0; k < zSize; k++) {
                    let z = k * zStep, z1 = (k + 1) * zStep;

                    minimum = Cartesian3.fromElements(x, y, z, minimum);
                    minimum = Cartesian3.multiplyComponents(size, minimum, minimum)
                    minimum = Cartesian3.add(aabb.minimum, minimum, minimum);

                    maximum = Cartesian3.fromElements(x1, y1, z1, maximum);
                    maximum = Cartesian3.multiplyComponents(size, maximum, maximum)
                    maximum = Cartesian3.add(aabb.minimum, maximum, maximum);

                    var box = AxisAlignedBoundingBox.fromPoints(vertices);

                    boxList.push(box);

                }

            }
        }

        return boxList;
    }

    /**
     * 
     * @param {Cesium.TileBoundingRegion} region 
     * @param {number} [xSize=2] 
     * @param {number} [ySize=2] 
     * @param {number} [zSize=1] 
     * @return {Cesium.TileBoundingRegion[]}
     * @private
     */
    static splitBoundingRegion(region, xSize, ySize, zSize) {
        const { Rectangle, TileBoundingRegion } = Cesium;

        let { west, south, width, height } = region.rectangle;
        let { maximumHeight, minimumHeight } = region;
        let deltH = maximumHeight - minimumHeight;
        let regionList = [];

        xSize = xSize || 2;
        ySize = ySize || 2;
        zSize = zSize || 1;

        let xStep = 1 / xSize;
        let yStep = 1 / ySize;
        let zStep = 1 / zSize;

        for (let i = 0; i < xSize; i++) {
            let x = i * xStep * width, x1 = (i + 1) * xStep * width;
            for (let j = 0; j < ySize; j++) {
                let y = j * yStep * height, y1 = (j + 1) * yStep * height;

                for (let k = 0; k < zSize; k++) {
                    let z = k * zStep * deltH, z1 = (k + 1) * zStep * deltH;

                    let rectangle = new Rectangle(
                        west + x, south + y, west + x1, south + y1
                    );

                    let newReginon = new TileBoundingRegion({
                        rectangle: rectangle,
                        minimumHeight: minimumHeight + z,
                        maximumHeight: minimumHeight + z1
                    })

                    regionList.push(newReginon);

                }

            }
        }

        return regionList;
    }

    /**
     * if a contains b,then return true,otherwise return false.
     * @param {Cesium.AxisAlignedBoundingBox|THREE.Box3} a 
     * @param {Cesium.AxisAlignedBoundingBox|THREE.Box3} b 
     * @returns {boolean}
     */
    static boxContains(a, b) {

        setBoxCache(a, b);
        return _quadtreeLODBox1.containsBox(_quadtreeLODBox2);
    }


    /**
     * 
     * @param {Cesium.AxisAlignedBoundingBox|THREE.Box3} a 
     * @param {Cesium.AxisAlignedBoundingBox|THREE.Box3} b 
     * @returns {boolean}
     */
    static boxIntersect(a, b) {

        setBoxCache(a, b);
        let intersection = _quadtreeLODBox1.intersect(_quadtreeLODBox2);
        intersection = !intersection.isEmpty();
        return intersection;
    }

    /**
     * if a contains b,then return true,otherwise return false.
     * @param {Cesium.TileBoundingRegion} a 
     * @param {Cesium.TileBoundingRegion} b 
     * @returns {boolean}
     */
    static regionContains(a, b) {
        let rectA = a.rectangle;
        let rectB = b.rectangle;
        let deltMinHeight = a.minimumHeight - b.minimumHeight;
        let deltMaxHeight = a.maximumHeight - b.maximumHeight;

        let contains = (
            rectA.west - rectB.west <= 0
            && rectA.east - rectB.east >= 0
            && rectA.south - rectB.south <= 0
            && rectA.north - rectB.north >= 0);

        if (contains) {
            contains = deltMinHeight <= 0 && deltMaxHeight >= 0;
        }

        return contains;
    }


    /**
     *  
     * @param {Cesium.TileBoundingRegion} a 
     * @param {Cesium.TileBoundingRegion} b 
     * @returns {boolean}
     */
    static regionIntersect(a, b) {
        let rectA = a.rectangle;
        let rectB = b.rectangle;
        let deltH1 = a.maximumHeight - b.minimumHeight;
        let deltH2 = b.maximumHeight - a.minimumHeight;
        let intersect = deltH1 > 0 && deltH2 > 0;
        if (intersect) {
            intersect = !!Cesium.Rectangle.intersection(rectA, rectB);
        }

        return intersect;
    }

    /**
     * 
     * @param {
        {
            uri:string,
            boundingVolume: {
                sphere: Cesium.BoundingSphere,
                box?: Cesium.AxisAlignedBoundingBox,
                region?:Cesium.TileBoundingRegion
            } 
        }[]
     *} contentSet 
     * @param {number}[power=2]
     * @param {boolean}[recursive=true]
     * @param {number}[maxRaius]
     * @param {QuadtreeLOD} lod 
     * @param {(node:QuadtreeLOD,content:QuadtreeNode)=>boolean} [filter] 
     */
    static filterContents(contentSet, lod, recursive, power, maxRaius, filter) {
        power = power || 2
        recursive = typeof recursive == 'undefined' ? true : recursive;

        if (lod.level == 0) {//tree

            lod.children.forEach(child => {
                this.filterContents(contentSet, child, power, recursive, maxRaius, filter);
            })
            return;
        }

        var lodContentSet = {
            contain: new Array(),
            intersect: new Array(),
            length: 0
        };

        var minRadius = lod.minRadius;
        if (!minRadius) {

            if (!maxRaius) {
                maxRaius = contentSet.maxRaius || 0;
                if (!maxRaius) {
                    contentSet.forEach((content) => {
                        maxRaius = Math.max(content.boundingVolume.sphere.radius, maxRaius)
                    });
                    contentSet.maxRaius = maxRaius;
                }
                maxRaius = Math.min(maxRaius, lod.boundingVolume.sphere.radius);
                contentSet.maxRaius = maxRaius;
            }

            let radiusStep = maxRaius / Math.pow(power, lod.maxLevel);
            minRadius = radiusStep * Math.pow(power, lod.maxLevel - lod.level);
            // minRadius = maxRaius * (1.0 - lod.level / lod.maxLevel);
            lod.minRadius = lod.level == lod.maxLevel ? 0 : minRadius;

        }

        contentSet.forEach((content) => {
            if (lod.region && content.boundingVolume.region) {

                let contentRegion = content.boundingVolume.region;
                let lodRegion = lod.region;
                if (this.regionContains(lodRegion, contentRegion)) {
                    lodContentSet.contain.push(content);
                    lodContentSet.length++
                } else if (this.regionIntersect(lodRegion, contentRegion)) {
                    lodContentSet.intersect.push(content);
                    lodContentSet.length++
                }

            } else {

                let contentBox = content.boundingVolume.box;
                let lodBox = lod.boundingBox
                if (this.boxContains(lodBox, contentBox)) {
                    lodContentSet.contain.push(content);
                    lodContentSet.length++
                } else if (this.boxIntersect(lodBox, contentBox)) {
                    lodContentSet.intersect.push(content);
                    lodContentSet.length++
                }

            }

        })

        lod.contentSet = lodContentSet;
        var currentContentSet = lodContentSet.contain.concat(lodContentSet.intersect);

        if (recursive) {
            lod.children.forEach(child => {
                this.filterContents(currentContentSet, child, power, recursive, maxRaius || contentSet.maxRaius, filter);
            })
        } else {
            currentContentSet.maxRaius = maxRaius;
            lod._getContentSetBeforeFilter = function () {
                return currentContentSet;
            }
        }

        function filterByRadius(lodNode, content) {
            let contentRadius = content.boundingVolume.sphere.radius
            if (contentRadius <= lod.minRadius) return false;
            if (lod.parent && contentRadius >= lod.parent.minRadius) return false
            return true;
        }

        if (lod.parent && lod.parent.refine == 'ADD') {
            filter = filter || filterByRadius;
            let finalLodContentSet = {
                contain: [],
                intersect: [],
                length: 0
            };
            lodContentSet.contain.forEach(content => {
                if (filter(lod, content)) {
                    finalLodContentSet.contain.push(content);
                    finalLodContentSet.length++;
                }
            });
            lodContentSet.intersect.forEach(content => {
                if (filter(lod, content)) {
                    finalLodContentSet.intersect.push(content);
                    finalLodContentSet.length++;
                }
            });

            lod.contentSet = finalLodContentSet;
        }

    }

    /**
     * 
     * @param {QuadtreeLOD} parent 
     * @param {number} maxLevel 
     * @param {number} minRange 
     * @param {boolean}[recursive=true]
     */
    static createLODTree(parent, maxLevel, minRange, recursive, zSliceLevel, xSize, ySize, zSize, debugVolume, debugVolumeColor, showDebugVolume) {

        let level = parent.level + 1;
        if (level > maxLevel) return;

        zSliceLevel = zSliceLevel || Infinity;

        let boundingVolumeList = this.splitBoundingVolume(parent.boundingVolume, level == 1 ? xSize : 2, level == 1 ? ySize : 2, (zSize && level == 1) ? zSize : 1);
        boundingVolumeList.forEach((boundingVolume) => {

            let child = new QuadtreeLOD({
                parent: parent,
                boundingVolume: boundingVolume,
                level: level,
                minRange: minRange * Math.pow(4, level - 1),
                debugVolumeColor: debugVolumeColor,
                debugVolume: debugVolume,
                showDebugVolume: showDebugVolume,
                modelMatrix: parent.modelMatrix,
                rtcCenter: parent.rtcCenter,
                maxLevel: maxLevel
            });

            if (recursive) {
                this.createLODTree(child, maxLevel, minRange, recursive, zSliceLevel, xSize, ySize, zSize, debugVolume, debugVolumeColor, showDebugVolume);
            } else {
                child._createLODTreeDelay = (pRecursive) => {
                    pRecursive = typeof pRecursive == 'undefined' ? false : pRecursive;
                    this.createLODTree(child, maxLevel, minRange, pRecursive, zSliceLevel, xSize, ySize, zSize, debugVolume, debugVolumeColor, showDebugVolume);
                }
            }
            parent.children.push(child);
        })
    }

    /**
     * 
     * @param {{
     *   rootBoundingVolume:{box?:Cesium.AxisAlignedBoundingBox,region?:Cesium.TileBoundingRegion},
     *   maxLevel:number,
     *   xSize?:number,
     *   ySize?:number,
     *   zSize?:number,
     *   zSliceLevel:number,
     *   minRange:number,
     *   debugVolumeColor?:Cesium.Color
     *   debugVolume?: boolean,
     *   showDebugVolume?: boolean,
     *   modelMatrix?:Cesium.Matrix4,
     *   rtcCenter?:Cesium.Cartesian3,
     *   recursive?:boolean,
     *   maximumTextureSize?:number
     * }} options 
     * @returns {QuadtreeLOD}
     */
    static create(options) {
        let rootBoundingVolume = options.rootBoundingVolume, maxLevel = options.maxLevel,
            zSliceLevel = options.zSliceLevel, minRange = options.minRange,
            debugVolume = options.debugVolume, showDebugVolume = options.showDebugVolume,
            debugVolumeColor = options.debugVolumeColor || Cesium.Color.YELLOW,
            modelMatrix = options.modelMatrix, rtcCenter = options.rtcCenter,
            recursive = typeof options.recursive == 'undefined' ? true : options.recursive;

        var root = new QuadtreeLOD({
            boundingVolume: rootBoundingVolume,
            level: 0,
            debugVolumeColor: debugVolumeColor,
            debugVolume: debugVolume,
            showDebugVolume: showDebugVolume,
            minRange: minRange,
            modelMatrix: modelMatrix,
            rtcCenter: rtcCenter,
            maxLevel: maxLevel,
            maximumTextureSize: options.maximumTextureSize
        });

        this.createLODTree(root, maxLevel, minRange, recursive, zSliceLevel, options.xSize, options.ySize, options.zSize || 2, debugVolume, debugVolumeColor, showDebugVolume);

        return root;
    }

    /**
     * 
     * @param {Object} json 
     * @param {{
     *   debugVolumeColor?:Cesium.Color
     *   debugVolume?: boolean,
     *   showDebugVolume?: boolean,
     *   modelMatrix?:Cesium.Matrix4
     * }} options
     * @returns {QuadtreeLOD} root
     */
    static fromJSON(json, options) {
        options = options || {};

        function parse(parent, json) {
            json.children.forEach((childJson) => {

                let childLod = new QuadtreeLOD(Object.assign(options, childJson));
                childLod.parent = parent;
                parse(childLod, childJson);
                parent.children.push(childLod);
            })
        }

        let root = new QuadtreeLOD(Object.assign(options, json));
        parse(root, json);

        return root;
    }
}
