import GeometryUtils from  "../../../../core/Source/GeometryUtils.js";

import SolidWireframeGeometry from  "../../geometries/SolidWireframeGeometry.js";

import BuildingVector3DLoader from  "../../../../renderable/Source/BuildingVector3DLoader.js";

import GenericWorker from  "../../../../utils/Source/GenericWorker.js";


export default class Vector3DWireframeProvider {
    constructor(root, styleFilter) {
        this.root = root;
        this.styleFilter = styleFilter;
    }
    requestContent(contentSet, rtcCenter) {
        let groups = [], materialMap = new WeakMap();
        const { root, styleFilter } = this
        const { Object3D, Mesh } = THREE;

        function createContent(content) {

            let group, style = {}, feature = content.uri;

            let featureId = feature.properties.featureId;
            styleFilter(feature, style);
            let geometry = BuildingVector3DLoader.parseFeature(feature, featureId, style);
            let material = style.material;

            if (rtcCenter) {
                if (!(geometry instanceof Cesium.Geometry)) {
                    geometry = geometry.constructor.createGeometry(geometry);
                }
                geometry = GeometryUtils.toBufferGeometry3js(geometry);
                geometry = new SolidWireframeGeometry(geometry, true)

                let positions = geometry.attributes.position.array;
                for (let i = 0; i < positions.length; i += 3) {
                    positions[i] -= rtcCenter.x;
                    positions[i + 1] -= rtcCenter.y;
                    positions[i + 2] -= rtcCenter.z;
                }
            } else {
                geometry = GeometryUtils.toBufferGeometry3js(geometry);
                geometry = new SolidWireframeGeometry(geometry, true)

            }


            if (materialMap.has(material)) {
                group = materialMap.get(material);
            } else {
                group = {
                    material: material,
                    geometries: []
                };
                groups.push(group)
                materialMap.set(material, group);
            }
            group.geometries.push(geometry);
        }

        let batchList = [], batch = [], batchSize = 100;
        batchList.push(batch);
        contentSet.contain.forEach(content => {
            if (batch.length > batchSize) {
                batch = [];
                batchList.push(batch);
            }
            batch.push(content)
        });
        contentSet.intersect.forEach(content => {
            if (batch.length > batchSize) {
                batch = [];
                batchList.push(batch);
            }
            batch.push(content)
        });

        return new GenericWorker(batchList, batch => {
            batch.forEach(createContent);
        }).run().then(() => {

            let object3d = new Object3D();
            groups.forEach(group => {
                let geometry = GeometryUtils.mergeGeometries3js(group.geometries);
                let material = group.material;
                let mesh = new Mesh(geometry, material);

                mesh.pickObject = {
                    id: mesh,
                    mesh: mesh,
                    primitive: root
                }
                object3d.add(mesh);

                mesh.matrixAutoUpdate = false;
                mesh.modelMatrixNeedsUpdate = false;
                mesh.frustumCulled = false;

                materialMap.delete(material)
            });
            object3d.matrixAutoUpdate = false;
            object3d.modelMatrixNeedsUpdate = false;
            object3d.frustumCulled = false;
            groups.splice(0);
            contentSet.contain.splice(0)
            contentSet.intersect.splice(0)
            return object3d;
        });
    }
}