// import MeshVisualizer from "../../../core/MeshVisualizer.js";
import PagedLOD from  "../../../../renderable/Source/PagedLOD.js";

import Earth from  "../../Earth.js";

import ILayer from  "../ILayer.js";


export default class OsgbLayer extends ILayer {
    constructor(params) {
        super(params);
        params.materialType = params.materialType || 'basic'
        this.params = params;
        this._root = new PagedLOD(params);
    }

    get root() {
        return this._root;
    }
    
    get ready() {
        return !!this._root && this._root.ready
    }
    get readyPromise() {
        return this._root && this._root.readyPromise
    }

    get boundingSphere() {
        return this._root && this._root.boundingSphere
    }

    set materialType(val) {
        let { params, _root } = this
        if (params.materialType != val) {
            params.materialType = val;
            if (_root) _root.materialType = val;
        }
    }
    get materialType() {
        return this.params.materialType
    }

    set show(val) {
        this._show = val;
        if (this.root) {
            this.root.show = val
        }
    }
    get show() {
        return this._show;
    }
    reload(earth) {
        this.unload(earth);
        this._root = new PagedLOD(this.params);
        this.load(earth)
    }
    /**
     * 
     * @param {Earth|MeshVisualizer} earth 
     */
    load(earth) {

        var localScene = earth.isMeshVisualizer ? earth : earth.globalScene;
        this.root.show = this.show
        localScene.add(this.root)
    }
    /**
     * 
     * @param {Earth|MeshVisualizer} earth 
     */
    unload(earth) {
        var localScene = earth.isMeshVisualizer ? earth : earth.globalScene;
        let root = this.root;
        localScene.remove(root, true);
        this._root = root.destroy()
    }
}